/* ========================================
   ХЕДЕР
   Современный адаптивный заголовок сайта
   ======================================== */

.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: var(--z-fixed);
    background-color: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(20px);
    border-bottom: 1px solid var(--color-border-light);
    transition: var(--transition-fast);
    box-sizing: border-box;
}

.header--scrolled {
    background-color: rgba(255, 255, 255, 0.98);
    box-shadow: var(--shadow-md);
}

.header__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: var(--spacing-4) var(--spacing-6);
    max-width: var(--container-xl);
    margin: 0 auto;
    min-height: 4rem;
    position: relative;
    width: 100%;
    box-sizing: border-box;
}

/* Логотип */
.header__logo {
    display: flex;
    align-items: center;
    gap: var(--spacing-3);
    text-decoration: none;
    color: var(--color-text-primary);
    font-weight: var(--font-weight-bold);
    font-size: var(--font-size-xl);
    transition: var(--transition-fast);
}

.header__logo:hover {
    color: var(--color-accent);
}

/* Навигация в хедере */
.header__nav {
    display: flex;
    align-items: center;
    gap: var(--spacing-8);
}

.header__actions {
    display: flex;
    align-items: center;
    gap: var(--spacing-4);
}

/* CTA кнопка в хедере */
.header__cta {
    background-color: var(--color-accent);
    color: var(--color-text-inverse);
    padding: var(--spacing-2) var(--spacing-4);
    border-radius: var(--radius-lg);
    font-weight: var(--font-weight-medium);
    text-decoration: none;
    transition: var(--transition-fast);
}

.header__cta:hover {
    background-color: var(--color-accent-hover);
    transform: translateY(-1px);
}

/* Социальные ссылки */
.header__social {
    display: flex;
    align-items: center;
    gap: var(--spacing-3);
}

.header__social-link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2rem;
    height: 2rem;
    color: var(--color-text-secondary);
    border-radius: var(--radius-md);
    transition: var(--transition-fast);
}

.header__social-link:hover {
    color: var(--color-accent);
    background-color: var(--color-accent-light);
}

/* Отступ для контента под фиксированным хедером */
html {
    scroll-padding-top: 4rem;
}

body {
    padding-top: 4rem;
}

.main-content {
    padding-top: 0;
}

/* Слайдер без дополнительного отступа */
.slider {
    margin-top: -4rem;
    padding-top: 4rem;
}

/* ========================================
   МОБИЛЬНОЕ МЕНЮ И АДАПТИВНОСТЬ
   ======================================== */

/* Бургер-кнопка */
.header__burger {
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 24px;
    height: 18px;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    margin: 0;
    z-index: 1001;
    box-sizing: border-box;
}
.header__burger span {
    display: block;
    width: 100%;
    height: 2px;
    background-color: var(--color-text-primary);
    border-radius: 2px;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    transform-origin: center;
}

.header__burger--active span:nth-child(1) {
    transform: translateY(8px) rotate(45deg);
}

.header__burger--active span:nth-child(2) {
    opacity: 0;
    transform: scaleX(0);
}

.header__burger--active span:nth-child(3) {
    transform: translateY(-8px) rotate(-45deg);
}

/* Мобильное меню */
.header__mobile-menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: var(--color-bg-primary);
    background-color: #ffffff; /* Явный белый фон как fallback */
    transform: translateX(-100%);
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    z-index: 1000;
    display: flex;
    flex-direction: column;
    padding-top: 5rem;
    overflow-y: auto;
}

.header__mobile-menu--active {
    transform: translateX(0);
}

.header__mobile-nav {
    flex: 1;
    padding: 2rem;
}

.header__mobile-nav-list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.header__mobile-nav-item {
    border-bottom: 1px solid var(--color-border-light);
}

.header__mobile-nav-link {
    display: block;
    padding: 1rem 0;
    font-size: 1.125rem;
    font-weight: 500;
    color: var(--color-text-primary);
    text-decoration: none;
    transition: color 0.3s ease;
}

.header__mobile-nav-link:hover,
.header__mobile-nav-link.nav__link--active {
    color: var(--color-accent);
}

/* Кнопки действий в мобильном меню */
.header__mobile-actions {
    padding: 2rem;
    border-top: 1px solid var(--color-border-light);
    margin-top: auto;
}

.header__mobile-actions .btn {
    width: 100%;
    justify-content: center;
}

.header__mobile-actions {
    padding: 2rem;
    border-top: 1px solid var(--color-border-light);
}

/* Блокировка прокрутки при открытом меню */
body.menu-open {
    overflow: hidden;
    position: fixed;
    width: 100%;
    height: 100%;
}

/* ========================================
   RESPONSIVE DESIGN
   ======================================== */

/* Десктопная версия и планшеты (включая iPad Pro) */
@media (min-width: 769px) {
    .header .header__burger {
        display: none;
    }
    
    .header .header__mobile-menu {
        display: none;
    }
}

/* iPad Pro и больше */
@media (min-width: 1024px) {
    .header .header__burger {
        display: none !important;
    }
    
    .header .header__mobile-menu {
        display: none !important;
    }
}

/* Планшеты и мобильные устройства (исключая iPad Pro) */
@media (max-width: 768px) {
    .header__nav {
        display: none;
    }
    
    .header__actions {
        display: none;
    }
    
    .header__burger {
        display: flex;
        flex-shrink: 0;
        margin-left: auto;
    }
    
    .header__container {
        padding: 1rem;
        justify-content: space-between;
        gap: 1rem;
        width: 100%;
        max-width: 100%;
        box-sizing: border-box;
    }
    
    .header__logo {
        flex-shrink: 0;
        margin-right: 0;
    }
    
    .slider {
        margin-top: -3.5rem;
        padding-top: 3.5rem;
    }
}

/* Средние планшеты (iPad Mini, iPad) */
@media (min-width: 769px) and (max-width: 1023px) {
    .header .header__burger {
        display: none;
    }
    
    .header .header__mobile-menu {
        display: none;
    }
    
    .header__nav {
        display: flex;
    }
    
    .header__actions {
        display: flex;
    }
}

/* iPad Air - только бургер-меню */
@media (min-width: 800px) and (max-width: 850px) and (orientation: portrait),
       (min-width: 1150px) and (max-width: 1200px) and (orientation: landscape) {
    .header__nav {
        display: none;
    }
    
    .header__actions {
        display: none;
    }
    
    .header .header__burger {
        display: flex;
        flex-shrink: 0;
        margin-left: auto;
    }
    
    .header .header__mobile-menu {
        display: block;
    }
    
    .header__container {
        padding: 1rem;
        justify-content: space-between;
        gap: 1rem;
        width: 100%;
        max-width: 100%;
        box-sizing: border-box;
    }
    
    .header__logo {
        flex-shrink: 0;
        margin-right: 0;
    }
}

/* Маленькие мобильные экраны */
@media (max-width: 480px) {
    .header__container {
        padding: 0.75rem 1rem;
        max-width: 100%;
    }
    
    .header__burger {
        width: 22px;
        height: 16px;
    }
    
    .header__logo {
        font-size: var(--font-size-lg);
    }
    
    .slider {
        margin-top: -3rem;
        padding-top: 3rem;
    }
}

/* Темная версия хедера */
.header--dark {
    background-color: rgba(0, 0, 0, 0.9);
    border-bottom-color: rgba(255, 255, 255, 0.1);
}

.header--dark .header__logo,
.header--dark .nav__link {
    color: var(--color-text-inverse);
}

.header--dark .nav__link:hover {
    color: var(--color-accent);
}

.header--dark .header__social-link {
    color: rgba(255, 255, 255, 0.7);
}

.header--dark .header__social-link:hover {
    color: var(--color-accent);
}

/* Анимации появления */
@keyframes header-slide-down {
    from {
        transform: translateY(-100%);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

.header--animated {
    animation: header-slide-down 0.5s ease-out;
}
