/* ========================================
   АДАПТИВНОСТЬ
   Responsive utilities и media queries
   ======================================== */

/* Display утилиты */
.hidden {
    display: none;
}

.block {
    display: block;
}

.inline {
    display: inline;
}

.inline-block {
    display: inline-block;
}

.flex {
    display: flex;
}

.inline-flex {
    display: inline-flex;
}

.grid {
    display: grid;
}

.inline-grid {
    display: inline-grid;
}

/* Позиционирование */
.static {
    position: static;
}

.fixed {
    position: fixed;
}

.absolute {
    position: absolute;
}

.relative {
    position: relative;
}

.sticky {
    position: sticky;
}

/* Width */
.w-auto {
    width: auto;
}

.w-full {
    width: 100%;
}

.w-3\/4 {
    width: 75%;
}

/* Height */
.h-auto {
    height: auto;
}

.h-full {
    height: 100%;
}

.h-screen {
    height: 100vh;
}

/* Overflow */
.overflow-auto {
    overflow: auto;
}

.overflow-hidden {
    overflow: hidden;
}

.overflow-visible {
    overflow: visible;
}

.overflow-scroll {
    overflow: scroll;
}

.overflow-x-auto {
    overflow-x: auto;
}

.overflow-y-auto {
    overflow-y: auto;
}

.overflow-x-hidden {
    overflow-x: hidden;
}

.overflow-y-hidden {
    overflow-y: hidden;
}

/* Адаптивные префиксы для мобильных */
@media (max-width: 639px) {
    .sm\:hidden {
        display: none;
    }
    
    .sm\:block {
        display: block;
    }
    
    .sm\:flex {
        display: flex;
    }
    
    .sm\:grid {
        display: grid;
    }
    
    .sm\:w-full {
        width: 100%;
    }
    
    .sm\:text-center {
        text-align: center;
    }
    
    .sm\:text-left {
        text-align: left;
    }
    
    .sm\:px-4 {
        padding-left: var(--spacing-4);
        padding-right: var(--spacing-4);
    }
    
    .sm\:py-8 {
        padding-top: var(--spacing-8);
        padding-bottom: var(--spacing-8);
    }
}

/* Планшеты */
@media (min-width: 640px) {
    .sm\:hidden {
        display: none;
    }
    
    .sm\:block {
        display: block;
    }
    
    .sm\:flex {
        display: flex;
    }
    
    .sm\:grid {
        display: grid;
    }
    
    .sm\:grid-cols-2 {
        grid-template-columns: repeat(2, 1fr);
    }
    
    .sm\:grid-cols-3 {
        grid-template-columns: repeat(3, 1fr);
    }
    
    .sm\:w-1\/2 {
        width: 50%;
    }
    
    .sm\:w-auto {
        width: auto;
    }
}

/* Средние экраны */
@media (min-width: 768px) {
    .md\:hidden {
        display: none;
    }
    
    .md\:block {
        display: block;
    }
    
    .md\:flex {
        display: flex;
    }
    
    .md\:grid {
        display: grid;
    }
    
    .md\:grid-cols-2 {
        grid-template-columns: repeat(2, 1fr);
    }
    
    .md\:grid-cols-3 {
        grid-template-columns: repeat(3, 1fr);
    }
    
    .md\:grid-cols-4 {
        grid-template-columns: repeat(4, 1fr);
    }
    
    .md\:w-1\/2 {
        width: 50%;
    }
    
    .md\:w-1\/3 {
        width: 33.333333%;
    }
    
    .md\:w-2\/3 {
        width: 66.666667%;
    }
    
    .md\:text-left {
        text-align: left;
    }
    
    .md\:text-center {
        text-align: center;
    }
    
    .md\:px-6 {
        padding-left: var(--spacing-6);
        padding-right: var(--spacing-6);
    }
    
    .md\:py-12 {
        padding-top: var(--spacing-12);
        padding-bottom: var(--spacing-12);
    }
}

/* Большие экраны */
@media (min-width: 1024px) {
    .lg\:hidden {
        display: none;
    }
    
    .lg\:block {
        display: block;
    }
    
    .lg\:flex {
        display: flex;
    }
    
    .lg\:grid {
        display: grid;
    }
    
    .lg\:grid-cols-3 {
        grid-template-columns: repeat(3, 1fr);
    }
    
    .lg\:grid-cols-4 {
        grid-template-columns: repeat(4, 1fr);
    }
    
    .lg\:grid-cols-5 {
        grid-template-columns: repeat(5, 1fr);
    }
    
    .lg\:w-1\/3 {
        width: 33.333333%;
    }
    
    .lg\:w-1\/4 {
        width: 25%;
    }
    
    .lg\:w-auto {
        width: auto;
    }
    
    .lg\:px-8 {
        padding-left: var(--spacing-8);
        padding-right: var(--spacing-8);
    }
    
    .lg\:py-16 {
        padding-top: var(--spacing-16);
        padding-bottom: var(--spacing-16);
    }
}

/* XL экраны */
@media (min-width: 1280px) {
    .xl\:grid-cols-4 {
        grid-template-columns: repeat(4, 1fr);
    }
    
    .xl\:grid-cols-5 {
        grid-template-columns: repeat(5, 1fr);
    }
    
    .xl\:grid-cols-6 {
        grid-template-columns: repeat(6, 1fr);
    }
    
    .xl\:w-1\/4 {
        width: 25%;
    }
    
    .xl\:w-1\/5 {
        width: 20%;
    }
}

/* 2XL экраны */
@media (min-width: 1536px) {
    .\\32xl\\:grid-cols-6 {
        grid-template-columns: repeat(6, 1fr);
    }
    
    .\\32xl\\:px-0 {
        padding-left: 0;
        padding-right: 0;
    }
}

/* Утилиты для скрытия на разных размерах */
.show-mobile {
    display: block;
}

.show-tablet {
    display: none;
}

.show-desktop {
    display: none;
}

@media (min-width: 640px) {
    .show-mobile {
        display: none;
    }
    
    .show-tablet {
        display: block;
    }
}

@media (min-width: 1024px) {
    .show-tablet {
        display: none;
    }
    
    .show-desktop {
        display: block;
    }
}

/* Container queries (для современных браузеров) */
@container (min-width: 400px) {
    .container-sm\:grid-cols-2 {
        grid-template-columns: repeat(2, 1fr);
    }
}

@container (min-width: 600px) {
    .container-md\:grid-cols-3 {
        grid-template-columns: repeat(3, 1fr);
    }
}

/* Landscape/Portrait */
@media (orientation: landscape) {
    .landscape\:h-screen {
        height: 100vh;
    }
}

@media (orientation: portrait) {
    .portrait\:h-auto {
        height: auto;
    }
}

/* Print стили */
@media print {
    .print\:hidden {
        display: none;
    }
    
    .print\:block {
        display: block;
    }
}

/* Dark mode готовность */
@media (prefers-color-scheme: dark) {
    .dark\:bg-gray-900 {
        background-color: var(--color-gray-900);
    }
    
    .dark\:text-white {
        color: var(--color-white);
    }
}

/* ========================================
   КНОПКА "НАВЕРХ" И СКРОЛЛ-ЭФФЕКТЫ
   Удобная навигация по сайту
   ======================================== */

/* Кнопка наверх */
.scroll-to-top {
    position: fixed;
    bottom: var(--spacing-6);
    right: var(--spacing-6);
    width: 50px;
    height: 50px;
    background: linear-gradient(135deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    border: none;
    border-radius: var(--radius-full);
    color: var(--color-text-inverse);
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: var(--font-size-lg);
    z-index: var(--z-fixed);
    transition: all 0.3s ease;
    transform: translateY(100px);
    opacity: 0;
    visibility: hidden;
    box-shadow: 0 8px 25px rgba(201, 169, 110, 0.3);
}

.scroll-to-top.visible {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
}

.scroll-to-top:hover {
    transform: translateY(-3px) scale(1.1);
    box-shadow: 0 15px 35px rgba(201, 169, 110, 0.4);
}

.scroll-to-top:active {
    transform: translateY(-1px) scale(1.05);
}

/* Индикатор прогресса чтения */
.reading-progress {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background: rgba(201, 169, 110, 0.2);
    z-index: var(--z-fixed);
}

.reading-progress__bar {
    height: 100%;
    background: linear-gradient(90deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    width: 0%;
    transition: width 0.1s ease;
}

/* Плавная прокрутка для всего сайта */
html {
    scroll-behavior: smooth;
}

/* Улучшенные стили для прокрутки */
::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-track {
    background: var(--color-bg-secondary);
}

::-webkit-scrollbar-thumb {
    background: linear-gradient(180deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    border-radius: var(--radius-full);
}

::-webkit-scrollbar-thumb:hover {
    background: linear-gradient(180deg, var(--color-accent-hover) 0%, var(--color-accent-dark) 100%);
}
