/* Анимации при скролле */
.scroll-animation {
    opacity: 1; /* Изменено с 0 на 1 для предотвращения исчезновения */
    transform: translateY(50px);
    transition: all 0.6s ease-out;
}

.scroll-animation.animate {
    opacity: 1;
    transform: translateY(0);
}

/* Hover анимации */
.hover-float:hover {
    animation: float 2s ease-in-out infinite;
}

.hover-pulse:hover {
    animation: pulse 1s ease-in-out infinite;
}

.hover-shake:hover {
    animation: shake 0.5s ease-in-out;
}

/* Анимация загрузки */
@keyframes loading {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.loading-spinner {
    width: 40px;
    height: 40px;
    border: 4px solid var(--color-border-light);
    border-top: 4px solid var(--color-accent);
    border-radius: 50%;
    animation: loading 1s linear infinite;
}

/* Анимация прогресс-бара */
@keyframes progressFill {
    from {
        width: 0%;
    }
    to {
        width: var(--progress, 100%);
    }
}

.animate-progress {
    animation: progressFill 2s ease-out;
}

/* Эффект пишущей машинки для заголовков */
.typewriter {
    overflow: hidden;
    white-space: nowrap;
    border-right: 3px solid var(--color-accent);
    animation: 
        typing 4s steps(40, end),
        blink 0.75s step-end infinite;
}

/* Анимация появления элементов списка */
.animate-list-item {
    opacity: 1;
    transform: translateX(-20px);
    animation: fadeInLeft 0.5s ease-out forwards;
}

.animate-list-item:nth-child(1) { animation-delay: 0.1s; }
.animate-list-item:nth-child(2) { animation-delay: 0.2s; }
