    opacity: 1;
    transform: translateY(0);
    transition: all 0.6s ease-out;
}

.slide-up.animate,
.slide-up.animate-in {
    opacity: 1;
    transform: translateY(0);
}

.slide-left {
    opacity: 1;
    transform: translateX(0);
    transition: all 0.6s ease-out;
}

.slide-left.animate,
.slide-left.animate-in {
    opacity: 1;
    transform: translateX(0);
}

.slide-right {
    opacity: 1;
    transform: translateX(0);
    transition: all 0.6s ease-out;
}

.slide-right.animate,
.slide-right.animate-in {
    opacity: 1;
    transform: translateX(0);
}

.zoom-in {
    opacity: 1;
    transform: scale(1);
    transition: all 0.6s ease-out;
}

.zoom-in.animate,
.zoom-in.animate-in {
    opacity: 1;
    transform: scale(1);
}

