/* Flashback School Styles */

/* Hero Section */
.flashback-hero {
    padding: 120px 0 80px;
    background: linear-gradient(135deg, var(--glass-bg-light) 0%, rgba(255, 255, 255, 0.8) 100%);
    backdrop-filter: blur(20px);
    border-bottom: 1px solid var(--glass-border);
}

.flashback-hero__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 60px;
    align-items: center;
}

.flashback-hero__title {
    font-family: var(--font-heading);
    font-size: 3.5rem;
    font-weight: 600;
    color: var(--text-primary);
    margin-bottom: 1rem;
    line-height: 1.2;
}

.flashback-hero__subtitle {
    font-size: 1.5rem;
    color: var(--color-accent, #c9a96e);
    margin-bottom: 1.5rem;
    font-weight: 500;
}

.flashback-hero__description {
    font-size: 1.1rem;
    color: var(--text-secondary);
    margin-bottom: 2rem;
    line-height: 1.6;
}

.flashback-hero__stats {
    display: flex;
    gap: 2rem;
    margin-bottom: 2.5rem;
}

.stat-item {
    text-align: center;
}

.stat-number {
    display: block;
    font-size: 2.5rem;
    font-weight: 700;
    color: var(--color-accent, #c9a96e);
    font-family: var(--font-heading);
}

.stat-label {
    display: block;
    font-size: 0.9rem;
    color: var(--text-secondary);
    margin-top: 0.5rem;
}

.flashback-hero__actions {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
}

.flashback-hero__image {
    position: relative;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: var(--shadow-glass);
}

.flashback-hero__image img {
    width: 100%;
    height: 500px;
    object-fit: cover;
    transition: transform 0.3s ease;
}

.flashback-hero__image:hover img {
    transform: scale(1.05);
}

/* Instructor Section */
.flashback-instructor {
    padding: 80px 0;
    background: var(--bg-primary);
}

.flashback-instructor__content {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 60px;
    align-items: start;
}

.flashback-instructor__image {
    position: relative;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: var(--shadow-glass);
}

.flashback-instructor__image img {
    width: 100%;
    height: 400px;
    object-fit: cover;
}

.flashback-instructor__text h2 {
    font-family: var(--font-heading);
    font-size: 2.5rem;
    color: var(--text-primary);
    margin-bottom: 1rem;
}

.flashback-instructor__text h3 {
    font-size: 2rem;
    color: var(--color-accent, #c9a96e);
    margin-bottom: 0.5rem;
}

.instructor-title {
    font-size: 1.1rem;
    color: var(--text-secondary);
    margin-bottom: 2rem;
    font-style: italic;
}

.instructor-bio p {
    margin-bottom: 1.5rem;
    line-height: 1.6;
}

.instructor-achievements {
    margin-top: 2rem;
    padding: 2rem;
    background: var(--glass-bg-light);
    border-radius: 15px;
    backdrop-filter: blur(10px);
    border: 1px solid var(--glass-border);
}

.instructor-achievements h4 {
    font-size: 1.3rem;
    color: var(--text-primary);
    margin-bottom: 1rem;
}

.instructor-achievements ul {
    list-style: none;
}

.instructor-achievements li {
    position: relative;
    padding-left: 1.5rem;
    margin-bottom: 0.8rem;
    color: var(--text-secondary);
}

.instructor-achievements li::before {
    content: "✓";
    position: absolute;
    left: 0;
    color: var(--color-accent, #c9a96e);
    font-weight: bold;
}

/* Courses Section */
.flashback-courses {
    padding: 80px 0;
    background: linear-gradient(135deg, var(--glass-bg-light) 0%, rgba(255, 255, 255, 0.9) 100%);
}

.courses-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 2rem;
    margin-top: 3rem;
}

.course-card {
    background: var(--glass-bg);
    backdrop-filter: blur(20px);
    border: 1px solid var(--glass-border);
    border-radius: 20px;
    padding: 2rem;
    position: relative;
    transition: all 0.3s ease;
    display: flex;
    flex-direction: column;
}

.course-card:hover {
    transform: translateY(-5px);
    box-shadow: var(--shadow-glass), 0 20px 40px rgba(0, 0, 0, 0.1);
}

.course-card--featured {
    border: 2px solid var(--color-accent, #c9a96e);
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.9) 0%, var(--glass-bg) 100%);
}

.course-card__badge {
    position: absolute;
    top: -10px;
    right: 20px;
    background: var(--color-accent, #c9a96e);
    color: white;
    padding: 0.5rem 1rem;
    border-radius: 20px;
    font-size: 0.8rem;
    font-weight: 600;
}

.course-card__header {
    margin-bottom: 1.5rem;
}

.course-card__header h3 {
    font-size: 1.5rem;
    color: var(--text-primary);
    margin-bottom: 0.5rem;
}

.course-level {
    display: inline-block;
    padding: 0.3rem 0.8rem;
    background: var(--color-accent-hover, #b8965d);
    color: white;
    border-radius: 20px;
    font-size: 0.8rem;
    font-weight: 500;
}

.course-duration {
    display: flex;
    flex-direction: column;
    gap: 0.3rem;
    margin-bottom: 1rem;
    font-size: 0.9rem;
    color: var(--text-secondary);
}

.course-description p {
    margin-bottom: 1.5rem;
    line-height: 1.6;
}

.course-program {
    flex-grow: 1;
    margin-bottom: 2rem;
}

.course-program h4 {
    font-size: 1.1rem;
    color: var(--text-primary);
    margin-bottom: 1rem;
}

.course-program ul {
    list-style: none;
}

.course-program li {
    position: relative;
    padding-left: 1.5rem;
    margin-bottom: 0.6rem;
    font-size: 0.9rem;
    color: var(--text-secondary);
}

.course-program li::before {
    content: "•";
    position: absolute;
    left: 0;
    color: var(--color-accent, #c9a96e);
    font-weight: bold;
}

.course-price {
    text-align: center;
    margin-bottom: 1.5rem;
    padding: 1rem;
    background: rgba(255, 255, 255, 0.5);
    border-radius: 10px;
}

.price-value {
    display: block;
    font-size: 2rem;
    font-weight: 700;
    color: var(--color-accent, #c9a96e);
    font-family: var(--font-heading);
}

.price-note {
    display: block;
    font-size: 0.8rem;
    color: var(--text-secondary);
    margin-top: 0.3rem;
}

/* Schedule Section */
.flashback-schedule {
    padding: 80px 0;
    background: var(--bg-primary);
}

/* Student Works */
.flashback-works {
    padding: 80px 0;
    background: linear-gradient(135deg, var(--glass-bg-light) 0%, rgba(255, 255, 255, 0.9) 100%);
}


/* Testimonials */
.flashback-testimonials {
    padding: 80px 0;
    background: var(--bg-primary);
}

.testimonials-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 2rem;
    margin-top: 3rem;
}

.testimonial-card {
    background: var(--glass-bg);
    backdrop-filter: blur(20px);
    border: 1px solid var(--glass-border);
    border-radius: 20px;
    padding: 2rem;
    transition: all 0.3s ease;
    cursor: pointer;
    position: relative;
}

.testimonial-card:hover {
    transform: translateY(-5px);
}

/* Accordion styles */
.testimonial-card.accordion {
    padding-bottom: 4rem;
}

.testimonial-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 1rem;
}

.testimonial-preview {
    flex: 1;
    margin-right: 1rem;
}

.testimonial-toggle {
    position: absolute;
    bottom: 1rem;
    right: 1rem;
    background: var(--color-accent, #c9a96e);
    color: white;
    border: none;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 1.2rem;
    z-index: 2;
}

.testimonial-toggle:hover {
    background: var(--color-accent-hover, #b8965d);
    transform: scale(1.1);
}

.testimonial-toggle.expanded {
    transform: rotate(180deg);
}

.testimonial-content {
    margin-bottom: 2rem;
    overflow: hidden;
    transition: all 0.3s ease;
}

.testimonial-content.collapsed {
    max-height: 4.8rem; /* Примерно 3 строки текста */
    position: relative;
}

.testimonial-content.collapsed::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 1.5rem;
    background: linear-gradient(transparent, var(--glass-bg));
    pointer-events: none;
}

.testimonial-content.expanded {
    max-height: none;
}

.testimonial-content p {
    font-style: italic;
    line-height: 1.6;
    color: var(--text-secondary);
    font-size: 1.05rem;
    margin: 0;
}

.testimonial-author {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.testimonial-author img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    object-fit: cover;
}

.author-info h4 {
    color: var(--text-primary);
    margin-bottom: 0.3rem;
}

.author-info span {
    font-size: 0.9rem;
    color: var(--text-secondary);
}

/* Success Story Section */
.flashback-success-story {
    padding: 80px 0;
    background: var(--bg-primary);
}

.success-story {
    margin-top: 3rem;
}

.success-story__content {
    display: grid;
    grid-template-columns: 1.2fr 1fr;
    gap: 60px;
    align-items: start;
}

.student-intro {
    margin-bottom: 3rem;
}

.student-intro h3 {
    font-size: 2rem;
    color: var(--text-primary);
    margin-bottom: 0.5rem;
    font-family: var(--font-heading);
}

.student-status {
    font-size: 1.1rem;
    color: var(--text-secondary);
    margin-bottom: 1rem;
}

.achievement-badge {
    background: linear-gradient(135deg, var(--color-accent, #c9a96e), var(--color-accent-hover, #b8965d));
    color: white;
    padding: 0.5rem 1rem;
    border-radius: 20px;
    font-size: 0.9rem;
    font-weight: 500;
}

.story-timeline {
    margin-bottom: 3rem;
}

.story-step {
    display: flex;
    gap: 1.5rem;
    margin-bottom: 2.5rem;
    align-items: flex-start;
}

.story-step__marker {
    width: 60px;
    height: 60px;
    background: var(--glass-bg);
    border: 2px solid var(--color-accent, #c9a96e);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    backdrop-filter: blur(10px);
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
}

.story-step__marker::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(135deg, var(--color-accent, #c9a96e), transparent);
    opacity: 0.1;
    border-radius: 50%;
    transition: opacity 0.3s ease;
}

.story-step:hover .story-step__marker::before {
    opacity: 0.2;
}

.story-step:hover .story-step__marker {
    transform: scale(1.05);
    box-shadow: 0 8px 25px rgba(201, 169, 110, 0.3);
}

.step-icon {
    width: 24px;
    height: 24px;
    color: var(--color-accent, #c9a96e);
    stroke-width: 2;
    transition: all 0.3s ease;
    z-index: 2;
    position: relative;
}

.story-step:hover .step-icon {
    color: var(--color-accent-light, #d4b77c);
    transform: scale(1.1);
}

.story-step__content h4 {
    font-size: 1.3rem;
    color: var(--text-primary);
    margin-bottom: 0.8rem;
    font-family: var(--font-heading);
}

.story-step__content p {
    color: var(--text-secondary);
    line-height: 1.6;
    font-style: italic;
}

.story-quote {
    background: var(--glass-bg-light);
    padding: 2rem;
    border-radius: 15px;
    border-left: 4px solid var(--color-accent, #c9a96e);
    margin-bottom: 2rem;
}

.story-quote blockquote {
    font-size: 1.1rem;
    color: var(--text-primary);
    line-height: 1.6;
    margin-bottom: 1rem;
    font-style: italic;
}

.story-quote cite {
    color: var(--color-accent, #c9a96e);
    font-weight: 600;
    font-style: normal;
}

.story-results h4 {
    color: var(--text-primary);
    margin-bottom: 1rem;
    font-family: var(--font-heading);
}

.story-results ul {
    list-style: none;
}

.story-results li {
    position: relative;
    padding-left: 1.5rem;
    margin-bottom: 0.5rem;
    color: var(--text-secondary);
}

.story-results li::before {
    content: "✓";
    position: absolute;
    left: 0;
    color: var(--color-accent, #c9a96e);
    font-weight: bold;
}

.success-story__photos {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.student-avatar {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 2rem;
}

.student-avatar img {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    object-fit: cover;
    border: 4px solid var(--accent-color);
}



.student-portfolio {
    margin-bottom: 2rem;
}

.student-portfolio h4 {
    color: var(--text-primary);
    margin-bottom: 1rem;
    font-family: var(--font-heading);
}

.portfolio-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 0.5rem;
    border-radius: 10px;
    overflow: hidden;
}

.portfolio-item {
    aspect-ratio: 1;
    overflow: hidden;
}

.portfolio-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
}

.portfolio-item:hover img {
    transform: scale(1.05);
}

.story-cta {
    background: var(--glass-bg-light);
    padding: 2rem;
    border-radius: 15px;
    text-align: center;
}

.story-cta h4 {
    color: var(--text-primary);
    margin-bottom: 1rem;
    font-family: var(--font-heading);
}

.story-cta p {
    color: var(--text-secondary);
    margin-bottom: 1rem;
}

.story-cta ul {
    list-style: none;
    text-align: left;
    margin-bottom: 1.5rem;
}

.story-cta li {
    position: relative;
    padding-left: 1.5rem;
    margin-bottom: 0.5rem;
    color: var(--text-secondary);
    font-size: 0.9rem;
}

.story-cta li::before {
    content: "→";
    position: absolute;
    left: 0;
    color: var(--color-accent, #c9a96e);
}

/* Contact Form */
/* Contact Section */
.flashback-contact {
    padding: 80px 0;
    background: linear-gradient(135deg, var(--glass-bg-light) 0%, rgba(255, 255, 255, 0.9) 100%);
}

.contact-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 30px;
    margin-top: 3rem;
    margin-bottom: 3rem;
}

.contact-card {
    background: var(--glass-bg);
    backdrop-filter: blur(20px);
    border: 1px solid var(--glass-border);
    border-radius: 20px;
    box-shadow: var(--shadow-glass);
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
    display: block;
    text-decoration: none;
}

.contact-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-accent, #c9a96e), var(--color-accent-hover, #b8965d));
}

.contact-card:hover {
    transform: translateY(-10px);
    box-shadow: var(--shadow-large);
    background: rgba(255, 255, 255, 0.95);
}

.contact-card__content {
    padding: 2.5rem 2rem;
    text-align: center;
}

.contact-card__icon {
    width: 80px;
    height: 80px;
    margin: 0 auto 1.5rem;
    background: linear-gradient(135deg, var(--glass-bg-light), rgba(255, 255, 255, 0.8));
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid var(--glass-border);
    transition: all 0.3s ease;
}

.contact-card__icon svg {
    width: 35px;
    height: 35px;
    fill: var(--color-accent, #c9a96e);
    transition: all 0.3s ease;
}

.contact-card__icon img {
    width: 35px;
    height: 35px;
    transition: all 0.3s ease;
}

.contact-card__title {
    font-size: 1.4rem;
    font-weight: 600;
    margin-bottom: 1rem;
    color: var(--text-primary);
}

.contact-card__link {
    display: inline-block;
    font-size: 1.2rem;
    color: var(--color-accent, #c9a96e);
    text-decoration: none;
    margin-bottom: 1rem;
    font-weight: 600;
    transition: all 0.3s ease;
}

.contact-card:hover .contact-card__link {
    color: var(--color-accent-hover, #b8965d);
    transform: scale(1.05);
}

.contact-card:hover .contact-card__icon svg {
    fill: var(--color-accent-hover, #b8965d);
    transform: scale(1.1);
}

.contact-card:hover .contact-card__icon img {
    filter: brightness(0) saturate(100%) invert(61%) sepia(31%) saturate(760%) hue-rotate(3deg) brightness(92%) contrast(87%) !important;
    transform: scale(1.1);
}

.contact-card__note {
    color: var(--text-secondary);
    font-size: 0.95rem;
    line-height: 1.5;
}

.contact-info-block {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    margin-top: 2rem;
}

.contact-studio-info,
.contact-schedule-info {
    background: var(--glass-bg);
    backdrop-filter: blur(20px);
    border: 1px solid var(--glass-border);
    border-radius: 15px;
    padding: 2rem;
    text-align: center;
}

.contact-studio-info h3,
.contact-schedule-info h3 {
    color: var(--text-primary);
    margin-bottom: 1rem;
    font-size: 1.3rem;
    font-weight: 600;
}

.contact-studio-info p,
.contact-schedule-info p {
    color: var(--text-secondary);
    margin-bottom: 0.5rem;
    line-height: 1.5;
}

.contact-studio-info p strong,
.contact-schedule-info p strong {
    color: var(--color-accent, #c9a96e);
    font-weight: 600;
}

.contact-studio-info .note,
.contact-schedule-info .note {
    font-size: 0.85rem;
    color: var(--text-muted);
    font-style: italic;
    margin-top: 0.5rem;
}

/* FAQ Section */
.flashback-faq {
    padding: 80px 0;
    background: var(--bg-primary);
}

.faq-list {
    max-width: 800px;
    margin: 3rem auto 0;
}

.faq-item {
    border: 1px solid var(--glass-border);
    border-radius: 15px;
    margin-bottom: 1rem;
    background: var(--glass-bg);
    backdrop-filter: blur(20px);
    overflow: hidden;
    transition: all 0.3s ease;
}

.faq-item.active {
    background: rgba(255, 255, 255, 0.9);
}

.faq-question {
    width: 100%;
    background: none;
    border: none;
    padding: 1.5rem 2rem;
    text-align: left;
    font-size: 1.1rem;
    font-weight: 500;
    color: var(--text-primary);
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: background-color 0.3s ease;
}

.faq-question:hover {
    background: rgba(255, 255, 255, 0.5);
}

.faq-icon {
    font-size: 1.5rem;
    color: var(--color-accent, #c9a96e);
    transition: transform 0.3s ease;
    font-weight: bold;
}

.faq-item.active .faq-icon {
    transform: rotate(45deg);
}

.faq-answer {
    padding: 0 2rem;
    max-height: 0;
    overflow: hidden;
    transition: all 0.3s ease;
}

.faq-item.active .faq-answer {
    max-height: 200px;
    padding: 0 2rem 1.5rem;
}

.faq-answer p {
    color: var(--text-secondary);
    line-height: 1.6;
}

/* Reading Progress Bar */
.reading-progress {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background: rgba(255, 255, 255, 0.1);
    z-index: 1000;
}

.reading-progress__bar {
    height: 100%;
    background: linear-gradient(90deg, var(--color-accent, #c9a96e), var(--color-accent-hover, #b8965d));
    width: 0%;
    transition: width 0.1s ease;
}

/* Scroll to Top Button */
.scroll-to-top {
    position: fixed;
    bottom: 30px;
    right: 30px;
    width: 50px;
    height: 50px;
    background: var(--glass-bg);
    backdrop-filter: blur(20px);
    border: 1px solid var(--glass-border);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    opacity: 0;
    visibility: hidden;
    transform: translateY(20px);
    transition: all 0.3s ease;
    z-index: 100;
    color: var(--color-accent, #c9a96e);
}

.scroll-to-top:hover {
    background: var(--color-accent, #c9a96e);
    color: white;
    transform: translateY(0) scale(1.1);
}

.scroll-to-top.visible {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
}

/* Instructor Stats Section */
.instructor-stats {
    padding: 80px 0;
    background: linear-gradient(135deg, var(--glass-bg-light) 0%, rgba(255, 255, 255, 0.9) 100%);
    backdrop-filter: blur(20px);
    border-top: 1px solid var(--glass-border);
    border-bottom: 1px solid var(--glass-border);
}

.instructor-stats__title {
    text-align: center;
    font-family: var(--font-heading);
    font-size: 2.5rem;
    color: var(--text-primary);
    margin-bottom: 3rem;
}

.instructor-stats__grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 2rem;
    max-width: 1000px;
    margin: 0 auto;
}

.instructor-stat {
    text-align: center;
    padding: 2rem;
    background: var(--glass-bg);
    backdrop-filter: blur(20px);
    border: 1px solid var(--glass-border);
    border-radius: 15px;
    transition: all 0.3s ease;
}

.instructor-stat:hover {
    transform: translateY(-5px);
    box-shadow: var(--shadow-lg);
}

.instructor-stat__number {
    display: block;
    font-size: 3rem;
    font-weight: 700;
    color: var(--color-accent, #c9a96e);
    margin-bottom: 0.5rem;
    font-family: var(--font-heading);
}

.instructor-stat__label {
    font-size: 1rem;
    color: var(--text-secondary);
    line-height: 1.4;
}

/* Course Timeline */
.course-timeline {
    margin-top: 1rem;
}

.timeline-item {
    display: flex;
    align-items: flex-start;
    margin-bottom: 1.5rem;
    padding: 1rem;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.3s ease;
    border: 1px solid transparent;
}

.timeline-item:hover {
    background: var(--glass-bg-light);
    border-color: var(--glass-border);
    transform: translateX(5px);
}

.timeline-item.active {
    background: var(--glass-bg);
    border-color: var(--color-accent, #c9a96e);
    box-shadow: var(--shadow-md);
}

.timeline-marker {
    width: 40px;
    height: 40px;
    background: var(--color-accent, #c9a96e);
    color: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    margin-right: 1rem;
    flex-shrink: 0;
    transition: all 0.3s ease;
}

.timeline-item:hover .timeline-marker {
    transform: scale(1.1);
}

.timeline-content h5 {
    margin: 0 0 0.5rem 0;
    color: var(--text-primary);
    font-weight: 600;
}

.timeline-content p {
    margin: 0;
    color: var(--text-secondary);
    font-size: 0.9rem;
}

/* Course Progress Demo */
.course-progress-demo {
    background: var(--glass-bg);
    backdrop-filter: blur(20px);
    border: 1px solid var(--glass-border);
    border-radius: 20px;
    padding: 3rem;
    margin-bottom: 3rem;
}

.course-progress-demo h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--text-primary);
    font-family: var(--font-heading);
}

.progress-timeline {
    margin-bottom: 1rem;
}

.progress-bar {
    margin-bottom: 1rem;
}

.progress-fill {
    height: 100%;
    background: linear-gradient(90deg, var(--color-accent, #c9a96e), var(--color-accent-hover, #b8965d));
    width: 16.66%;
    border-radius: 2px;
    transition: width 0.5s ease;
}

.progress-steps {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 2rem;
    flex-wrap: wrap;
}

.progress-step {
    background: var(--glass-bg-light);
    border: 2px solid var(--glass-border);
    border-radius: 15px;
    padding: 1rem;
    cursor: pointer;
    transition: all 0.3s ease;
    text-align: center;
    min-width: 120px;
}

.progress-step:hover {
    transform: translateY(-3px);
    box-shadow: var(--shadow-md);
}

.progress-step.active {
    background: var(--color-accent, #c9a96e);
    border-color: var(--color-accent, #c9a96e);
    color: white;
    transform: translateY(-3px);
    box-shadow: 0 8px 25px rgba(201, 169, 110, 0.3);
}

.step-number {
    display: block;
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 0.5rem;
}

.step-title {
    font-size: 0.9rem;
    font-weight: 500;
}

.progress-content {
    min-height: 200px;
}

.week-content {
    display: none;
    padding: 2rem;
    background: var(--glass-bg-light);
    border-radius: 15px;
    border: 1px solid var(--glass-border);
}

.week-content.active {
    display: block;
    animation: fadeInUp 0.5s ease;
}

.week-content h4 {
    color: var(--text-primary);
    margin-bottom: 1rem;
}

.week-content p {
    color: var(--text-secondary);
    margin-bottom: 1rem;
}

.week-content ul {
    list-style: none;
    padding: 0;
}

.week-content li {
    padding: 0.5rem 0;
    color: var(--text-secondary);
    position: relative;
    padding-left: 1.5rem;
}

.week-content li::before {
    content: '•';
    color: var(--color-accent, #c9a96e);
    position: absolute;
    left: 0;
    font-weight: bold;
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Section Header */
.section-header {
    text-align: center;
    margin-bottom: 3rem;
}

.section-header h2 {
    font-family: var(--font-heading);
    font-size: 2.5rem;
    color: var(--text-primary);
    margin-bottom: 1rem;
}

.section-header p {
    font-size: 1.1rem;
    color: var(--text-secondary);
    max-width: 600px;
    margin: 0 auto;
}

/* Responsive Design */
@media (max-width: 768px) {
    .flashback-hero {
        padding: 100px 0 60px;
    }
    
    .flashback-hero__content {
        grid-template-columns: 1fr;
        gap: 2rem;
        text-align: center;
    }
    
    .flashback-hero__title {
        font-size: 2.5rem;
    }
    
    .flashback-hero__stats {
        justify-content: center;
        gap: 1.5rem;
    }
    
    .flashback-hero__actions {
        justify-content: center;
    }
    
    .flashback-instructor__content {
        grid-template-columns: 1fr;
        gap: 2rem;
    }
    
    .courses-grid {
        grid-template-columns: 1fr;
    }
    
    .contact-grid {
        grid-template-columns: 1fr;
        gap: 20px;
    }
    
    .contact-info-block {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
    
    .student-works-grid {
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    }
    
    .testimonials-grid {
        grid-template-columns: 1fr;
    }
    
    .section-header h2 {
        font-size: 2rem;
    }
    
    .success-story__content {
        grid-template-columns: 1fr;
        gap: 3rem;
    }
    
    .story-step {
        gap: 1rem;
    }
    
    .story-step__marker {
        width: 50px;
        height: 50px;
    }
    
    .step-icon {
        width: 20px;
        height: 20px;
    }

    .student-avatar img {
        width: 100px;
        height: 100px;
    }
    
    .portfolio-grid {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 480px) {
    .flashback-hero__title {
        font-size: 2rem;
    }
    
    .flashback-hero__stats {
        flex-direction: column;
        gap: 1rem;
    }
    
    .stat-number {
        font-size: 2rem;
    }
    
    .course-card,
    .testimonial-card {
        padding: 1.5rem;
    }
    
    .contact-card__content {
        padding: 2rem 1.5rem;
    }
    
    .contact-card__icon {
        width: 70px;
        height: 70px;
        margin-bottom: 1rem;
    }
    
    .contact-card__icon svg,
    .contact-card__icon img {
        width: 30px;
        height: 30px;
    }
    
    .contact-card__title {
        font-size: 1.2rem;
    }
    
    .contact-card__link {
        font-size: 1.1rem;
    }
    
    .contact-studio-info,
    .contact-schedule-info {
        padding: 1.5rem;
    }
}

/* Mobile Responsive Styles */
@media (max-width: 768px) {
    .instructor-stats__grid {
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;
    }
    
    .instructor-stat {
        padding: 1.5rem 1rem;
    }
    
    .instructor-stat__number {
        font-size: 2.5rem;
    }
    
    .instructor-stats__title {
        font-size: 2rem;
    }
    
    .progress-steps {
        justify-content: center;
        flex-wrap: wrap;
        gap: 1rem;
    }
    
    .progress-step {
        min-width: 100px;
        padding: 0.8rem;
    }
    
    .course-progress-demo {
        padding: 2rem 1rem;
    }
    
    .timeline-item {
        margin-bottom: 1rem;
        padding: 0.8rem;
    }
    
    .timeline-marker {
        width: 35px;
        height: 35px;
        margin-right: 0.8rem;
    }
    
    .scroll-to-top {
        bottom: 20px;
        right: 20px;
        width: 45px;
        height: 45px;
    }

    .student-avatar img {
        width: 80px;
        height: 80px;
    }
}

@media (max-width: 480px) {
    .instructor-stats__grid {
        grid-template-columns: 1fr;
    }
    
    .instructor-stat__number {
        font-size: 2rem;
    }
    
    .instructor-stats__title {
        font-size: 1.8rem;
    }
    
    .progress-steps {
        justify-content: flex-start;
        flex-direction: row;
        gap: 0.5rem;
        overflow-x: auto;
        padding: 0.5rem 0;
        -webkit-overflow-scrolling: touch;
    }
    
    .progress-step {
        min-width: 60px;
        max-width: 60px;
        padding: 0.3rem;
        margin-bottom: 0;
        flex-shrink: 0;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    
    .step-number {
        font-size: 1.2rem;
        margin-bottom: 0;
    }
    
    .step-title {
        display: none;
    }
    
    .progress-step.active {
        background: var(--color-accent, #c9a96e);
        border-color: var(--color-accent, #c9a96e);
        color: white;
    }
    
    .progress-step.active .step-number {
        color: white;
    }
    
    .week-content {
        padding: 1.5rem;
    }
    
    .course-progress-demo {
        padding: 1rem 0.5rem;
    }
    
    .course-progress-demo h3 {
        font-size: 1.3rem;
        margin-bottom: 1rem;
    }
    
    .student-intro h3 {
        font-size: 1.5rem;
    }
    
    .story-step {
        flex-direction: column;
        text-align: center;
        gap: 1rem;
    }
    
    .story-step__marker {
        width: 40px;
        height: 40px;
        align-self: center;
    }
    
    .story-step__marker .step-icon {
        width: 16px;
        height: 16px;
    }
    
    .story-quote {
        padding: 1.5rem;
    }
    
    .story-cta {
        padding: 1.5rem;
    }
    
    .portfolio-grid {
        grid-template-columns: 1fr;
    }
}

/* Lightbox стили для изображений */
[data-lightbox] {
    cursor: pointer;
    position: relative;
    transition: all 0.3s ease;
}

[data-lightbox]:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
}

[data-lightbox]::before {
    content: "🔍";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: rgba(0, 0, 0, 0.7);
    color: white;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: 2;
}

[data-lightbox]:hover::before {
    opacity: 1;
}

/* Адаптивные стили для lightbox */
@media (max-width: 768px) {
    [data-lightbox]::before {
        width: 35px;
        height: 35px;
        font-size: 1rem;
    }
}
