}

/* Margin утилиты */
.m-0 {
    margin: 0;
}

.m-auto {
    margin: auto;
}

.mx-auto {
    margin-left: auto;
    margin-right: auto;
}

.mt {
    margin-top: var(--spacing-4);
}

.mb {
    margin-bottom: var(--spacing-4);
}

.mb-lg {
    margin-bottom: var(--spacing-8);
}

/* Overflow утилиты */
.overflow-hidden {
    overflow: hidden;
}

.overflow-visible {
    overflow: visible;
}

.overflow-auto {
    overflow: auto;
}

/* Z-index утилиты */
.z-10 {
    z-index: 10;
}

.z-20 {
    z-index: 20;
}

.z-50 {
    z-index: 50;
}

/* Утилиты для изображений */
.img-cover {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.img-contain {
    object-fit: contain;
}

.img-rounded {
    border-radius: var(--radius-lg);
}

.img-circle {
    border-radius: var(--radius-full);
}

/* Hover эффекты */
.hover-scale:hover {
    transform: scale(1.05);
    transition: transform 0.3s ease;
}

.hover-lift:hover {
    transform: translateY(-5px);
    transition: transform 0.3s ease;
}

.hover-rotate:hover {
    transform: rotate(5deg);
    transition: transform 0.3s ease;
}

/* Утилиты видимости */
.visible {
    visibility: visible;
    opacity: 1;
}

.invisible {
    visibility: hidden;
    opacity: 0;
}

.fade-in {
    opacity: 0;
    transition: opacity 0.3s ease;
}

.fade-in.active {
    opacity: 1;
}

/* Pointer events */
.pointer-events-none {
    pointer-events: none;
}

.pointer-events-auto {
    pointer-events: auto;
}

/* User select */
.select-none {
    user-select: none;
}

.select-text {
    user-select: text;
}

/* Курсор */
.cursor-pointer {
    cursor: pointer;
}

.cursor-not-allowed {
    cursor: not-allowed;
}

/* =================================================================
   ДЕКОРАТИВНЫЕ ПЕРЕХОДЫ МЕЖДУ СЕКЦИЯМИ
   Универсальные стили для визуального разделения
   ================================================================= */

/* Элегантный переход с градиентной линией */
.section-separator {
  position: relative;
  margin: 0;
  padding: 2rem 0;
  background: transparent;
}

.section-separator::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    rgba(201, 169, 110, 0.1) 10%,
    rgba(201, 169, 110, 0.3) 25%,
    rgba(201, 169, 110, 0.6) 40%,
    rgba(201, 169, 110, 0.8) 50%,
    rgba(201, 169, 110, 0.6) 60%,
    rgba(201, 169, 110, 0.3) 75%,
    rgba(201, 169, 110, 0.1) 90%,
    transparent 100%);
  transform: translateY(-50%);
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(201, 169, 110, 0.2);
}

/* Декоративный элемент с точками */
.section-separator::after {
  content: '⬥ ⬥ ⬥';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: var(--color-background, #ffffff);
  padding: 0.5rem 1rem;
  color: var(--color-accent, #c9a96e);
  font-size: 1rem;
  letter-spacing: 0.5rem;
  opacity: 0.8;
  border-radius: 20px;
  box-shadow: 0 2px 8px rgba(255, 255, 255, 0.8);
}

/* Вариант с более тонкой линией */
.section-separator--thin::before {
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(201, 169, 110, 0.4), transparent);
  box-shadow: none;
}

.section-separator--thin::after {
  content: '• • •';
  font-size: 0.8rem;
  letter-spacing: 0.3rem;
}

/* =================================================================
   УЛУЧШЕНИЯ ОТСТУПОВ МЕЖДУ СЕКЦИЯМИ
   ================================================================= */

/* Добавляем стандартные отступы между секциями */
.section + .section {
  margin-top: 0;
}

/* Особые отступы для важных переходов */
.flashback-promo + .services {
  margin-top: 0;
}

.services + .process {
  margin-top: 0;
}

.process + .testimonials {
  margin-top: 0;
}

.testimonials + .faq {
  margin-top: 0;
}
