/* ========================================
   GLASSMORPHISM УТИЛИТЫ
   Современные стили для премиального UI
   ======================================== */

/* Базовые glassmorphism классы */
.glass {
    background: var(--glass-bg-light);
    backdrop-filter: var(--glass-blur);
    border: 1px solid var(--glass-border);
}

.glass-heavy {
    background: var(--glass-bg-medium);
    backdrop-filter: var(--glass-blur-heavy);
    border: 1px solid var(--glass-border);
}

.glass-dark {
    background: var(--glass-bg-dark);
    backdrop-filter: var(--glass-blur);
    border: 1px solid rgba(255, 255, 255, 0.1);
    color: var(--color-text-inverse);
}

.glass-accent {
    background: var(--glass-bg-accent);
    backdrop-filter: var(--glass-blur);
    border: 1px solid rgba(var(--color-accent-rgb), 0.3);
}

/* Эффекты глубины */
.depth-1 { box-shadow: var(--shadow-glass-1); }
.depth-2 { box-shadow: var(--shadow-glass-2); }
.depth-3 { box-shadow: var(--shadow-glass-3); }
.depth-4 { box-shadow: var(--shadow-glass-4); }
.depth-5 { box-shadow: var(--shadow-glass-5); }

/* Hover эффекты глубины */
.depth-hover {
    transition: var(--transition-base);
}

.depth-hover:hover {
    transform: translateY(-4px);
    box-shadow: var(--shadow-glass-4);
}

/* Магнитные эффекты */
.magnetic {
    transition: var(--transition-base);
    cursor: pointer;
}

.magnetic:hover {
    transform: translateY(-2px) scale(1.02);
}

/* Морфинг эффекты */
.morph-gentle {
    transition: var(--transition-spring);
    border-radius: var(--radius-xl);
}

.morph-gentle:hover {
    border-radius: var(--radius-2xl);
    transform: scale(1.05);
}

/* Цветные акценты */
.accent-glow {
    position: relative;
    overflow: visible;
}

.accent-glow::before {
    content: '';
    position: absolute;
    inset: -2px;
    background: linear-gradient(
        45deg,
        rgba(var(--color-accent-rgb), 0.2),
        rgba(var(--color-accent-rgb), 0.1),
        rgba(var(--color-accent-rgb), 0.2)
    );
    border-radius: inherit;
    z-index: -1;
    opacity: 0;
    transition: var(--transition-base);
    filter: blur(8px);
}

.accent-glow:hover::before {
    opacity: 1;
}

/* Анимации появления */
.fade-in {
    animation: fadeIn 0.6s ease-out;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.slide-up {
    animation: slideUp 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

@keyframes slideUp {
    from {
        opacity: 0;
        transform: translateY(40px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.zoom-in {
    animation: zoomIn 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

@keyframes zoomIn {
    from {
        opacity: 0;
        transform: scale(0.8);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}

/* Floating эффекты */
.float {
    animation: float 6s ease-in-out infinite;
}

@keyframes float {
    0%, 100% {
        transform: translateY(0px);
    }
    50% {
        transform: translateY(-10px);
    }
}

.float-gentle {
    animation: floatGentle 4s ease-in-out infinite;
}

@keyframes floatGentle {
    0%, 100% {
        transform: translateY(0px);
    }
    50% {
        transform: translateY(-3px);
    }
}

/* Pulse эффекты */
.pulse-soft {
    animation: pulseSoft 2s ease-in-out infinite;
}

@keyframes pulseSoft {
    0%, 100% {
        transform: scale(1);
        opacity: 1;
    }
    50% {
        transform: scale(1.05);
        opacity: 0.8;
    }
}

.pulse-glow {
    animation: pulseGlow 3s ease-in-out infinite;
}

@keyframes pulseGlow {
    0%, 100% {
        box-shadow: var(--shadow-glass-2);
    }
    50% {
        box-shadow: var(--shadow-accent-medium);
    }
}

/* Ripple эффект */
.ripple {
    position: relative;
    overflow: hidden;
}

.ripple::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 0;
    height: 0;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.3);
    transform: translate(-50%, -50%);
    transition: width 0.6s, height 0.6s;
}

.ripple:active::before {
    width: 300px;
    height: 300px;
}

/* Parallax эффекты */
.parallax-gentle {
    transform-style: preserve-3d;
    transition: var(--transition-base);
}

.parallax-gentle:hover {
    transform: perspective(1000px) rotateX(5deg) rotateY(5deg);
}

/* Градиентные акценты */
.gradient-accent {
    background: linear-gradient(
        135deg,
        rgba(var(--color-accent-rgb), 0.1),
        rgba(var(--color-accent-rgb), 0.05)
    );
}

.gradient-shimmer {
    position: relative;
    overflow: hidden;
}

.gradient-shimmer::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(
        90deg,
        transparent,
        rgba(255, 255, 255, 0.2),
        transparent
    );
    transition: left 0.8s ease-in-out;
}

.gradient-shimmer:hover::before {
    left: 100%;
}

/* Состояния интерактивности */
.interactive {
    cursor: pointer;
    transition: var(--transition-base);
    user-select: none;
}

.interactive:hover {
    transform: translateY(-2px);
}

.interactive:active {
    transform: translateY(0);
    transition-duration: var(--transition-micro);
}

/* Специальные borders */
.border-gradient {
    position: relative;
    background: var(--glass-bg-light);
    border-radius: var(--radius-xl);
}

.border-gradient::before {
    content: '';
    position: absolute;
    inset: 0;
    padding: 1px;
    background: linear-gradient(
        45deg,
        rgba(var(--color-accent-rgb), 0.3),
        rgba(var(--color-accent-rgb), 0.1),
        rgba(var(--color-accent-rgb), 0.3)
    );
    border-radius: inherit;
    mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
    mask-composite: exclude;
}

/* Дополнительные утилиты */
.blur-light { backdrop-filter: blur(5px); }
.blur-medium { backdrop-filter: blur(10px); }
.blur-heavy { backdrop-filter: blur(20px); }
.blur-extreme { backdrop-filter: blur(40px); }

.rounded-glass { border-radius: var(--radius-xl); }
.rounded-glass-large { border-radius: var(--radius-2xl); }
.rounded-glass-full { border-radius: var(--radius-full); }
