/* ========================================
   СТРАНИЦА КОНТАКТОВ
   ======================================== */

/* Герой секция */
.contact-hero {
    padding: var(--spacing-20) 0;
    background: linear-gradient(135deg, var(--color-bg-secondary) 0%, var(--color-bg-primary) 100%);
    text-align: center;
}

.contact-hero__content {
    max-width: 800px;
    margin: 0 auto;
}

.contact-hero__photo {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    object-fit: cover;
    margin: 0 auto 30px;
    display: block;
    border: 4px solid var(--color-primary);
    box-shadow: 
        0 10px 30px rgba(0, 0, 0, 0.1),
        0 0 0 8px rgba(214, 186, 140, 0.1);
    transition: all 0.3s ease;
    background: var(--color-bg-primary);
}

.contact-hero__photo:hover {
    transform: scale(1.05);
    box-shadow: 
        0 20px 50px rgba(0, 0, 0, 0.15),
        0 0 0 8px rgba(214, 186, 140, 0.2);
}

.contact-hero__title {
    font-size: var(--font-size-5xl);
    font-weight: 700;
    margin-bottom: var(--spacing-4);
    color: var(--color-text-primary);
}

.contact-hero__subtitle {
    font-size: var(--font-size-lg);
    color: var(--color-text-secondary);
    line-height: 1.6;
}

/* Контактная информация */
.contact-info {
    padding: var(--spacing-20) 0;
}

.contact-info__grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: var(--spacing-8);
    max-width: 1000px;
    margin: 0 auto;
}

.contact-card {
    text-align: center;
    padding: var(--spacing-8);
    background: var(--color-bg-primary);
    border-radius: var(--border-radius-lg);
    border: 1px solid var(--color-border);
    transition: all 0.3s ease;
}

.contact-card:hover {
    transform: translateY(-4px);
    box-shadow: var(--shadow-lg);
    border-color: var(--color-primary);
}

.contact-card__icon {
    width: 60px;
    height: 60px;
    margin: 0 auto var(--spacing-4);
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--color-primary-light);
    border-radius: 50%;
}

.contact-card__icon img,
.contact-card__icon svg {
    width: 30px;
    height: 30px;
}

.contact-card__whatsapp-icon {
    filter: brightness(0) saturate(100%) invert(72%) sepia(23%) saturate(586%) hue-rotate(359deg) brightness(96%) contrast(87%);
}

.contact-card__title {
    font-size: var(--font-size-xl);
    font-weight: 600;
    margin-bottom: var(--spacing-2);
    color: var(--color-text-primary);
}

.contact-card__link {
    display: block;
    font-size: var(--font-size-lg);
    font-weight: 500;
    color: var(--color-primary);
    text-decoration: none;
    margin-bottom: var(--spacing-2);
    transition: color 0.3s ease;
}

.contact-card__link:hover {
    color: var(--color-primary-dark);
    text-decoration: underline;
}

.contact-card__note {
    font-size: var(--font-size-sm);
    color: var(--color-text-secondary);
}

/* Форма обратной связи */
.contact-form-section {
    padding: var(--spacing-20) 0;
    background: var(--color-bg-secondary);
}

.contact-form-wrapper {
    max-width: 600px;
    margin: 0 auto;
}

.contact-form__title {
    font-size: var(--font-size-3xl);
    font-weight: 700;
    text-align: center;
    margin-bottom: var(--spacing-4);
    color: var(--color-text-primary);
}

.contact-form__subtitle {
    font-size: var(--font-size-lg);
    color: var(--color-text-secondary);
    text-align: center;
    margin-bottom: var(--spacing-8);
    line-height: 1.6;
}

.contact-form {
    background: var(--color-bg-primary);
    padding: var(--spacing-8);
    border-radius: var(--border-radius-lg);
    border: 1px solid var(--color-border);
}

.form-group {
    margin-bottom: var(--spacing-6);
}

.form-label {
    display: block;
    font-weight: 500;
    margin-bottom: var(--spacing-2);
    color: var(--color-text-primary);
}

.form-input,
.form-textarea {
    width: 100%;
    padding: var(--spacing-3);
    border: 1px solid var(--color-border);
    border-radius: var(--border-radius-md);
    font-size: var(--font-size-base);
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.form-input:focus,
.form-textarea:focus {
    outline: none;
    border-color: var(--color-primary);
    box-shadow: 0 0 0 3px var(--color-primary-light);
}

.form-textarea {
    resize: vertical;
    min-height: 120px;
}

.form-submit {
    width: 100%;
    padding: var(--spacing-4) var(--spacing-6);
    background: var(--color-primary);
    color: white;
    border: none;
    border-radius: var(--border-radius-md);
    font-size: var(--font-size-lg);
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
}

.form-submit:hover {
    background: var(--color-primary-dark);
    transform: translateY(-2px);
}

.form-submit:active {
    transform: translateY(0);
}

/* Адаптивность */
@media (max-width: 768px) {
    .contact-hero {
        padding: var(--spacing-16) 0;
    }
    
    .contact-hero__photo {
        width: 100px;
        height: 100px;
        margin-bottom: var(--spacing-6);
    }
    
    .contact-hero__title {
        font-size: var(--font-size-3xl);
    }
    
    .contact-info__grid {
        grid-template-columns: 1fr;
        gap: var(--spacing-6);
    }
    
    .contact-form {
        padding: var(--spacing-6);
    }
}

.contacts__info h2 {
    font-size: var(--font-size-2xl);
    margin-bottom: var(--spacing-6);
}

.contacts__item {
    display: flex;
    align-items: center;
    gap: var(--spacing-4);
    margin-bottom: var(--spacing-4);
}

.contacts__icon {
    width: 2rem;
    height: 2rem;
    color: var(--color-accent);
}

@media (min-width: 1024px) {
    .contacts__content {
        grid-template-columns: 1fr 1fr;
        gap: var(--spacing-20);
    }
}
