/* ========================================
   ФУТЕР
   Современный футер с контактами и социальными сетями
   ======================================== */

.footer {
    background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 50%, #1a1a1a 100%);
    color: var(--color-text-inverse);
    padding: var(--spacing-20) 0 var(--spacing-8);
    margin-top: var(--spacing-32);
    position: relative;
    overflow: hidden;
}

.footer::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: 
        radial-gradient(circle at 20% 20%, rgba(201, 169, 110, 0.1) 0%, transparent 50%),
        radial-gradient(circle at 80% 80%, rgba(201, 169, 110, 0.05) 0%, transparent 50%);
    pointer-events: none;
}

.footer__container {
    max-width: var(--container-xl);
    margin: 0 auto;
    padding: 0 var(--spacing-6);
    position: relative;
    z-index: 1;
}

.footer__content {
    display: grid;
    gap: var(--spacing-12);
    grid-template-columns: 1fr;
    margin-bottom: var(--spacing-12);
}

@media (min-width: 768px) {
    .footer__content {
        grid-template-columns: 2fr 1fr 1fr;
    }
}

/* Основная информация */
.footer__main {
    max-width: 400px;
}

.footer__logo {
    display: flex;
    align-items: center;
    gap: var(--spacing-4);
    margin-bottom: var(--spacing-6);
    text-decoration: none;
    color: var(--color-text-inverse);
    transition: var(--transition-fast);
}

.footer__logo:hover {
    transform: translateY(-2px);
}

.footer__logo-text {
    font-size: var(--font-size-2xl);
    font-family: var(--font-display);
    font-weight: var(--font-weight-bold);
    background: linear-gradient(135deg, var(--color-text-inverse) 0%, var(--color-accent) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.footer__description {
    color: rgba(255, 255, 255, 0.7);
    line-height: var(--line-height-relaxed);
    margin-bottom: var(--spacing-8);
    font-size: var(--font-size-lg);
}

/* Социальные сети */
.footer__social {
    display: flex;
    gap: var(--spacing-4);
    margin-bottom: var(--spacing-8);
}

.footer__social-link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 3rem;
    height: 3rem;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: var(--radius-lg);
    color: var(--color-text-inverse);
    text-decoration: none;
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    position: relative;
    overflow: hidden;
}

.footer__social-link::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
    transition: left 0.5s ease;
}

.footer__social-link:hover::before {
    left: 100%;
}

.footer__social-link:hover {
    background: var(--color-accent);
    transform: translateY(-3px) scale(1.1);
    box-shadow: 0 15px 30px rgba(201, 169, 110, 0.4);
    border-color: var(--color-accent);
}

/* Навигация в футере */
.footer__nav h3 {
    font-size: var(--font-size-lg);
    font-weight: var(--font-weight-semibold);
    margin-bottom: var(--spacing-6);
    color: var(--color-text-inverse);
    position: relative;
}

.footer__nav h3::after {
    content: '';
    position: absolute;
    bottom: -8px;
    left: 0;
    width: 30px;
    height: 2px;
    background: var(--color-accent);
    border-radius: var(--radius-full);
}

.footer__nav-list {
    list-style: none;
}

.footer__nav-list li {
    margin-bottom: var(--spacing-3);
}

.footer__nav-link {
    color: rgba(255, 255, 255, 0.7);
    text-decoration: none;
    transition: var(--transition-fast);
    font-size: var(--font-size-base);
    display: inline-block;
}

.footer__nav-link:hover {
    color: var(--color-accent);
    transform: translateX(5px);
}

/* Контактная информация */
.footer__contact {
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(20px);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: var(--radius-xl);
    padding: var(--spacing-6);
    transition: all 0.3s ease;
}

.footer__contact:hover {
    background: rgba(255, 255, 255, 0.08);
    transform: translateY(-5px);
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
}

.footer__contact h3 {
    font-size: var(--font-size-lg);
    font-weight: var(--font-weight-semibold);
    margin-bottom: var(--spacing-6);
    color: var(--color-text-inverse);
    position: relative;
}

.footer__contact h3::after {
    content: '';
    position: absolute;
    bottom: -8px;
    left: 0;
    width: 30px;
    height: 2px;
    background: var(--color-accent);
    border-radius: var(--radius-full);
}

.footer__contact-item {
    display: flex;
    align-items: center;
    gap: var(--spacing-3);
    margin-bottom: var(--spacing-4);
    color: rgba(255, 255, 255, 0.7);
}

.footer__contact-item svg {
    flex-shrink: 0;
    color: var(--color-accent);
}

.footer__contact-link,
.footer__contact-text {
    color: inherit;
    text-decoration: none;
    transition: var(--transition-fast);
}

.footer__contact-link:hover {
    color: var(--color-accent);
}

/* Нижняя часть футера */
.footer__bottom {
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    padding-top: var(--spacing-8);
    margin-top: var(--spacing-8);
}

.footer__bottom-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: var(--spacing-4);
}

.footer__copyright {
    color: rgba(255, 255, 255, 0.6);
    font-size: var(--font-size-sm);
}

.footer__bottom-links {
    display: flex;
    gap: var(--spacing-6);
}

.footer__bottom-link {
    color: rgba(255, 255, 255, 0.6);
    text-decoration: none;
    font-size: var(--font-size-sm);
    transition: var(--transition-fast);
}

.footer__bottom-link:hover {
    color: var(--color-accent);
}

/* Подписка на новости */
.footer__newsletter {
    background-color: rgba(255, 255, 255, 0.05);
    padding: var(--spacing-8);
    border-radius: var(--radius-xl);
    margin-bottom: var(--spacing-12);
    text-align: center;
}

.footer__newsletter h3 {
    font-size: var(--font-size-xl);
    font-weight: var(--font-weight-bold);
    margin-bottom: var(--spacing-2);
    color: var(--color-text-inverse);
}

.footer__newsletter p {
    color: var(--color-gray-300);
    margin-bottom: var(--spacing-6);
}

.footer__newsletter-form {
    display: flex;
    gap: var(--spacing-3);
    max-width: 400px;
    margin: 0 auto;
}

.footer__newsletter-input {
    flex: 1;
    padding: var(--spacing-3) var(--spacing-4);
    background-color: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: var(--radius-lg);
    color: var(--color-text-inverse);
    font-size: var(--font-size-base);
}

.footer__newsletter-input::placeholder {
    color: var(--color-gray-400);
}

.footer__newsletter-button {
    background-color: var(--color-accent);
    color: var(--color-text-inverse);
    padding: var(--spacing-3) var(--spacing-6);
    border: none;
    border-radius: var(--radius-lg);
    font-weight: var(--font-weight-medium);
    cursor: pointer;
    transition: var(--transition-fast);
}

.footer__newsletter-button:hover {
    background-color: var(--color-accent-hover);
}

/* Адаптивность */
@media (max-width: 640px) {
    .footer {
        padding: var(--spacing-16) 0 var(--spacing-6);
    }
    
    .footer__content {
        gap: var(--spacing-8);
    }
    
    .footer__newsletter-form {
        flex-direction: column;
    }
    
    .footer__bottom {
        flex-direction: column;
        text-align: center;
    }
    
    .footer__bottom-links {
        justify-content: center;
    }
}

@media (max-width: 768px) {
    .footer__content {
        grid-template-columns: 1fr;
        text-align: center;
        gap: var(--spacing-8);
    }
    
    .footer__main {
        max-width: none;
    }
    
    .footer__nav h3,
    .footer__contact h3 {
        margin-top: var(--spacing-6);
    }
    
    .footer__bottom-content {
        flex-direction: column;
        text-align: center;
    }
    
    .footer__bottom-links {
        flex-direction: column;
        gap: var(--spacing-2);
    }
}
