/* =================================================================
   ENHANCED FORMS
   Современные интерактивные формы с плавающими лейблами
   ================================================================= */

/* Основные стили форм */
.form-group,
.input-wrapper {
  position: relative;
  margin-bottom: 1.5rem;
}

/* Базовые стили для полей ввода */
.form-group input,
.form-group textarea,
.form-group select,
.input-wrapper input,
.input-wrapper textarea,
.input-wrapper select {
  width: 100%;
  padding: 1rem 1rem 0.5rem 1rem;
  border: 2px solid var(--color-border, #e1e5e9);
  border-radius: 8px;
  background: var(--color-background-light, #ffffff);
  font-size: 1rem;
  font-family: var(--font-primary);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  outline: none;
}

/* Плавающие лейблы */
.floating-label {
  position: absolute;
  left: 1rem;
  top: 1rem;
  color: var(--color-text-muted, #6b7280);
  font-size: 1rem;
  pointer-events: none;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  background: var(--color-background-light, #ffffff);
  padding: 0 0.25rem;
  z-index: 1;
}

.floating-label.active {
  top: -0.5rem;
  left: 0.75rem;
  font-size: 0.75rem;
  color: var(--color-primary, #3b82f6);
  font-weight: 500;
}

/* Состояния фокуса */
.form-group.focused input,
.form-group.focused textarea,
.form-group.focused select,
.input-wrapper.focused input,
.input-wrapper.focused textarea,
.input-wrapper.focused select {
  border-color: var(--color-primary, #3b82f6);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

/* Состояние валидности */
.form-group.valid input,
.form-group.valid textarea,
.input-wrapper.valid input,
.input-wrapper.valid textarea {
  border-color: var(--color-success, #10b981);
}

.form-group.valid .floating-label,
.input-wrapper.valid .floating-label {
  color: var(--color-success, #10b981);
}

/* Состояние ошибки */
.form-group.invalid input,
.form-group.invalid textarea,
.input-wrapper.invalid input,
.input-wrapper.invalid textarea {
  border-color: var(--color-error, #ef4444);
}

.form-group.invalid .floating-label,
.input-wrapper.invalid .floating-label {
  color: var(--color-error, #ef4444);
}

/* Сообщения об ошибках */
.error-message {
  position: absolute;
  bottom: -1.25rem;
  left: 0;
  color: var(--color-error, #ef4444);
  font-size: 0.75rem;
  font-weight: 500;
  animation: shake 0.3s ease-in-out;
}

@keyframes shake {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-3px); }
  75% { transform: translateX(3px); }
}

/* Сообщения об успехе */
.success-message {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem;
  background: linear-gradient(135deg, #10b981, #059669);
  color: white;
  border-radius: 12px;
  margin-top: 1rem;
  animation: slideInUp 0.4s ease-out;
  box-shadow: 0 4px 12px rgba(16, 185, 129, 0.3);
}

.success-icon {
  width: 24px;
  height: 24px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  flex-shrink: 0;
}

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Textarea специальные стили */
.form-group textarea,
.input-wrapper textarea {
  min-height: 120px;
  resize: vertical;
  padding-top: 1.5rem;
}

.form-group textarea ~ .floating-label,
.input-wrapper textarea ~ .floating-label {
  top: 1.5rem;
}

/* Кнопки отправки */
.submit-button,
button[type="submit"],
input[type="submit"] {
  position: relative;
  width: 100%;
  padding: 1rem 2rem;
  background: linear-gradient(135deg, var(--color-primary, #3b82f6), var(--color-primary-dark, #2563eb));
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  overflow: hidden;
}

.submit-button:hover,
button[type="submit"]:hover,
input[type="submit"]:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(59, 130, 246, 0.4);
}

.submit-button:active,
button[type="submit"]:active,
input[type="submit"]:active {
  transform: translateY(0);
}

/* Состояние загрузки */
.submit-button.loading,
button[type="submit"].loading,
input[type="submit"].loading {
  color: transparent;
  pointer-events: none;
}

.submit-button.loading::after,
button[type="submit"].loading::after,
input[type="submit"].loading::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  margin: -10px 0 0 -10px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Стили для чекбоксов и радиокнопок */
.checkbox-wrapper,
.radio-wrapper {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 1rem;
  cursor: pointer;
}

.checkbox-wrapper input[type="checkbox"],
.radio-wrapper input[type="radio"] {
  width: auto;
  margin: 0;
  padding: 0;
}

/* Кастомные чекбоксы */
.custom-checkbox,
.custom-radio {
  position: relative;
  width: 20px;
  height: 20px;
  border: 2px solid var(--color-border, #e1e5e9);
  border-radius: 4px;
  background: var(--color-background-light, #ffffff);
  transition: all 0.2s ease;
  flex-shrink: 0;
}

.custom-radio {
  border-radius: 50%;
}

.checkbox-wrapper input[type="checkbox"]:checked + .custom-checkbox,
.radio-wrapper input[type="radio"]:checked + .custom-radio {
  background: var(--color-primary, #3b82f6);
  border-color: var(--color-primary, #3b82f6);
}

.custom-checkbox::after {
  content: '✓';
  position: absolute;
  top: -2px;
  left: 2px;
  color: white;
  font-size: 12px;
  font-weight: bold;
  opacity: 0;
  transition: opacity 0.2s ease;
}

.checkbox-wrapper input[type="checkbox"]:checked + .custom-checkbox::after {
  opacity: 1;
}

.custom-radio::after {
  content: '';
  position: absolute;
  top: 4px;
  left: 4px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: white;
  opacity: 0;
  transition: opacity 0.2s ease;
}

.radio-wrapper input[type="radio"]:checked + .custom-radio::after {
  opacity: 1;
}

/* Скрываем оригинальные чекбоксы и радиокнопки */
.checkbox-wrapper input[type="checkbox"],
.radio-wrapper input[type="radio"] {
  position: absolute;
  opacity: 0;
  pointer-events: none;
}

/* Responsive */
@media (max-width: 768px) {
  .form-group,
  .input-wrapper {
    margin-bottom: 1.25rem;
  }
  
  .form-group input,
  .form-group textarea,
  .input-wrapper input,
  .input-wrapper textarea {
    padding: 0.875rem;
    font-size: 16px; /* Предотвращает зум на iOS */
  }
  
  .floating-label {
    font-size: 0.875rem;
  }
  
  .floating-label.active {
    font-size: 0.75rem;
  }
}
