/* ========================================
   ПРЕМИАЛЬНЫЕ GLASSMORPHISM ФОРМЫ
   Современные стили с backdrop-filter эффектами
   ======================================== */

/* Группа полей формы */
.form-group {
    margin-bottom: var(--spacing-6);
    position: relative;
}

.form-group:last-child {
    margin-bottom: 0;
}

/* Лейблы с современным дизайном */
.form-label {
    display: block;
    font-size: var(--font-size-sm);
    font-weight: var(--font-weight-medium);
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-2);
    transition: var(--transition-fast);
}

.form-label--required::after {
    content: ' *';
    color: var(--color-error);
    font-weight: var(--font-weight-bold);
}

/* Glassmorphism поля ввода */
.form-input,
.form-textarea,
.form-select {
    width: 100%;
    padding: var(--spacing-4) var(--spacing-5);
    font-size: var(--font-size-base);
    line-height: var(--line-height-normal);
    color: var(--color-text-primary);
    background: var(--glass-bg-light);
    backdrop-filter: var(--glass-blur);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-xl);
    transition: var(--transition-base);
    appearance: none;
    position: relative;
}

.form-input::placeholder,
.form-textarea::placeholder {
    color: var(--color-text-tertiary);
    transition: var(--transition-fast);
}

.form-input:focus,
.form-textarea:focus,
.form-select:focus {
    outline: none;
    border-color: rgba(var(--color-accent-rgb), 0.5);
    background: rgba(255, 255, 255, 0.95);
    box-shadow: 
        var(--shadow-glass-2),
        0 0 0 3px rgba(var(--color-accent-rgb), 0.1);
    transform: translateY(-1px);
}

.form-input:focus::placeholder,
.form-textarea:focus::placeholder {
    opacity: 0.7;
    transform: translateY(-2px);
}

/* Floating labels эффект */
.form-group--floating {
    position: relative;
}

.form-group--floating .form-label {
    position: absolute;
    top: 50%;
    left: var(--spacing-5);
    transform: translateY(-50%);
    font-size: var(--font-size-base);
    color: var(--color-text-tertiary);
    pointer-events: none;
    transition: var(--transition-base);
    background: linear-gradient(
        180deg,
        transparent 40%,
        var(--glass-bg-light) 40%,
        var(--glass-bg-light) 60%,
        transparent 60%
    );
    padding: 0 var(--spacing-2);
    z-index: 1;
}

.form-group--floating .form-input:focus + .form-label,
.form-group--floating .form-input:not(:placeholder-shown) + .form-label,
.form-group--floating .form-textarea:focus + .form-label,
.form-group--floating .form-textarea:not(:placeholder-shown) + .form-label {
    top: 0;
    font-size: var(--font-size-sm);
    color: var(--color-accent);
    font-weight: var(--font-weight-medium);
}

.form-input::placeholder,
.form-textarea::placeholder {
    color: var(--color-text-tertiary);
}

/* Текстовая область */
.form-textarea {
    min-height: 120px;
    resize: vertical;
}

/* Состояния полей */
.form-input--error,
.form-textarea--error,
.form-select--error {
    border-color: var(--color-error);
}

.form-input--error:focus,
.form-textarea--error:focus,
.form-select--error:focus {
    box-shadow: 0 0 0 3px rgba(239, 68, 68, 0.1);
}

.form-input--success,
.form-textarea--success,
.form-select--success {
    border-color: var(--color-success);
}

.form-input--disabled,
.form-textarea--disabled,
.form-select--disabled {
    background-color: var(--color-bg-tertiary);
    color: var(--color-text-tertiary);
    cursor: not-allowed;
}

/* Размеры полей */
.form-input--small,
.form-textarea--small,
.form-select--small {
    padding: var(--spacing-2) var(--spacing-3);
    font-size: var(--font-size-sm);
}

.form-input--large,
.form-textarea--large,
.form-select--large {
    padding: var(--spacing-4) var(--spacing-5);
    font-size: var(--font-size-lg);
}

/* Группа с иконкой */
.form-input-group {
    position: relative;
}

.form-input-group .form-input {
    padding-left: var(--spacing-12);
}

.form-input-group .form-icon {
    position: absolute;
    left: var(--spacing-4);
    top: 50%;
    transform: translateY(-50%);
    color: var(--color-text-tertiary);
    font-size: var(--font-size-lg);
    pointer-events: none;
}

.form-input-group .form-input:focus + .form-icon {
    color: var(--color-accent);
}

/* Чекбоксы и радиокнопки */
.form-checkbox,
.form-radio {
    appearance: none;
    width: 1.25rem;
    height: 1.25rem;
    border: 2px solid var(--color-border-dark);
    background-color: var(--color-bg-primary);
    cursor: pointer;
    transition: var(--transition-fast);
}

.form-checkbox {
    border-radius: var(--radius-base);
}

.form-radio {
    border-radius: var(--radius-full);
}

.form-checkbox:checked,
.form-radio:checked {
    background-color: var(--color-accent);
    border-color: var(--color-accent);
    background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='white' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='m13.854 3.646-7.5 7.5a.5.5 0 0 1-.708 0l-3.5-3.5a.5.5 0 1 1 .708-.708L6 10.293l7.146-7.147a.5.5 0 0 1 .708.708z'/%3e%3c/svg%3e");
    background-size: 0.75rem;
    background-position: center;
    background-repeat: no-repeat;
}

.form-radio:checked {
    background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='white' xmlns='http://www.w3.org/2000/svg'%3e%3ccircle cx='8' cy='8' r='3'/%3e%3c/svg%3e");
}

.form-checkbox:focus,
.form-radio:focus {
    outline: none;
    box-shadow: 0 0 0 3px var(--color-accent-light);
}

/* Лейбл для чекбокса/радио */
.form-check-label {
    display: flex;
    align-items: center;
    gap: var(--spacing-3);
    cursor: pointer;
    font-size: var(--font-size-base);
    color: var(--color-text-primary);
}

/* Сообщения об ошибках */
.form-error {
    display: block;
    margin-top: var(--spacing-2);
    font-size: var(--font-size-sm);
    color: var(--color-error);
}

.form-help {
    display: block;
    margin-top: var(--spacing-2);
    font-size: var(--font-size-sm);
    color: var(--color-text-tertiary);
}

/* Форма контактов */
.contact-form {
    max-width: 600px;
    margin: 0 auto;
    padding: var(--spacing-8);
    background-color: var(--color-bg-primary);
    border-radius: var(--radius-2xl);
    box-shadow: var(--shadow-lg);
}

.contact-form .form-row {
    display: grid;
    gap: var(--spacing-4);
    grid-template-columns: 1fr;
}

@media (min-width: 640px) {
    .contact-form .form-row--2-cols {
        grid-template-columns: 1fr 1fr;
    }
}

/* Анимация загрузки формы */
.form--loading {
    position: relative;
    pointer-events: none;
}

.form--loading::after {
    content: '';
    position: absolute;
    inset: 0;
    background-color: rgba(255, 255, 255, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: inherit;
}

/* Успешная отправка */
.form--success {
    border: 2px solid var(--color-success);
    background-color: rgba(34, 197, 94, 0.05);
}

.form-success-message {
    padding: var(--spacing-4);
    background-color: var(--color-success);
    color: var(--color-text-inverse);
    border-radius: var(--radius-lg);
    text-align: center;
    font-weight: var(--font-weight-medium);
}
