/* ========================================
   ГЛАВНАЯ СТРАНИЦА
   Стили для домашней страницы фотографа
   ======================================== */

/* ========================================
   FLASHBACK PROMO SECTION - СОВРЕМЕННЫЙ ДИЗАЙН
   Секция обучения фотографии в современном стиле
   ======================================== */

/* Основная секция */
.flashback-promo {
    display: block;
    visibility: visible;
    opacity: 1;
    transform: none;
    position: relative;
    z-index: 1;
    padding: 120px 0;
    background: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%);
    overflow: hidden;
}

.flashback-promo::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: radial-gradient(circle at 30% 20%, rgba(201, 169, 110, 0.08) 0%, transparent 50%),
                radial-gradient(circle at 70% 80%, rgba(201, 169, 110, 0.05) 0%, transparent 50%);
    pointer-events: none;
}

/* Декоративная линия снизу для перехода к services */
.flashback-promo::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 2px;
    background: linear-gradient(90deg, 
        transparent 0%, 
        rgba(201, 169, 110, 0.1) 10%,
        rgba(201, 169, 110, 0.4) 25%,
        rgba(201, 169, 110, 0.7) 50%,
        rgba(201, 169, 110, 0.4) 75%,
        rgba(201, 169, 110, 0.1) 90%,
        transparent 100%);
    box-shadow: 0 1px 4px rgba(201, 169, 110, 0.3);
}

.flashback-promo * {
    visibility: visible;
    opacity: 1;
}

.flashback-promo .container {
    display: block;
    visibility: visible;
    opacity: 1;
    transform: none;
    position: relative;
    z-index: 2;
}

/* Заголовок секции */
.flashback-promo__header {
    display: block;
    visibility: visible;
    opacity: 1;
    transform: none;
    text-align: center;
    margin-bottom: 64px;
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
}

.flashback-promo .section-title {
    font-family: var(--font-display);
    font-size: clamp(2.5rem, 5vw, 3.5rem);
    font-weight: 700;
    color: var(--color-text-primary);
    margin-bottom: 24px;
    position: relative;
}

.flashback-promo .section-title::after {
    content: '';
    position: absolute;
    bottom: -12px;
    left: 50%;
    transform: translateX(-50%);
    width: 80px;
    height: 3px;
    background: linear-gradient(90deg, var(--color-accent), var(--color-accent-hover));
    border-radius: 2px;
}

.flashback-promo .section-subtitle {
    font-size: var(--font-size-lg);
    color: var(--color-text-secondary);
    line-height: var(--line-height-relaxed);
    max-width: 500px;
    margin: 0 auto;
}

/* Современная карточка */
.flashback-card {
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(20px);
    border-radius: 24px;
    padding: 0;
    box-shadow: 
        0 20px 40px -12px rgba(0, 0, 0, 0.08),
        0 4px 12px -4px rgba(0, 0, 0, 0.04);
    border: 1px solid rgba(255, 255, 255, 0.8);
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    position: relative;
    overflow: hidden;
    max-width: 1000px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 400px 1fr;
    gap: 0;
    align-items: stretch;
}

.flashback-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(201, 169, 110, 0.06), transparent);
    transition: left 0.8s cubic-bezier(0.4, 0, 0.2, 1);
    z-index: 1;
}

.flashback-card:hover::before {
    left: 100%;
}

.flashback-card:hover {
    transform: translateY(-12px);
    box-shadow: 
        0 32px 64px -12px rgba(0, 0, 0, 0.12),
        0 8px 24px -4px rgba(0, 0, 0, 0.08);
}

/* Блок изображения */
.flashback-card__image {
    position: relative;
    border-radius: 24px 0 0 24px;
    overflow: hidden;
    height: 100%;
    min-height: 500px;
}

.flashback-card__badge {
    position: absolute;
    top: 24px;
    right: 24px;
    background: linear-gradient(135deg, var(--color-accent), var(--color-accent-hover));
    color: var(--color-white);
    padding: 12px 20px;
    border-radius: 50px;
    font-size: var(--font-size-sm);
    font-weight: 600;
    z-index: 3;
    display: flex;
    align-items: center;
    gap: 8px;
    box-shadow: 0 8px 20px rgba(201, 169, 110, 0.3);
    backdrop-filter: blur(8px);
}

.flashback-card__badge .badge-icon {
    font-size: 18px;
}

.flashback-card__image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

.flashback-card:hover .flashback-card__image img {
    transform: scale(1.08);
}

/* Контент карточки */
.flashback-card__content {
    padding: 48px 40px;
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.flashback-card__header {
    margin-bottom: 32px;
}

.flashback-card__title {
    font-family: var(--font-display);
    font-size: var(--font-size-3xl);
    font-weight: 700;
    color: var(--color-text-primary);
    margin-bottom: 12px;
    line-height: 1.2;
}

.flashback-card__subtitle {
    font-size: var(--font-size-lg);
    color: var(--color-accent);
    font-weight: 500;
    line-height: 1.4;
}

.flashback-card__description {
    margin-bottom: 32px;
}

.flashback-card__description p {
    color: var(--color-text-secondary);
    line-height: var(--line-height-relaxed);
    font-size: var(--font-size-base);
}

/* Особенности курса */
.flashback-features {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
    margin-bottom: 32px;
}

.flashback-feature {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 16px;
    background: rgba(201, 169, 110, 0.08);
    border-radius: 12px;
    transition: all 0.3s ease;
    border: 1px solid rgba(201, 169, 110, 0.12);
}

.flashback-feature:hover {
    background: rgba(201, 169, 110, 0.12);
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(201, 169, 110, 0.15);
}

.flashback-feature__icon {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    background: rgba(201, 169, 110, 0.1);
    border-radius: 10px;
    color: var(--color-accent);
    transition: all 0.3s ease;
}

.flashback-feature__icon svg {
    width: 24px;
    height: 24px;
    stroke: currentColor;
}

.flashback-feature:hover .flashback-feature__icon {
    background: rgba(201, 169, 110, 0.2);
    transform: scale(1.05);
}

.flashback-feature__text {
    font-size: var(--font-size-sm);
    color: var(--color-text-primary);
    font-weight: 500;
    line-height: 1.3;
}

/* Статистика */
.flashback-stats {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    margin-bottom: 40px;
}

.flashback-stat {
    text-align: center;
    padding: 20px 16px;
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.4));
    border-radius: 16px;
    border: 1px solid rgba(201, 169, 110, 0.15);
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    position: relative;
    overflow: hidden;
}

.flashback-stat::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    background: linear-gradient(90deg, var(--color-accent), var(--color-accent-hover));
}

.flashback-stat:hover {
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.7));
    transform: translateY(-4px);
    box-shadow: 0 8px 20px rgba(201, 169, 110, 0.2);
}

.flashback-stat__number {
    font-family: var(--font-display);
    font-size: var(--font-size-3xl);
    font-weight: 700;
    color: var(--color-accent);
    display: block;
    margin-bottom: 8px;
    line-height: 1;
}

.flashback-stat__label {
    font-size: var(--font-size-sm);
    color: var(--color-text-secondary);
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.05em;
}

/* Кнопки действий */
.flashback-card__actions {
    display: flex;
    gap: 16px;
    flex-wrap: wrap;
}

.flashback-card__actions .btn {
    padding: 16px 32px;
    font-size: var(--font-size-base);
    font-weight: 600;
    border-radius: 12px;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    text-decoration: none;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;
}

.flashback-card__actions .btn--primary {
    background: linear-gradient(135deg, var(--color-accent), var(--color-accent-hover));
    color: var(--color-white);
    border: none;
    box-shadow: 0 4px 12px rgba(201, 169, 110, 0.3);
}

.flashback-card__actions .btn--primary:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 20px rgba(201, 169, 110, 0.4);
}

.flashback-card__actions .btn--outline {
    background: transparent;
    color: var(--color-accent);
    border: 2px solid var(--color-accent);
}

.flashback-card__actions .btn--outline:hover {
    background: var(--color-accent);
    color: var(--color-white);
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(201, 169, 110, 0.3);
}

/* Адаптивность */
@media (max-width: 1024px) {
    .flashback-card {
        grid-template-columns: 1fr;
        max-width: 600px;
    }
    
    .flashback-card__image {
        border-radius: 24px 24px 0 0;
        min-height: 300px;
    }
    
    .flashback-card__content {
        padding: 32px 24px;
    }
}

@media (max-width: 768px) {
    .flashback-promo {
        padding: 80px 0;
    }
    
    .flashback-promo__header {
        margin-bottom: 48px;
    }
    
    .flashback-features {
        grid-template-columns: 1fr;
        gap: 12px;
    }
    
    .flashback-stats {
        grid-template-columns: 1fr;
        gap: 16px;
    }
    
    .flashback-card__content {
        padding: 24px 20px;
    }
    
    .flashback-card__actions {
        flex-direction: column;
    }
    
    .flashback-card__actions .btn {
        width: 100%;
    }
}

/* Hero секция */
.hero {
    position: relative;
    min-height: calc(100vh - 4rem);
    margin-top: -4rem;
    padding-top: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    background: linear-gradient(135deg, var(--color-bg-primary) 0%, var(--color-bg-secondary) 100%);
}

.hero__background {
    position: absolute;
    inset: 0;
    z-index: 0;
}

.hero__background img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0.3;
}

.hero__overlay {
    position: absolute;
    inset: 0;
    background: linear-gradient(
        135deg,
        rgba(255, 255, 255, 0.9) 0%,
        rgba(255, 255, 255, 0.7) 100%
    );
    z-index: 1;
}

.hero__content {
    position: relative;
    z-index: 2;
    text-align: center;
    max-width: 800px;
    padding: 0 var(--spacing-6);
}

.hero__subtitle {
    font-size: var(--font-size-lg);
    color: var(--color-accent);
    font-weight: var(--font-weight-medium);
    margin-bottom: var(--spacing-4);
    letter-spacing: 0.05em;
    text-transform: uppercase;
}

.hero__title {
    font-family: var(--font-display);
    font-size: var(--font-size-5xl);
    font-weight: var(--font-weight-black);
    line-height: var(--line-height-tight);
    margin-bottom: var(--spacing-6);
    background: linear-gradient(135deg, var(--color-text-primary) 0%, var(--color-accent) 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.hero__description {
    font-size: var(--font-size-xl);
    color: var(--color-text-secondary);
    line-height: var(--line-height-relaxed);
    margin-bottom: var(--spacing-8);
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
}

.hero__actions {
    display: flex;
    gap: var(--spacing-4);
    justify-content: center;
    flex-wrap: wrap;
}

/* Статистика */
.hero__stats {
    margin-top: var(--spacing-16);
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: var(--spacing-8);
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
}

.hero__stat {
    text-align: center;
}

.hero__stat-number {
    font-family: var(--font-display);
    font-size: var(--font-size-3xl);
    font-weight: var(--font-weight-bold);
    color: var(--color-accent);
    display: block;
}

.hero__stat-label {
    font-size: var(--font-size-sm);
    color: var(--color-text-secondary);
    text-transform: uppercase;
    letter-spacing: 0.05em;
    margin-top: var(--spacing-1);
}

/* Секция услуг */
.services {
    padding: var(--spacing-20) 0;
    background: linear-gradient(135deg, var(--color-bg-primary) 0%, var(--color-bg-secondary) 50%, var(--color-bg-primary) 100%);
    position: relative;
    overflow: hidden;
}

.services::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: radial-gradient(circle at 20% 80%, rgba(201, 169, 110, 0.1) 0%, transparent 50%),
                radial-gradient(circle at 80% 20%, rgba(201, 169, 110, 0.05) 0%, transparent 50%);
    z-index: 0;
}

.services__container {
    position: relative;
    z-index: 1;
    max-width: var(--container-xl);
    margin: 0 auto;
    padding: 0 var(--spacing-6);
}

.services__header {
    text-align: center;
    margin-bottom: var(--spacing-16);
}

.services__title {
    font-size: var(--font-size-4xl);
    font-family: var(--font-display);
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-4);
    position: relative;
}

.services__title::after {
    content: '';
    position: absolute;
    bottom: -15px;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
    height: 4px;
    background: linear-gradient(90deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    border-radius: var(--radius-full);
}

.services__subtitle {
    font-size: var(--font-size-lg);
    color: var(--color-text-secondary);
    max-width: 600px;
    margin: 0 auto;
    line-height: var(--line-height-relaxed);
}

.services__grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: var(--spacing-8);
    margin-bottom: var(--spacing-16);
}

.service-card {
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(20px);
    border-radius: var(--radius-2xl);
    padding: var(--spacing-8);
    text-align: center;
    border: 1px solid rgba(255, 255, 255, 0.2);
    transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    position: relative;
    overflow: hidden;
}

.service-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(
        90deg,
        transparent,
        rgba(201, 169, 110, 0.1),
        transparent
    );
    transition: left 0.6s ease;
}

.service-card:hover::before {
    left: 100%;
}

.service-card:hover {
    transform: translateY(-12px) scale(1.02);
    box-shadow: 
        0 25px 50px rgba(0, 0, 0, 0.1),
        0 0 0 1px rgba(201, 169, 110, 0.2);
    background: rgba(255, 255, 255, 0.95);
}

.service-card__icon {
    width: 80px;
    height: 80px;
    margin: 0 auto var(--spacing-6);
    background: linear-gradient(135deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    border-radius: var(--radius-2xl);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: var(--font-size-3xl);
    color: var(--color-text-inverse);
    transition: all 0.4s ease;
    position: relative;
}

.service-card__icon svg {
    position: relative;
    z-index: 2;
    transition: all 0.4s ease;
}

.service-card__icon::after {
    content: '';
    position: absolute;
    inset: 4px;
    background: linear-gradient(135deg, var(--color-accent-hover) 0%, var(--color-accent) 100%);
    border-radius: calc(var(--radius-2xl) - 4px);
    opacity: 0;
    transition: opacity 0.4s ease;
    z-index: 1;
}

.service-card:hover .service-card__icon {
    transform: scale(1.1);
    box-shadow: 0 15px 30px rgba(201, 169, 110, 0.3);
}

.service-card:hover .service-card__icon svg {
    transform: scale(1.05);
    color: white;
}

.service-card:hover .service-card__icon::after {
    opacity: 1;
}

.service-card__title {
    font-size: var(--font-size-xl);
    font-family: var(--font-display);
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-4);
    transition: color 0.3s ease;
}

.service-card:hover .service-card__title {
    color: var(--color-accent);
}

.service-card__description {
    font-size: var(--font-size-base);
    color: var(--color-text-secondary);
    line-height: var(--line-height-relaxed);
    margin-bottom: var(--spacing-6);
}

.service-card__features {
    list-style: none;
    margin-bottom: var(--spacing-6);
}

.service-card__feature {
    display: flex;
    align-items: center;
    gap: var(--spacing-2);
    margin-bottom: var(--spacing-2);
    font-size: var(--font-size-sm);
    color: var(--color-text-secondary);
}

.service-card__feature::before {
    content: '✓';
    color: var(--color-accent);
    font-weight: var(--font-weight-bold);
    flex-shrink: 0;
}

.service-card__price {
    font-size: var(--font-size-2xl);
    font-weight: var(--font-weight-bold);
    color: var(--color-accent);
    margin-bottom: var(--spacing-4);
}

.service-card__price-note {
    font-size: var(--font-size-xs);
    color: var(--color-text-tertiary);
    margin-bottom: var(--spacing-6);
}

.service-card__action {
    width: 100%;
}

/* Особые стили для премиум услуги */
.service-card--premium {
    background: linear-gradient(135deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    color: var(--color-text-inverse);
    border: 2px solid var(--color-accent-hover);
    position: relative;
    transform: scale(1.05);
}

.service-card--premium::after {
    content: 'Популярно';
    position: absolute;
    top: -10px;
    left: 50%;
    transform: translateX(-50%);
    background: var(--color-accent-dark);
    color: var(--color-text-inverse);
    padding: var(--spacing-1) var(--spacing-4);
    font-size: var(--font-size-xs);
    font-weight: var(--font-weight-bold);
    text-transform: uppercase;
    letter-spacing: 1px;
    border-radius: var(--radius-full);
}

.service-card--premium .service-card__title,
.service-card--premium .service-card__description,
.service-card--premium .service-card__feature {
    color: var(--color-text-inverse);
}

.service-card--premium .service-card__icon {
    background: rgba(255, 255, 255, 0.2);
    color: var(--color-text-inverse);
}

.service-card--premium .service-card__price {
    color: var(--color-text-inverse);
}

/* CTA секция услуг */
.services__cta {
    text-align: center;
    padding: var(--spacing-12);
    background: linear-gradient(135deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    border-radius: var(--radius-2xl);
    color: var(--color-text-inverse);
    position: relative;
    overflow: hidden;
}

.services__cta::before {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: radial-gradient(circle, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
    animation: rotate 20s linear infinite;
}

@keyframes rotate {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.services__cta-content {
    position: relative;
    z-index: 1;
}

.services__cta-title {
    font-size: var(--font-size-3xl);
    font-family: var(--font-display);
    margin-bottom: var(--spacing-4);
    color: var(--color-text-inverse);
}

.services__cta-description {
    font-size: var(--font-size-lg);
    margin-bottom: var(--spacing-8);
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
    opacity: 0.9;
}

.services__cta-actions {
    display: flex;
    gap: var(--spacing-4);
    justify-content: center;
    flex-wrap: wrap;
}

/* ========================================
   СЛАЙДЕР
   Современный адаптивный слайдер
   ======================================== */

.slider {
    position: relative;
    height: 100vh;
    overflow: hidden;
}

.slider__container {
    position: relative;
    width: 100%;
    height: 100%;
}

.slider__slide {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity 1s ease-in-out;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    padding-bottom: 15vh;
}

.slider__slide--active {
    opacity: 1;
}

.slider__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
}

.slider__image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.slider__content {
    position: relative;
    z-index: 3;
    text-align: center;
    color: var(--color-text-inverse);
    padding: var(--spacing-6);
    background: rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(10px);
    border-radius: var(--radius-xl);
    max-width: 600px;
    margin: 0 var(--spacing-4);
}

.slider__title {
    font-size: var(--font-size-4xl);
    font-weight: var(--font-weight-bold);
    margin-bottom: var(--spacing-4);
    color: var(--color-text-inverse);
}

.slider__description {
    font-size: var(--font-size-lg);
    margin-bottom: 0;
    color: rgba(255, 255, 255, 0.9);
}

/* Overlay для лучшей читаемости */
.slider__slide::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(
        135deg,
        rgba(0, 0, 0, 0.3) 0%,
        rgba(0, 0, 0, 0.1) 100%
    );
    z-index: 2;
}

/* Элементы управления слайдером */
.slider__controls {
    position: absolute;
    bottom: var(--spacing-8);
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    gap: var(--spacing-3);
    z-index: 4;
}

.slider__dot {
    width: 12px;
    height: 12px;
    border-radius: var(--radius-full);
    background: rgba(255, 255, 255, 0.5);
    cursor: pointer;
    transition: var(--transition-fast);
}

.slider__dot--active {
    background: var(--color-accent);
    transform: scale(1.2);
}

/* Стрелки навигации */
.slider__arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
    border: none;
    width: 50px;
    height: 50px;
    border-radius: var(--radius-full);
    cursor: pointer;
    z-index: 4;
    color: var(--color-text-inverse);
    font-size: var(--font-size-xl);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: var(--transition-fast);
}

.slider__arrow:hover {
    background: rgba(255, 255, 255, 0.3);
    transform: translateY(-50%) scale(1.1);
}

.slider__arrow--prev {
    left: var(--spacing-6);
}

.slider__arrow--next {
    right: var(--spacing-6);
}

/* Контейнер для стрелок */
.slider__arrows {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 4;
    pointer-events: none;
}

.slider__arrows .slider__arrow {
    pointer-events: auto;
}

/* Индикаторы слайдера */
.slider__indicators {
    position: absolute;
    bottom: var(--spacing-8);
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    gap: var(--spacing-3);
    z-index: 4;
}

.slider__indicator {
    width: 12px;
    height: 12px;
    border-radius: var(--radius-full);
    background: rgba(255, 255, 255, 0.5);
    cursor: pointer;
    transition: var(--transition-fast);
    border: 2px solid transparent;
}

.slider__indicator:hover {
    background: rgba(255, 255, 255, 0.7);
    transform: scale(1.1);
}

.slider__indicator--active {
    background: var(--color-accent);
    transform: scale(1.2);
    border-color: rgba(255, 255, 255, 0.3);
}



/* Адаптивные стили для слайдера */
@media (max-width: 768px) {
    .slider__arrow {
        width: 40px;
        height: 40px;
        font-size: var(--font-size-lg);
    }
    
    .slider__arrow--prev {
        left: var(--spacing-4);
    }
    
    .slider__arrow--next {
        right: var(--spacing-4);
    }
    
    .slider__content {
        padding: var(--spacing-4);
        margin: 0 var(--spacing-3);
    }
    
    .slider__title {
        font-size: var(--font-size-2xl);
    }
    
    .slider__description {
        font-size: var(--font-size-base);
    }
    
    .slider__indicators {
        bottom: var(--spacing-6);
    }
    
    .slider__indicator {
        width: 10px;
        height: 10px;
    }
}

@media (max-width: 480px) {
    .slider__arrow {
        width: 36px;
        height: 36px;
        font-size: var(--font-size-base);
    }
    
    .slider__arrow--prev {
        left: var(--spacing-3);
    }
    
    .slider__arrow--next {
        right: var(--spacing-3);
    }
    
    .slider__content {
        padding: var(--spacing-3);
        margin: 0 var(--spacing-2);
    }
    
    .slider__title {
        font-size: var(--font-size-xl);
        margin-bottom: var(--spacing-2);
    }
    
    .slider__description {
        font-size: var(--font-size-sm);
    }
    
    .slider__indicators {
        bottom: var(--spacing-4);
        gap: var(--spacing-2);
    }
    
    .slider__indicator {
        width: 8px;
        height: 8px;
    }
}

