/* Стили для юридических модальных окон */
.legal-modal {
  display: none;
  position: fixed;
  z-index: 10000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(5px);
  animation: fadeIn 0.3s ease-out;
}

.legal-modal.show {
  display: block;
}

.legal-modal__content {
  position: relative;
  background-color: var(--color-background, #ffffff);
  margin: 2% auto;
  padding: 0;
  border-radius: 12px;
  width: 90%;
  max-width: 800px;
  max-height: 90vh;
  overflow: hidden;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  animation: slideIn 0.3s ease-out;
}

.legal-modal__header {
  background: linear-gradient(135deg, var(--color-accent, #c9a96e), var(--color-accent-dark, #a08749));
  color: white;
  padding: 20px 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.legal-modal__header h2 {
  margin: 0;
  font-size: 1.5rem;
  font-weight: 600;
}

.legal-modal__close {
  background: none;
  border: none;
  color: white;
  font-size: 28px;
  font-weight: bold;
  cursor: pointer;
  padding: 0;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: background-color 0.2s;
}

.legal-modal__close:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.legal-modal__body {
  padding: 30px;
  max-height: calc(90vh - 100px);
  overflow-y: auto;
  overflow-x: hidden;
  line-height: 1.6;
  color: var(--color-text, #333);
  font-size: 14px;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.legal-modal__body h3 {
  color: var(--color-accent, #c9a96e);
  margin-top: 1.5em;
  margin-bottom: 0.5em;
  font-weight: 600;
  font-size: 1.1em;
}

.legal-modal__body h3:first-child {
  margin-top: 0;
}

.legal-modal__body p {
  margin-bottom: 1em;
  text-align: justify;
}

.legal-modal__body ul {
  margin: 0.5em 0 1em 1.2em;
  padding-left: 0;
}

.legal-modal__body li {
  margin-bottom: 0.3em;
}

.legal-modal__body strong {
  font-weight: 600;
  color: var(--color-text-dark, #222);
}

/* Анимации */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideIn {
  from {
    transform: translateY(-50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Адаптивность */
@media (max-width: 768px) {
  .legal-modal__content {
    width: 95%;
    margin: 5% auto;
    max-height: 85vh;
  }
  
  .legal-modal__header {
    padding: 15px 20px;
  }
  
  .legal-modal__header h2 {
    font-size: 1.3rem;
  }
  
  .legal-modal__body {
    padding: 20px;
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .legal-modal__content {
    width: 98%;
    margin: 2% auto;
  }
  
  .legal-modal__body {
    padding: 15px;
  }
}