/* ========================================
   GLASSMORPHISM МОДАЛЬНЫЕ ОКНА
   Современные модальные окна с backdrop-filter
   ======================================== */

/* Базовое модальное окно */
.glass-modal {
    position: fixed;
    inset: 0;
    z-index: var(--z-modal);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: var(--spacing-4);
    opacity: 0;
    visibility: hidden;
    transition: var(--transition-base);
}

.glass-modal--active {
    opacity: 1;
    visibility: visible;
}

/* Подложка модального окна */
.glass-modal__backdrop {
    position: absolute;
    inset: 0;
    background: rgba(0, 0, 0, 0.6);
    backdrop-filter: var(--glass-blur);
    cursor: pointer;
    animation: backdropFadeIn 0.3s ease-out;
}

@keyframes backdropFadeIn {
    from {
        opacity: 0;
        backdrop-filter: blur(0px);
    }
    to {
        opacity: 1;
        backdrop-filter: var(--glass-blur);
    }
}

/* Контейнер модального окна */
.glass-modal__container {
    position: relative;
    max-width: 90vw;
    max-height: 90vh;
    width: 100%;
    max-width: 500px;
    background: var(--glass-bg-light);
    backdrop-filter: var(--glass-blur);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-2xl);
    box-shadow: var(--shadow-glass-5);
    overflow: hidden;
    transform: scale(0.9) translateY(20px);
    transition: var(--transition-spring);
}

.glass-modal--active .glass-modal__container {
    transform: scale(1) translateY(0);
}

/* Заголовок модального окна */
.glass-modal__header {
    padding: var(--spacing-6);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    background: linear-gradient(
        135deg,
        rgba(255, 255, 255, 0.1),
        rgba(255, 255, 255, 0.05)
    );
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.glass-modal__title {
    font-size: var(--font-size-xl);
    font-weight: var(--font-weight-semibold);
    color: var(--color-text-primary);
    margin: 0;
}

.glass-modal__close {
    width: 2.5rem;
    height: 2.5rem;
    border: none;
    background: rgba(255, 255, 255, 0.1);
    border-radius: var(--radius-full);
    color: var(--color-text-secondary);
    cursor: pointer;
    transition: var(--transition-fast);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: var(--font-size-lg);
}

.glass-modal__close:hover {
    background: rgba(255, 255, 255, 0.2);
    color: var(--color-text-primary);
    transform: scale(1.1);
}

/* Контент модального окна */
.glass-modal__body {
    padding: var(--spacing-6);
    backdrop-filter: blur(5px);
}

.glass-modal__footer {
    padding: var(--spacing-6);
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    background: linear-gradient(
        135deg,
        rgba(255, 255, 255, 0.05),
        rgba(255, 255, 255, 0.02)
    );
    display: flex;
    gap: var(--spacing-3);
    justify-content: flex-end;
}

/* Варианты модальных окон */
.glass-modal--accent {
    --glass-bg-light: rgba(var(--color-accent-rgb), 0.1);
    --glass-border: rgba(var(--color-accent-rgb), 0.3);
}

.glass-modal--accent .glass-modal__container {
    border: 1px solid rgba(var(--color-accent-rgb), 0.3);
    box-shadow: var(--shadow-accent-medium);
}

.glass-modal--dark {
    --glass-bg-light: rgba(0, 0, 0, 0.8);
    --glass-border: rgba(255, 255, 255, 0.1);
}

.glass-modal--dark .glass-modal__title,
.glass-modal--dark .glass-modal__body {
    color: var(--color-text-inverse);
}

.glass-modal--fullscreen {
    padding: 0;
}

.glass-modal--fullscreen .glass-modal__container {
    max-width: 100vw;
    max-height: 100vh;
    width: 100%;
    height: 100%;
    border-radius: 0;
}

/* Анимации появления */
.glass-modal--slide-up .glass-modal__container {
    transform: translateY(100px);
}

.glass-modal--slide-up.glass-modal--active .glass-modal__container {
    transform: translateY(0);
}

.glass-modal--zoom .glass-modal__container {
    transform: scale(0.5);
}

.glass-modal--zoom.glass-modal--active .glass-modal__container {
    transform: scale(1);
}

.glass-modal--rotate .glass-modal__container {
    transform: scale(0.8) rotate(-5deg);
}

.glass-modal--rotate.glass-modal--active .glass-modal__container {
    transform: scale(1) rotate(0deg);
}

/* Адаптивность */
@media (max-width: 768px) {
    .glass-modal__container {
        max-width: 95vw;
        margin: var(--spacing-4);
    }
    
    .glass-modal__header,
    .glass-modal__body,
    .glass-modal__footer {
        padding: var(--spacing-4);
    }
    
    .glass-modal__title {
        font-size: var(--font-size-lg);
    }
}

/* Утилитарные классы для контента */
.glass-modal .form-group {
    margin-bottom: var(--spacing-4);
}

.glass-modal .btn {
    backdrop-filter: blur(10px);
}
