/* ========================================
   ПРЕМИАЛЬНЫЕ КАРТОЧКИ
   Glassmorphism дизайн с современными эффектами
   ======================================== */

/* Базовая карточка с glassmorphism */
.card {
    position: relative;
    background: var(--glass-bg-light);
    backdrop-filter: var(--glass-blur);
    border: 1px solid var(--glass-border);
    border-radius: var(--radius-xl);
    box-shadow: var(--shadow-glass-2);
    overflow: hidden;
    transition: var(--transition-base);
}

.card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 1px;
    background: linear-gradient(
        90deg,
        transparent,
        rgba(255, 255, 255, 0.4),
        transparent
    );
    opacity: 0;
    transition: var(--transition-base);
}

.card:hover {
    box-shadow: var(--shadow-glass-4);
    transform: translateY(-6px) scale(1.02);
    background: rgba(255, 255, 255, 0.95);
    border-color: rgba(255, 255, 255, 0.4);
}

.card:hover::before {
    opacity: 1;
}

/* Части карточки */
.card__header {
    position: relative;
    padding: var(--spacing-6);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    background: linear-gradient(
        135deg,
        rgba(255, 255, 255, 0.1),
        rgba(255, 255, 255, 0.05)
    );
}

.card__body {
    padding: var(--spacing-6);
    backdrop-filter: blur(5px);
}

.card__footer {
    padding: var(--spacing-6);
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    background: linear-gradient(
        135deg,
        rgba(255, 255, 255, 0.05),
        rgba(255, 255, 255, 0.02)
    );
    backdrop-filter: blur(10px);
}

.card__image {
    width: 100%;
    height: 200px;
    object-fit: cover;
    transition: var(--transition-spring);
    border-radius: var(--radius-lg);
    margin-bottom: var(--spacing-4);
}

.card:hover .card__image {
    transform: scale(1.08) rotate(1deg);
    filter: brightness(1.1) contrast(1.05);
}

/* Варианты карточек с glassmorphism */
.card--glass-accent {
    background: var(--glass-bg-accent);
    border: 1px solid rgba(var(--color-accent-rgb), 0.3);
    box-shadow: var(--shadow-accent-light);
}

.card--glass-accent:hover {
    box-shadow: var(--shadow-accent-medium);
    background: rgba(var(--color-accent-rgb), 0.15);
}

.card--glass-dark {
    background: var(--glass-bg-dark);
    color: var(--color-text-inverse);
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.card--glass-dark:hover {
    background: rgba(0, 0, 0, 0.4);
    border-color: rgba(255, 255, 255, 0.2);
}

.card--elevated {
    box-shadow: var(--shadow-glass-3);
    transform: translateY(-2px);
}

.card--elevated:hover {
    box-shadow: var(--shadow-glass-5);
    transform: translateY(-8px) scale(1.03);
}

.card--floating {
    animation: cardFloat 6s ease-in-out infinite;
}

@keyframes cardFloat {
    0%, 100% {
        transform: translateY(0px);
    }
    50% {
        transform: translateY(-6px);
    }
}

.card--glow {
    position: relative;
    overflow: visible;
}

.card--glow::after {
    content: '';
    position: absolute;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    background: linear-gradient(
        45deg,
        rgba(var(--color-accent-rgb), 0.3),
        rgba(var(--color-accent-rgb), 0.1),
        rgba(var(--color-accent-rgb), 0.3)
    );
    border-radius: var(--radius-xl);
    z-index: -1;
    opacity: 0;
    transition: var(--transition-base);
    filter: blur(10px);
}

.card--glow:hover::after {
    opacity: 1;
    animation: glowPulse 2s ease-in-out infinite;
}

@keyframes glowPulse {
    0%, 100% {
        filter: blur(10px);
    }
    50% {
        filter: blur(20px);
    }
}

.card--rounded-none {
    border-radius: 0;
}

.card--rounded-sm {
    border-radius: var(--radius-sm);
}

/* Карточка портфолио */
.card--portfolio {
    position: relative;
    overflow: hidden;
    aspect-ratio: 4 / 3;
    border-radius: var(--radius-lg);
}

.card--portfolio .card__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: var(--transition-slow);
}

.card--portfolio:hover .card__image {
    transform: scale(1.1);
}

.card--portfolio .card__overlay {
    position: absolute;
    inset: 0;
    background: linear-gradient(
        0deg,
        rgba(0, 0, 0, 0.8) 0%,
        rgba(0, 0, 0, 0.2) 50%,
        transparent 100%
    );
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: var(--spacing-6);
    opacity: 0;
    transition: var(--transition-base);
}

.card--portfolio:hover .card__overlay {
    opacity: 1;
}

.card--portfolio .card__title {
    color: var(--color-text-inverse);
    font-size: var(--font-size-lg);
    font-weight: var(--font-weight-semibold);
    margin-bottom: var(--spacing-2);
}

.card--portfolio .card__subtitle {
    color: var(--color-gray-300);
    font-size: var(--font-size-sm);
}

/* Карточка услуги */
.card--service {
    text-align: center;
    padding: var(--spacing-8);
    border: 2px solid transparent;
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
}

.card--service::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    transform: translateX(-100%);
    transition: transform 0.3s ease;
}

.card--service:hover::before {
    transform: translateX(0);
}

.card--service:hover {
    border-color: var(--color-accent-light);
    background: var(--color-accent-light);
    transform: translateY(-8px);
}

.card--service .card__icon {
    width: 60px;
    height: 60px;
    margin: 0 auto var(--spacing-4);
    background: linear-gradient(135deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    border-radius: var(--radius-full);
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--color-text-inverse);
    font-size: var(--font-size-2xl);
    transition: all 0.3s ease;
}

.card--service:hover .card__icon {
    transform: scale(1.1) rotateY(360deg);
}

/* Карточка отзыва */
.card--testimonial {
    position: relative;
    padding: var(--spacing-8);
    background: linear-gradient(135deg, var(--color-bg-primary) 0%, var(--color-bg-secondary) 100%);
    border-left: 4px solid var(--color-accent);
}

.card--testimonial::before {
    content: '"';
    position: absolute;
    top: var(--spacing-4);
    left: var(--spacing-4);
    font-size: 4rem;
    font-family: var(--font-display);
    color: var(--color-accent);
    opacity: 0.3;
    line-height: 1;
}

.card--testimonial .card__quote {
    font-style: italic;
    font-size: var(--font-size-lg);
    margin-bottom: var(--spacing-6);
    position: relative;
    z-index: 1;
}

.card--testimonial .card__author {
    display: flex;
    align-items: center;
    gap: var(--spacing-4);
}

.card--testimonial .card__avatar {
    width: 50px;
    height: 50px;
    border-radius: var(--radius-full);
    object-fit: cover;
}

/* Сетка карточек */
.cards-grid {
    display: grid;
    gap: var(--spacing-6);
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}

.cards-grid--2-cols {
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
}

.cards-grid--3-cols {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}

.cards-grid--4-cols {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

/* Адаптивность */
@media (min-width: 768px) {
    .cards-grid--2-cols {
        grid-template-columns: repeat(2, 1fr);
    }
    
    .cards-grid--3-cols {
        grid-template-columns: repeat(3, 1fr);
    }
    
    .cards-grid--4-cols {
        grid-template-columns: repeat(4, 1fr);
    }
}

/* ========================================
   ПРОДВИНУТЫЕ ЭФФЕКТЫ КАРТОЧЕК
   Современные hover-эффекты и анимации
   ======================================== */

/* Карточка с glass-эффектом */
.card--glass {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(20px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    box-shadow: 
        0 8px 32px rgba(0, 0, 0, 0.1),
        inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.card--glass:hover {
    background: rgba(255, 255, 255, 0.15);
    transform: translateY(-8px) scale(1.02);
    box-shadow: 
        0 20px 40px rgba(0, 0, 0, 0.15),
        inset 0 1px 0 rgba(255, 255, 255, 0.3);
}

/* Карточка с неоморфизмом */
.card--neumorphism {
    background: var(--color-bg-secondary);
    border: none;
    box-shadow: 
        12px 12px 24px rgba(163, 163, 163, 0.2),
        -12px -12px 24px rgba(255, 255, 255, 0.8);
    transition: all 0.3s ease;
}

.card--neumorphism:hover {
    box-shadow: 
        6px 6px 12px rgba(163, 163, 163, 0.2),
        -6px -6px 12px rgba(255, 255, 255, 0.8),
        inset 2px 2px 4px rgba(163, 163, 163, 0.1),
        inset -2px -2px 4px rgba(255, 255, 255, 0.9);
    transform: translateY(-2px);
}

/* Карточка с градиентной границей */
.card--gradient-border {
    position: relative;
    background: var(--color-bg-primary);
    border: none;
    padding: 2px;
    border-radius: var(--radius-xl);
}

.card--gradient-border::before {
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(45deg, var(--color-accent), var(--color-accent-hover), var(--color-accent));
    border-radius: inherit;
    z-index: -1;
}

.card--gradient-border .card__content {
    background: var(--color-bg-primary);
    border-radius: calc(var(--radius-xl) - 2px);
    height: 100%;
    position: relative;
    z-index: 1;
}

/* Карточка с флип-эффектом */
.card--flip {
    perspective: 1000px;
    height: 300px;
}

.card--flip .card__inner {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.6s;
    transform-style: preserve-3d;
}

.card--flip:hover .card__inner {
    transform: rotateY(180deg);
}

.card--flip .card__front,
.card--flip .card__back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    border-radius: var(--radius-xl);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: var(--spacing-6);
}

.card--flip .card__back {
    transform: rotateY(180deg);
    background: linear-gradient(135deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    color: var(--color-text-inverse);
}

/* Карточка с магнитным эффектом */
.card--magnetic {
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    cursor: pointer;
}

.card--magnetic:hover {
    transform: translateY(-12px) rotateX(5deg);
    box-shadow: 
        0 25px 50px rgba(0, 0, 0, 0.15),
        0 0 0 1px rgba(201, 169, 110, 0.1);
}

/* Карточка с тилт-эффектом */
.card--tilt {
    transition: all 0.3s ease;
    transform-style: preserve-3d;
}

.card--tilt:hover {
    transform: perspective(1000px) rotateX(10deg) rotateY(-5deg) translateZ(20px);
    box-shadow: 
        20px 20px 40px rgba(0, 0, 0, 0.1),
        0 0 20px rgba(201, 169, 110, 0.2);
}

/* Карточка с волновой анимацией */
.card--wave {
    position: relative;
    overflow: hidden;
}

.card--wave::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(
        90deg,
        transparent,
        rgba(255, 255, 255, 0.2),
        transparent
    );
    transition: left 0.6s ease;
}

.card--wave:hover::before {
    left: 100%;
}

/* Сетка карточек с продвинутыми эффектами */
.cards-grid--masonry {
    columns: 3;
    column-gap: var(--spacing-6);
}

.cards-grid--masonry .card {
    break-inside: avoid;
    margin-bottom: var(--spacing-6);
}

@media (max-width: 768px) {
    .cards-grid--masonry {
        columns: 2;
    }
}

@media (max-width: 480px) {
    .cards-grid--masonry {
        columns: 1;
    }
}

/* Карточка с параллакс эффектом */
.card--parallax {
    position: relative;
    overflow: hidden;
    height: 400px;
}

.card--parallax .card__bg {
    position: absolute;
    top: -20%;
    left: -20%;
    width: 140%;
    height: 140%;
    background-size: cover;
    background-position: center;
    transition: transform 0.3s ease;
}

.card--parallax:hover .card__bg {
    transform: scale(1.1);
}

.card--parallax .card__content {
    position: relative;
    z-index: 2;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: var(--spacing-6);
    background: linear-gradient(
        transparent 0%,
        rgba(0, 0, 0, 0.3) 50%,
        rgba(0, 0, 0, 0.8) 100%
    );
    color: var(--color-text-inverse);
}
