/* =================================================================
   TESTIMONIALS SECTION
   Стили для секции отзывов клиентов
   ================================================================= */

.testimonials {
  padding: 6rem 0;
  background: var(--color-background-light);
  position: relative;
}

/* Декоративная линия сверху */
.testimonials::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    rgba(201, 169, 110, 0.15) 20%,
    rgba(201, 169, 110, 0.4) 40%,
    rgba(201, 169, 110, 0.6) 50%,
    rgba(201, 169, 110, 0.4) 60%,
    rgba(201, 169, 110, 0.15) 80%,
    transparent 100%);
  box-shadow: 0 1px 4px rgba(201, 169, 110, 0.2);
}

/* Декоративная линия снизу */
.testimonials::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(201, 169, 110, 0.3), transparent);
}

.testimonials__container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.testimonials__header {
  text-align: center;
  margin-bottom: 4rem;
}

.testimonials__title {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--color-text);
  margin-bottom: 1rem;
}

.testimonials__subtitle {
  font-size: 1.125rem;
  color: var(--color-text-light);
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

.testimonials__slider {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

/* Testimonial Cards */
.testimonial-card {
  background: var(--color-background);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: var(--shadow-md);
  border: 1px solid var(--color-border);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.testimonial-card::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(
    circle,
    rgba(59, 130, 246, 0.1) 0%,
    transparent 70%
  );
  transform: scale(0);
  transition: transform 0.6s ease;
}

.testimonial-card:hover::before {
  transform: scale(1);
}

.testimonial-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-lg);
  border-color: var(--color-primary);
}

.testimonial-card__content {
  margin-bottom: 2rem;
  position: relative;
  z-index: 1;
}

.testimonial-card__rating {
  display: flex;
  gap: 0.25rem;
  margin-bottom: 1rem;
}

.testimonial-card__star {
  color: #fbbf24;
  font-size: 1.25rem;
}

.testimonial-card__text {
  color: var(--color-text-light);
  line-height: 1.7;
  font-size: 1rem;
  font-style: italic;
  position: relative;
}

.testimonial-card__text::before {
  content: '"';
  position: absolute;
  top: -10px;
  left: -15px;
  font-size: 3rem;
  color: var(--color-primary);
  opacity: 0.3;
  font-family: serif;
}

.testimonial-card__author {
  display: flex;
  align-items: center;
  gap: 1rem;
  position: relative;
  z-index: 1;
}

.testimonial-card__avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  overflow: hidden;
  background: var(--color-background-light);
  flex-shrink: 0;
  border: 3px solid var(--color-primary);
}

.testimonial-card__avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.testimonial-card__info {
  flex-grow: 1;
}

.testimonial-card__name {
  font-size: 1.125rem;
  font-weight: 600;
  color: var(--color-text);
  margin-bottom: 0.25rem;
}

.testimonial-card__service {
  color: var(--color-text-muted);
  font-size: 0.875rem;
}

/* Navigation */
.testimonials__navigation {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

/* Hide navigation on desktop */
@media (min-width: 1024px) {
  .testimonials__navigation {
    display: none;
  }
}

.testimonials__nav-btn {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: var(--color-background);
  border: 2px solid var(--color-border);
  color: var(--color-text-light);
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.testimonials__nav-btn:hover {
  background: var(--color-primary);
  border-color: var(--color-primary);
  color: white;
  transform: scale(1.1);
}

.testimonials__nav-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

/* Slider Animation */
.testimonials__slider.sliding .testimonial-card {
  transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Slider functionality */
.testimonials__slides-container {
  overflow: hidden;
  border-radius: 16px;
}

.testimonials__slides-wrapper {
  display: flex;
  transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.testimonials__slides-wrapper .testimonial-card {
  flex: 0 0 100%;
  margin-right: 0;
}

.testimonials__slides-wrapper .testimonial-card:not(.active) {
  opacity: 0.7;
}

/* Dots navigation */
.testimonials__dots {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-top: 2rem;
}

/* Hide dots on desktop */
@media (min-width: 1024px) {
  .testimonials__dots {
    display: none;
  }
}

.testimonials__dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: var(--color-border);
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.testimonials__dot:hover {
  background: var(--color-primary);
  transform: scale(1.2);
}

.testimonials__dot.active {
  background: var(--color-primary);
  width: 32px;
  border-radius: 6px;
}

/* Grid fallback when slider is not active */
@media (min-width: 1024px) {
  .testimonials__slider:not(.slider-active) {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 2rem;
  }
  
  .testimonials__slider:not(.slider-active) .testimonials__slides-container {
    display: contents;
  }
  
  .testimonials__slider:not(.slider-active) .testimonials__slides-wrapper {
    display: contents;
  }
}

/* Mobile slider improvements */
@media (max-width: 768px) {
  .testimonials__slider {
    overflow: hidden;
  }
  
  .testimonials__slides-wrapper .testimonial-card {
    flex: 0 0 100%;
  }
}
