/* =================================================================
   PROGRESSIVE IMAGE LOADING
   Стили для прогрессивной загрузки и отображения изображений
   ================================================================= */

/* Базовые стили для прогрессивных изображений */
.progressive-image {
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
  background-color: var(--color-background-light, #f8fafc);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

/* Состояние с placeholder */
.progressive-image.placeholder-loaded {
  filter: blur(5px);
  transform: scale(1.02);
}

/* Состояние загрузки */
.progressive-image.loading {
  position: relative;
  overflow: hidden;
}

.progressive-image.loading::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    90deg,
    transparent 0%,
    rgba(255, 255, 255, 0.4) 50%,
    transparent 100%
  );
  animation: shimmer 1.5s infinite;
  z-index: 1;
}

.progressive-image.loading::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 32px;
  height: 32px;
  margin: -16px 0 0 -16px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-top: 3px solid var(--color-primary, #3b82f6);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  z-index: 2;
}

/* Состояние успешной загрузки */
.progressive-image.loaded {
  filter: none;
  transform: scale(1);
}

.progressive-image.loaded.placeholder-loaded {
  animation: fadeInSharp 0.6s ease-out;
}

/* Состояние ошибки */
.progressive-image.error {
  background-color: #f3f4f6;
  color: #6b7280;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 200px;
}

.progressive-image.error::before,
.progressive-image.error::after {
  display: none;
}

/* Адаптивные изображения */
.responsive-image {
  width: 100%;
  height: auto;
  max-width: 100%;
}

/* Стили для изображений портфолио */
.portfolio-image.progressive-image {
  border-radius: 12px;
  aspect-ratio: 4/3;
  object-fit: cover;
}

/* Стили для изображений слайдера */
.slider-image.progressive-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Стили для аватаров */
.avatar.progressive-image {
  border-radius: 50%;
  aspect-ratio: 1/1;
  object-fit: cover;
}

/* Hero изображения */
.hero-image.progressive-image {
  width: 100%;
  height: 60vh;
  min-height: 400px;
  object-fit: cover;
}

/* Thumbnail изображения */
.thumbnail.progressive-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 8px;
}

/* Анимации */
@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

@keyframes fadeInSharp {
  from {
    opacity: 0;
    filter: blur(5px);
    transform: scale(1.02);
  }
  to {
    opacity: 1;
    filter: none;
    transform: scale(1);
  }
}

/* Lazy loading с Intersection Observer */
.lazy-image:not(.loaded) {
  opacity: 0;
  transform: translateY(20px);
}

.lazy-image.loaded {
  opacity: 1;
  transform: translateY(0);
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Picture элементы */
picture {
  display: block;
  width: 100%;
}

picture img {
  width: 100%;
  height: auto;
  display: block;
}

/* Стили для галереи */
.gallery-image.progressive-image {
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.gallery-image.progressive-image:hover {
  transform: scale(1.05);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

/* Оптимизация для retina дисплеев */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .progressive-image {
    image-rendering: -webkit-optimize-contrast;
    image-rendering: optimize-contrast;
  }
}

/* Поддержка WebP форматов обрабатывается в JavaScript */

/* Accessibility */
@media (prefers-reduced-motion: reduce) {
  .progressive-image,
  .lazy-image {
    transition: none;
    animation: none;
  }
  
  .progressive-image.loading::before {
    animation: none;
  }
  
  .progressive-image.loading::after {
    animation: none;
  }
}

/* Состояния подключения */
@media (max-width: 768px) {
  .progressive-image.loading::after {
    width: 24px;
    height: 24px;
    margin: -12px 0 0 -12px;
    border-width: 2px;
  }
}

/* Поддержка темной темы */
@media (prefers-color-scheme: dark) {
  .progressive-image {
    background-color: var(--color-background-dark, #1f2937);
  }
  
  .progressive-image.error {
    background-color: #374151;
    color: #d1d5db;
  }
  
  .progressive-image.loading::before {
    background: linear-gradient(
      90deg,
      transparent 0%,
      rgba(255, 255, 255, 0.1) 50%,
      transparent 100%
    );
  }
}

/* Специальные эффекты для изображений */
.image-glow.progressive-image.loaded {
  box-shadow: 
    0 0 20px rgba(59, 130, 246, 0.3),
    0 0 40px rgba(59, 130, 246, 0.1);
}

.image-zoom.progressive-image {
  overflow: hidden;
}

.image-zoom.progressive-image img {
  transition: transform 0.3s ease;
}

.image-zoom.progressive-image:hover img {
  transform: scale(1.1);
}

/* Skeleton loading для изображений */
.image-skeleton {
  background: linear-gradient(
    90deg,
    var(--color-background-light, #f8fafc) 25%,
    var(--color-background-lighter, #f1f5f9) 50%,
    var(--color-background-light, #f8fafc) 75%
  );
  background-size: 200% 100%;
  animation: skeleton-loading 1.5s infinite;
}

@keyframes skeleton-loading {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}
