/* =================================================================
   PROCESS SECTION
   Стили для секции процесса работы
   ================================================================= */

.process {
  padding: 6rem 0;
  background: var(--color-bg-primary);
  position: relative;
}

/* Элегантная декоративная линия сверху */
.process::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    rgba(201, 169, 110, 0.2) 15%,
    rgba(201, 169, 110, 0.5) 35%,
    rgba(201, 169, 110, 0.8) 50%,
    rgba(201, 169, 110, 0.5) 65%,
    rgba(201, 169, 110, 0.2) 85%,
    transparent 100%);
  box-shadow: 0 2px 6px rgba(201, 169, 110, 0.3);
}

/* Декоративная линия снизу */
.process::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(201, 169, 110, 0.3), transparent);
}

.process__container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.process__header {
  text-align: center;
  margin-bottom: 4rem;
}

.process__title {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--color-text-primary);
  margin-bottom: 1rem;
}

.process__subtitle {
  font-size: 1.125rem;
  color: var(--color-text-secondary);
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

.process__steps {
  display: flex;
  flex-direction: column;
  gap: 3rem;
  position: relative;
}

.process__steps::before {
  content: '';
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 3px;
  background: linear-gradient(
    to bottom,
    var(--color-accent),
    var(--color-accent-light),
    var(--color-accent)
  );
  transform: translateX(-50%);
  z-index: 0;
}

/* Process Steps */
.process-step {
  display: flex;
  align-items: center;
  gap: 2rem;
  position: relative;
}

.process-step:nth-child(even) {
  flex-direction: row-reverse;
}

.process-step:nth-child(even) .process-step__content {
  text-align: right;
}

.process-step__number {
  width: 80px;
  height: 80px;
  background: linear-gradient(135deg, var(--color-accent), var(--color-accent-hover));
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  font-weight: 700;
  flex-shrink: 0;
  position: relative;
  z-index: 2;
  box-shadow: 0 4px 20px rgba(201, 169, 110, 0.3);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.process-step:hover .process-step__number {
  transform: scale(1.1);
  box-shadow: 0 6px 30px rgba(201, 169, 110, 0.4);
}

.process-step__content {
  flex: 1;
  background: var(--color-bg-primary);
  padding: 2rem;
  border-radius: 16px;
  box-shadow: var(--shadow-md);
  border: 1px solid var(--color-border);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
}

.process-step__content::before {
  content: '';
  position: absolute;
  top: 50%;
  width: 0;
  height: 0;
  border: 10px solid transparent;
  transform: translateY(-50%);
}

.process-step:nth-child(odd) .process-step__content::before {
  left: -20px;
  border-right-color: var(--color-bg-primary);
}

.process-step:nth-child(even) .process-step__content::before {
  right: -20px;
  border-left-color: var(--color-bg-primary);
}

.process-step:hover .process-step__content {
  transform: translateY(-4px);
  box-shadow: var(--shadow-lg);
  border-color: var(--color-accent);
}

.process-step__title {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--color-text-primary);
  margin-bottom: 0.75rem;
}

.process-step__description {
  color: var(--color-text-secondary);
  line-height: 1.6;
}

/* Responsive */
@media (max-width: 768px) {
  .process {
    padding: 4rem 0;
  }
  
  .process__container {
    padding: 0 1rem;
  }
  
  .process__title {
    font-size: 2rem;
  }
  
  .process__steps {
    gap: 2rem;
  }
  
  .process__steps::before {
    left: 30px;
  }
  
  .process-step {
    flex-direction: column !important;
    align-items: flex-start;
    padding-left: 60px;
  }
  
  .process-step__content {
    text-align: left !important;
  }
  
  .process-step__content::before {
    display: none;
  }
  
  .process-step__number {
    position: absolute;
    left: 0;
    width: 60px;
    height: 60px;
    font-size: 1.25rem;
  }
}

@media (max-width: 480px) {
  .process-step {
    padding-left: 80px;
  }
  
  .process-step__content {
    padding: 1.5rem;
  }
}
