/* =================================================================
   FAQ SECTION
   Стили для секции часто задаваемых вопросов
   ================================================================= */

.faq {
  padding: 3rem 0;
  background: var(--color-background);
  position: relative;
}

/* Декоративная линия сверху */
.faq::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    rgba(201, 169, 110, 0.2) 25%,
    rgba(201, 169, 110, 0.5) 50%,
    rgba(201, 169, 110, 0.2) 75%,
    transparent 100%);
  box-shadow: 0 1px 3px rgba(201, 169, 110, 0.2);
}

/* Декоративная линия снизу */
.faq::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(201, 169, 110, 0.3), transparent);
}

.faq__container {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 2rem;
}

.faq__header {
  text-align: center;
  margin-bottom: 2.5rem;
}

.faq__title {
  font-size: 2rem;
  font-weight: 700;
  color: var(--color-text);
  margin-bottom: 1rem;
}

.faq__subtitle {
  font-size: 1.125rem;
  color: var(--color-text-light);
  line-height: 1.6;
}

.faq__list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

/* FAQ Items */
.faq-item {
  background: var(--color-background-light);
  border-radius: 8px;
  border: 1px solid var(--color-border);
  overflow: hidden;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.faq-item:hover {
  border-color: var(--color-primary);
  box-shadow: var(--shadow-md);
}

.faq-item.active {
  border-color: var(--color-primary);
  box-shadow: 0 4px 20px rgba(59, 130, 246, 0.1);
}

.faq-item__trigger {
  width: 100%;
  padding: 1rem 1.5rem;
  background: none;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.75rem;
  text-align: left;
  transition: all 0.3s ease;
  position: relative;
}

.faq-item__trigger:hover {
  background: rgba(59, 130, 246, 0.05);
}

.faq-item.active .faq-item__trigger {
  background: rgba(59, 130, 246, 0.1);
}

.faq-item__question {
  font-size: 1rem;
  font-weight: 600;
  color: var(--color-text);
  line-height: 1.4;
}

.faq-item__icon {
  flex-shrink: 0;
  color: var(--color-primary);
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.faq-item.active .faq-item__icon {
  transform: rotate(180deg);
}

.faq-item__content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  background: var(--color-background);
  opacity: 1; /* Всегда видимый */
  transform: translateY(0); /* Убираем смещение */
}

.faq-item.active .faq-item__content {
  max-height: 500px; /* Увеличено с 300px */
  border-top: 1px solid var(--color-border-light);
}

.faq-item__answer {
  padding: 1rem 1.5rem;
  color: var(--color-text-light);
  line-height: 1.6;
  margin: 0;
}

