  transform: translateY(0);
  transition-delay: 0.1s;
} */

/* Focus states */
.faq-item__trigger:focus {
  outline: none;
  box-shadow: inset 0 0 0 2px var(--color-primary);
}

/* Responsive */
@media (max-width: 768px) {
  .faq {
    padding: 2.5rem 0;
  }
  
  .faq__container {
    padding: 0 1rem;
  }
  
  .faq__title {
    font-size: 1.75rem;
  }
  
  .faq-item__trigger {
    padding: 0.875rem 1.25rem;
  }
  
  .faq-item__question {
    font-size: 0.95rem;
  }
  
  .faq-item__answer {
    padding: 0.875rem 1.25rem;
  }
}

/* Advanced animations */
@keyframes faqSlideIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.faq-item {
  animation: faqSlideIn 0.6s cubic-bezier(0.4, 0, 0.2, 1);
  animation-fill-mode: both;
}

.faq-item:nth-child(1) { animation-delay: 0.1s; }
.faq-item:nth-child(2) { animation-delay: 0.2s; }
.faq-item:nth-child(3) { animation-delay: 0.3s; }
.faq-item:nth-child(4) { animation-delay: 0.4s; }
.faq-item:nth-child(5) { animation-delay: 0.5s; }

/* Accessibility */
@media (prefers-reduced-motion: reduce) {
  .faq-item {
    animation: none;
  }
  
  .faq-item__content,
  .faq-item__icon {
    transition: none;
  }
}

/* Enhanced hover effects */
.faq-item__trigger::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    90deg,
    transparent 0%,
    rgba(59, 130, 246, 0.05) 50%,
    transparent 100%
  );
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.faq-item__trigger:hover::before {
  transform: scaleX(1);
}

/* Plus/minus icon animation */
.faq-item__icon svg {
  position: relative;
}

.faq-item__icon svg::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 2px;
  height: 12px;
  background: currentColor;
  transform: translate(-50%, -50%);
  transition: opacity 0.3s ease;
}

.faq-item.active .faq-item__icon svg::before {
  opacity: 0;
}

/* FAQ highlight styles */
.faq-highlight {
  background: linear-gradient(120deg, var(--color-primary), var(--color-primary-light));
  color: white;
  padding: 0.1em 0.2em;
  border-radius: 3px;
  font-weight: 600;
}

/* =================================================================
   FAQ SECTION STABILIZATION
   Дополнительные стили для стабильной работы FAQ
   ================================================================= */

/* Базовые стили видимости для FAQ секции */
.faq,
.faq * {
  visibility: visible !important;
  display: block !important;
}

.faq__list {
  display: flex !important;
}

.faq-item__trigger {
  display: flex !important;
}

/* Гарантированная видимость элементов FAQ */
section.faq {
  opacity: 1;
  visibility: visible;
  display: block;
  position: static;
  overflow: visible;
}

.faq .faq__container,
.faq .faq__header,
.faq .faq__title,
.faq .faq__subtitle,
.faq .faq__list,
.faq .faq-item,
.faq .faq-item__trigger,
.faq .faq-item__question,
.faq .faq-item__icon,
.faq .faq-item__answer {
  opacity: 1;
  visibility: visible;
  display: block;
  position: static;
}

/* Специальные стили для flex элементов */
.faq .faq__list {
  display: flex;
  flex-direction: column;
}

.faq .faq-item__trigger {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* Высокоспецифичные стили для анимаций */
.faq .faq-item.slide-up {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
  transition: all 0.3s ease;
}

.faq .faq-item.fade-in {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

/* Устранение конфликтов с AOS */
.faq [data-aos] {
  opacity: 1 !important;
  visibility: visible !important;
  transform: translateY(0) !important;
}

.faq [data-aos].aos-animate {
  opacity: 1 !important;
  transform: translateY(0) !important;
}

/* Принудительная видимость после загрузки */
.page-loaded .faq,
.page-loaded .faq * {
  opacity: 1;
  visibility: visible;
}

/* Принудительная видимость */
.force-visible .faq,
.force-visible .faq * {
  opacity: 1 !important;
  visibility: visible !important;
}

/* =================================================================
   END FAQ STABILIZATION
   ================================================================= */
