/* ========================================
   ПРЕМИАЛЬНЫЙ GLASSMORPHISM ЛАЙТБОКС
   Современный лайтбокс с backdrop-filter эффектами
   ======================================== */

:root {
    --lightbox-bg: rgba(0, 0, 0, 0.9);
    --lightbox-blur: blur(20px);
    --lightbox-glass-bg: rgba(255, 255, 255, 0.1);
    --lightbox-glass-border: rgba(255, 255, 255, 0.2);
    --lightbox-transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.modern-lightbox {
    position: fixed;
    inset: 0;
    z-index: 9999;
    opacity: 0;
    visibility: hidden;
    transition: var(--lightbox-transition);
    background: var(--lightbox-bg);
    backdrop-filter: var(--lightbox-blur);
    display: flex;
    align-items: center;
    justify-content: center;
}

.modern-lightbox--active {
    opacity: 1;
    visibility: visible;
}

.modern-lightbox__overlay {
    position: absolute;
    inset: 0;
    cursor: pointer;
}

.modern-lightbox__container {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    max-width: 90vw;
    max-height: 90vh;
}

.modern-lightbox__content {
    position: relative;
    max-width: 100%;
    max-height: 100%;
    border-radius: 16px;
    overflow: hidden;
    box-shadow: var(--elevation-5);
    transform: scale(0.9);
    transition: transform 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.modern-lightbox--active .modern-lightbox__content {
    transform: scale(1);
}

.modern-lightbox__image {
    width: 100%;
    height: 100%;
    object-fit: contain;
    display: block;
}

/* Кнопки управления */
.modern-lightbox__close,
.modern-lightbox__prev,
.modern-lightbox__next,
.modern-lightbox__zoom,
.modern-lightbox__share {
    position: absolute;
    background: rgba(255, 255, 255, 0.1);
    border: none;
    color: white;
    cursor: pointer;
    border-radius: var(--radius-md);
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;
    backdrop-filter: blur(10px);
}

.modern-lightbox__close:hover,
.modern-lightbox__prev:hover,
.modern-lightbox__next:hover,
.modern-lightbox__zoom:hover,
.modern-lightbox__share:hover {
    background: rgba(255, 255, 255, 0.2);
    transform: scale(1.1);
}

.modern-lightbox__close {
    top: var(--spacing-lg);
    right: var(--spacing-lg);
    width: 48px;
    height: 48px;
    z-index: 10001;
}

.modern-lightbox__prev,
.modern-lightbox__next {
    top: 50%;
    transform: translateY(-50%);
    width: 56px;
    height: 56px;
    z-index: 10001;
}

.modern-lightbox__prev {
    left: var(--spacing-lg);
}

.modern-lightbox__next {
    right: var(--spacing-lg);
}

.modern-lightbox__zoom,
.modern-lightbox__share {
    width: 40px;
    height: 40px;
}

/* Контент */
.modern-lightbox__content {
    position: relative;
    max-width: 90vw;
    max-height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.modern-lightbox__image-container {
    position: relative;
    max-width: 100%;
    max-height: 80vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.modern-lightbox__image {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
    border-radius: var(--radius-lg);
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
    transition: all 0.4s cubic-bezier(0.4, 0.0, 0.2, 1);
    cursor: zoom-in;
}

.modern-lightbox__image:hover {
    transform: scale(1.02);
}

/* Загрузка */
.modern-lightbox__loading {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: none;
    align-items: center;
    justify-content: center;
}

.modern-lightbox__spinner {
    width: 48px;
    height: 48px;
    border: 3px solid rgba(255, 255, 255, 0.2);
    border-top: 3px solid var(--color-accent);
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

/* Информация */
.modern-lightbox__info {
    margin-top: var(--spacing-lg);
    text-align: center;
    color: white;
    max-width: 600px;
}

.modern-lightbox__title {
    font-family: var(--font-secondary);
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: var(--spacing-sm);
    color: white;
}

.modern-lightbox__description {
    font-size: 1rem;
    line-height: 1.6;
    color: rgba(255, 255, 255, 0.8);
    margin-bottom: var(--spacing-md);
}

.modern-lightbox__meta {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: var(--spacing-md);
}

.modern-lightbox__counter {
    font-size: 0.9rem;
    color: rgba(255, 255, 255, 0.7);
    font-weight: 500;
}

.modern-lightbox__actions {
    display: flex;
    gap: var(--spacing-sm);
}

/* Миниатюры */
.modern-lightbox__thumbnails {
    position: absolute;
    bottom: var(--spacing-lg);
    left: 50%;
    transform: translateX(-50%);
    max-width: 90vw;
    overflow-x: auto;
    scrollbar-width: none;
    -ms-overflow-style: none;
}

.modern-lightbox__thumbnails::-webkit-scrollbar {
    display: none;
}

.modern-lightbox__thumbnails-container {
    display: flex;
    gap: var(--spacing-sm);
    padding: var(--spacing-sm);
}

.modern-lightbox__thumbnail {
    flex-shrink: 0;
    width: 60px;
    height: 60px;
    border-radius: var(--radius-sm);
    overflow: hidden;
    cursor: pointer;
    border: 2px solid transparent;
    transition: all 0.3s ease;
    opacity: 0.6;
}

.modern-lightbox__thumbnail:hover {
    opacity: 0.8;
    transform: scale(1.1);
}

.modern-lightbox__thumbnail--active {
    border-color: var(--color-accent);
    opacity: 1;
    transform: scale(1.1);
}

.modern-lightbox__thumbnail img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/* Уведомления */
.modern-lightbox__toast {
    position: absolute;
    bottom: 100px;
    left: 50%;
    transform: translateX(-50%) translateY(20px);
    background: rgba(0, 0, 0, 0.8);
    color: white;
    padding: var(--spacing-md) var(--spacing-lg);
    border-radius: var(--radius-lg);
    backdrop-filter: blur(10px);
    opacity: 0;
    transition: all 0.3s ease;
    z-index: 10002;
}

.modern-lightbox__toast--show {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
}

/* Адаптивность */
@media (max-width: 768px) {
    .modern-lightbox__container {
        padding: var(--spacing-md);
    }
    
    .modern-lightbox__close {
        top: var(--spacing-md);
        right: var(--spacing-md);
        width: 44px;
        height: 44px;
    }
    
    .modern-lightbox__prev,
    .modern-lightbox__next {
        width: 48px;
        height: 48px;
    }
    
    .modern-lightbox__prev {
        left: var(--spacing-md);
    }
    
    .modern-lightbox__next {
        right: var(--spacing-md);
    }
    
    .modern-lightbox__content {
        max-width: 95vw;
        max-height: 95vh;
    }
    
    .modern-lightbox__image-container {
        max-height: 70vh;
    }
    
    .modern-lightbox__title {
        font-size: 1.25rem;
    }
    
    .modern-lightbox__description {
        font-size: 0.9rem;
    }
    
    .modern-lightbox__thumbnails {
        bottom: var(--spacing-md);
        max-width: 95vw;
    }
    
    .modern-lightbox__thumbnail {
        width: 50px;
        height: 50px;
    }
    
    .modern-lightbox__meta {
        flex-direction: column;
        gap: var(--spacing-sm);
        align-items: center;
    }
}

@media (max-width: 480px) {
    .modern-lightbox__prev,
    .modern-lightbox__next {
        display: none;
    }
    
    .modern-lightbox__image-container {
        max-height: 60vh;
    }
    
    .modern-lightbox__thumbnails {
        display: none;
    }
}
