/* ========================================
   СТРАНИЦЫ ГАЛЕРЕЙ
   Стили для отдельных страниц портфолио
   ======================================== */

/* Хлебные крошки */
.breadcrumbs {
    padding: var(--spacing-6) 0;
    background: var(--color-background-alt);
    border-bottom: 1px solid var(--color-border-light);
}

.breadcrumbs__nav {
    display: flex;
    align-items: center;
    gap: var(--spacing-2);
    font-size: var(--font-size-sm);
}

.breadcrumbs__link {
    color: var(--color-text-secondary);
    text-decoration: none;
    transition: color var(--transition-fast);
}

.breadcrumbs__link:hover {
    color: var(--color-accent);
}

.breadcrumbs__separator {
    color: var(--color-text-tertiary);
}

.breadcrumbs__current {
    color: var(--color-text-primary);
    font-weight: var(--font-weight-medium);
}

/* Герой галереи */
.gallery-hero {
    padding: var(--spacing-20) 0;
    background: linear-gradient(135deg, var(--color-background) 0%, var(--color-background-alt) 100%);
    text-align: center;
}

.gallery-hero__title {
    font-size: clamp(2rem, 5vw, 3.5rem);
    font-weight: var(--font-weight-bold);
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-4);
}

.gallery-hero__subtitle {
    font-size: var(--font-size-lg);
    color: var(--color-text-secondary);
    line-height: 1.6;
    max-width: 800px;
    margin: 0 auto var(--spacing-8);
}

.gallery-hero__stats {
    display: flex;
    justify-content: center;
    gap: var(--spacing-8);
    flex-wrap: wrap;
}

.gallery-hero__stat {
    text-align: center;
}

.gallery-hero__stat-number {
    display: block;
    font-size: var(--font-size-2xl);
    font-weight: var(--font-weight-bold);
    color: var(--color-accent);
    margin-bottom: var(--spacing-1);
}

.gallery-hero__stat-label {
    font-size: var(--font-size-sm);
    color: var(--color-text-secondary);
    text-transform: uppercase;
    letter-spacing: 0.05em;
}

/* Галерея фотографий */
.photo-gallery {
    padding: var(--spacing-20) 0;
    display: block;
    visibility: visible;
    opacity: 1;
}

.photo-gallery__grid {
    display: grid;
    visibility: visible;
    opacity: 1;
    grid-template-columns: repeat(auto-fill, minmax(350px, 400px));
    gap: var(--spacing-6);
    justify-content: center;
}

.photo-gallery__item {
    position: relative;
    aspect-ratio: 4/3;
    border-radius: 12px;
    overflow: hidden;
    cursor: pointer;
    transition: transform 0.3s ease;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    display: block;
    visibility: visible;
    opacity: 1;
}

.photo-gallery__item:hover {
    transform: translateY(-4px);
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.15);
}

.photo-gallery__item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.5s ease;
}

.photo-gallery__item:hover img {
    transform: scale(1.05);
}

.photo-gallery__overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: linear-gradient(transparent, rgba(0, 0, 0, 0.9));
    color: white;
    padding: 1.5rem;
    transform: translateY(100%);
    transition: transform 0.3s ease;
    z-index: 2;
}

.photo-gallery__item:hover .photo-gallery__overlay {
    transform: translateY(0);
}

.photo-gallery__title {
    font-size: 1.125rem;
    font-weight: 600;
    margin-bottom: 0.25rem;
    color: white;
}

.photo-gallery__description {
    font-size: 0.875rem;
    opacity: 0.9;
    color: white;
}

/* Информация о съемке */
.service-info {
    padding: var(--spacing-20) 0;
    background: var(--color-background-alt);
}

.service-info__grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: var(--spacing-12);
    align-items: start;
}

.service-info__title {
    font-size: var(--font-size-2xl);
    font-weight: var(--font-weight-bold);
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-6);
}

.service-info__list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.service-info__list li {
    padding: var(--spacing-3) 0;
    border-bottom: 1px solid var(--color-border-light);
    position: relative;
    padding-left: var(--spacing-6);
}

.service-info__list li:before {
    content: "✓";
    position: absolute;
    left: 0;
    color: var(--color-accent);
    font-weight: var(--font-weight-bold);
}

.service-info__list li:last-child {
    border-bottom: none;
}

.service-info__prices-title {
    font-size: var(--font-size-xl);
    font-weight: var(--font-weight-bold);
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-6);
    text-align: center;
}

/* Карточки цен */
.price-card {
    background: var(--color-background);
    border-radius: var(--radius-lg);
    padding: var(--spacing-6);
    margin-bottom: var(--spacing-4);
    border: 2px solid var(--color-border-light);
    transition: all var(--transition-normal);
}

.price-card:hover {
    border-color: var(--color-accent);
    transform: translateY(-2px);
}

.price-card--featured {
    border-color: var(--color-accent);
    position: relative;
}

.price-card--featured:before {
    content: "Популярный";
    position: absolute;
    top: -10px;
    left: 50%;
    transform: translateX(-50%);
    background: var(--color-accent);
    color: var(--color-text-inverse);
    padding: var(--spacing-1) var(--spacing-3);
    border-radius: var(--radius-full);
    font-size: var(--font-size-sm);
    font-weight: var(--font-weight-medium);
}

.price-card__title {
    font-size: var(--font-size-lg);
    font-weight: var(--font-weight-bold);
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-2);
    text-align: center;
}

.price-card__price {
    font-size: var(--font-size-xl);
    font-weight: var(--font-weight-bold);
    color: var(--color-accent);
    text-align: center;
    margin-bottom: var(--spacing-4);
}

.price-card__features {
    list-style: none;
    padding: 0;
    margin: 0;
}

.price-card__features li {
    padding: var(--spacing-2) 0;
    border-bottom: 1px solid var(--color-border-light);
    position: relative;
    padding-left: var(--spacing-5);
    font-size: var(--font-size-sm);
}

.price-card__features li:before {
    content: "•";
    position: absolute;
    left: 0;
    color: var(--color-accent);
}

.price-card__features li:last-child {
    border-bottom: none;
}

/* Индивидуальный расчет стоимости */
.pricing-info {
    background: var(--color-surface);
    border-radius: var(--border-radius-large);
    padding: var(--spacing-8);
    border: 2px solid var(--color-border);
    transition: all 0.3s ease;
}

.pricing-info:hover {
    border-color: var(--color-accent);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.pricing-info__title {
    font-size: var(--font-size-xl);
    font-weight: var(--font-weight-semibold);
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-4);
    text-align: center;
}

.pricing-info__description {
    font-size: var(--font-size-lg);
    color: var(--color-text-secondary);
    text-align: center;
    margin-bottom: var(--spacing-6);
    line-height: 1.6;
}

.pricing-factors {
    margin-bottom: var(--spacing-6);
}

.pricing-factors__title {
    font-size: var(--font-size-lg);
    font-weight: var(--font-weight-medium);
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-4);
}

.pricing-factors__list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.pricing-factors__list li {
    position: relative;
    padding: var(--spacing-3) 0 var(--spacing-3) var(--spacing-6);
    border-bottom: 1px solid var(--color-border);
    font-size: var(--font-size-base);
    color: var(--color-text-secondary);
    line-height: 1.5;
}

.pricing-factors__list li:before {
    content: "→";
    position: absolute;
    left: 0;
    top: var(--spacing-3);
    color: var(--color-accent);
    font-weight: var(--font-weight-bold);
}

.pricing-factors__list li:last-child {
    border-bottom: none;
}

.pricing-factors__list li strong {
    color: var(--color-text-primary);
    font-weight: var(--font-weight-medium);
}

.pricing-contact {
    background: rgba(var(--color-accent-rgb), 0.1);
    border-radius: var(--border-radius-medium);
    padding: var(--spacing-6);
    text-align: center;
}

.pricing-contact__text {
    font-size: var(--font-size-base);
    color: var(--color-text-secondary);
    margin-bottom: var(--spacing-4);
    line-height: 1.6;
}

.pricing-contact__text strong {
    color: var(--color-text-primary);
    font-weight: var(--font-weight-medium);
}

/* CTA секция */
.cta-section {
    padding: var(--spacing-20) 0;
    background: linear-gradient(135deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    color: var(--color-text-inverse);
    text-align: center;
}

.cta-content__title {
    font-size: var(--font-size-2xl);
    font-weight: var(--font-weight-bold);
    margin-bottom: var(--spacing-4);
}

.cta-content__text {
    font-size: var(--font-size-lg);
    margin-bottom: var(--spacing-8);
    opacity: 0.9;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
}

.cta-content__actions {
    display: flex;
    justify-content: center;
    gap: var(--spacing-4);
    flex-wrap: wrap;
}

/* Кнопка "Загрузить еще" */
.photo-gallery__load-more {
    text-align: center;
    margin-top: var(--spacing-12);
}

/* Информация о съемке */
.service-info__technical {
    background: var(--color-background-alt);
    padding: var(--spacing-8);
    border-radius: 12px;
    border: 1px solid var(--color-border-light);
}

.service-info__technical-title {
    font-size: var(--font-size-lg);
    font-weight: var(--font-weight-semibold);
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-6);
}

.technical-info {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-4);
}

.technical-info__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: var(--spacing-3) 0;
    border-bottom: 1px solid var(--color-border-light);
}

.technical-info__item:last-child {
    border-bottom: none;
}

.technical-info__label {
    font-weight: var(--font-weight-medium);
    color: var(--color-text-secondary);
}

.technical-info__value {
    font-weight: var(--font-weight-semibold);
    color: var(--color-text-primary);
}

.gallery-hero__meta {
    display: flex;
    justify-content: center;
    gap: var(--spacing-6);
    margin-top: var(--spacing-6);
}

.gallery-hero__count,
.gallery-hero__type {
    background: rgba(255, 255, 255, 0.1);
    padding: var(--spacing-2) var(--spacing-4);
    border-radius: 20px;
    font-size: var(--font-size-sm);
    font-weight: var(--font-weight-medium);
    color: var(--color-text-secondary);
    border: 1px solid var(--color-border-light);
}

/* Стабильные стили для photo-gallery, предотвращаем исчезновение */
.photo-gallery {
    display: block;
    visibility: visible;
    opacity: 1;
}

.photo-gallery .photo-gallery__grid {
    display: grid;
    visibility: visible;
    opacity: 1;
}

.photo-gallery .photo-gallery__item {
    display: block;
    visibility: visible;
    opacity: 1;
    position: relative;
}

/* Высокая специфичность для стабильности элементов галереи */
section.photo-gallery {
    display: block;
    visibility: visible;
    opacity: 1;
}

section.photo-gallery .photo-gallery__grid {
    display: grid;
    visibility: visible;
    opacity: 1;
}

section.photo-gallery .photo-gallery__item {
    display: block;
    visibility: visible;
    opacity: 1;
}

/* Специфичные селекторы для анимированных элементов */
section.photo-gallery .photo-gallery__item[class*="animate-"] {
    opacity: 1;
    visibility: visible;
}

section.photo-gallery .photo-gallery__item[class*="delay-"] {
    opacity: 1;
    visibility: visible;
}

/* Переопределяем начальные состояния анимаций для галереи */
section.photo-gallery .photo-gallery__item.animate-fade-in-up {
    opacity: 1;
    transform: translateY(0);
}

section.photo-gallery .photo-gallery__item.animate-fade-in-up.animate-in {
    opacity: 1;
    transform: translateY(0);
}

/* Адаптивность */
@media (max-width: 768px) {
    .gallery-hero {
        padding: var(--spacing-16) 0;
    }
    
    .gallery-hero__stats {
        gap: var(--spacing-6);
    }
    
    .photo-gallery {
        padding: var(--spacing-16) 0;
    }
    
    .photo-gallery__grid {
        grid-template-columns: 1fr;
        gap: var(--spacing-4);
    }
    
    .service-info {
        padding: var(--spacing-16) 0;
    }
    
    .service-info__grid {
        grid-template-columns: 1fr;
        gap: var(--spacing-8);
    }
    
    .cta-section {
        padding: var(--spacing-16) 0;
    }
    
    .cta-content__actions {
        flex-direction: column;
        align-items: center;
    }
    
    .lightbox__container {
        max-width: 95vw;
        max-height: 85vh;
    }
    
    .lightbox__close {
        top: -60px;
        right: 0;
        width: 44px;
        height: 44px;
        font-size: 20px;
    }
    
    .lightbox__prev,
    .lightbox__next {
        width: 44px;
        height: 44px;
        font-size: 20px;
    }
    
    .lightbox__prev {
        left: -60px;
    }
    
    .lightbox__next {
        right: -60px;
    }
    
    .lightbox__counter {
        bottom: -60px;
        font-size: 12px;
        padding: 6px 12px;
    }
    
    .gallery-hero__meta {
        flex-direction: column;
        gap: var(--spacing-3);
        align-items: center;
    }
}

@media (max-width: 480px) {
    .breadcrumbs__nav {
        flex-wrap: wrap;
        gap: var(--spacing-1);
    }
    
    .gallery-hero__stats {
        flex-direction: column;
        gap: var(--spacing-4);
    }
    
    .photo-gallery__grid {
        grid-template-columns: 1fr;
    }
    
    .lightbox__prev {
        left: 10px;
        top: 20px;
        transform: none;
    }
    
    .lightbox__next {
        right: 10px;
        top: 20px;
        transform: none;
    }
    
    .lightbox__close {
        top: 10px;
        right: 10px;
    }
    
    .lightbox__counter {
        bottom: 20px;
    }
}
