/* ========================================
   СТРАНИЦА "О ФОТОГРАФЕ" - В СТИЛИСТИКЕ ГЛАВНОЙ СТРАНИЦЫ
   Современные стили в соответствии с дизайн-системой
   ======================================== */

/* История фотографа */
.about-story {
    padding: var(--spacing-20) 0;
    background: linear-gradient(135deg, var(--color-bg-primary) 0%, var(--color-bg-secondary) 50%, var(--color-bg-primary) 100%);
    position: relative;
    overflow: hidden;
}

.about-story::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: radial-gradient(circle at 30% 70%, rgba(201, 169, 110, 0.05) 0%, transparent 50%);
    z-index: 0;
}

.about-story__grid {
    display: grid;
    grid-template-columns: 3fr 1fr;
    gap: var(--spacing-12);
    align-items: center;
    position: relative;
    z-index: 1;
    max-width: var(--container-xl);
    margin: 0 auto;
    padding: 0 var(--spacing-6);
}

.about-story__title {
    font-family: var(--font-display);
    font-size: var(--font-size-4xl);
    font-weight: var(--font-weight-bold);
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-6);
    position: relative;
}

.about-story__title::after {
    content: '';
    position: absolute;
    bottom: -15px;
    left: 0;
    width: 100px;
    height: 4px;
    background: linear-gradient(90deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    border-radius: var(--radius-full);
}

.about-story__text {
    font-size: var(--font-size-lg);
    line-height: var(--line-height-relaxed);
    color: var(--color-text-secondary);
    margin-bottom: var(--spacing-4);
}

.about-story__text:last-child {
    margin-bottom: 0;
}

.about-story__photo {
    border-radius: var(--radius-2xl);
    overflow: hidden;
    box-shadow: var(--shadow-2xl);
    transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    height: 400px;
    max-width: 100%;
    width: auto;
    position: relative;
}

.about-story__photo::before {
    content: '';
    position: absolute;
    inset: 0;
    background: rgba(201, 169, 110, 0.1);
    opacity: 0;
    transition: opacity 0.3s ease;
    border-radius: var(--radius-2xl);
    z-index: 1;
}

.about-story__photo:hover {
    transform: translateY(-8px);
    box-shadow: var(--shadow-4xl);
}

.about-story__photo:hover::before {
    opacity: 1;
}

.about-story__photo img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
}

/* Лайтбокс для фото */
.photo-lightbox {
    position: relative;
    cursor: pointer;
    overflow: hidden;
}

.about-story__photo-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
    transition: transform 0.3s ease;
}

.photo-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.3s ease;
    backdrop-filter: blur(2px);
}

.photo-overlay__icon {
    color: white;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
}

.photo-overlay__icon svg {
    width: 32px;
    height: 32px;
}

.photo-overlay__icon span {
    font-size: 0.875rem;
    font-weight: 500;
}

.photo-lightbox:hover .photo-overlay {
    opacity: 1;
}

.photo-lightbox:hover .about-story__photo-img {
    transform: scale(1.05);
}

/* Полноэкранный лайтбокс */
.lightbox-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.95);
    z-index: 10000;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
}

.lightbox-modal.active {
    opacity: 1;
    visibility: visible;
}

.lightbox-content {
    position: relative;
    max-width: 90vw;
    max-height: 90vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.lightbox-image {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
    border-radius: 8px;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
}

.lightbox-close {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 40px;
    height: 40px;
    background: rgba(255, 255, 255, 0.1);
    border: none;
    border-radius: 50%;
    color: white;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background 0.3s ease;
    backdrop-filter: blur(10px);
}

.lightbox-close:hover {
    background: rgba(255, 255, 255, 0.2);
}

.lightbox-close svg {
    width: 20px;
    height: 20px;
}

/* Направления съемки */
.about-philosophy {
    padding: var(--spacing-20) 0;
    background: linear-gradient(135deg, var(--color-bg-primary) 0%, var(--color-bg-secondary) 100%);
    position: relative;
    overflow: hidden;
}

.about-philosophy::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: radial-gradient(circle at 70% 30%, rgba(201, 169, 110, 0.08) 0%, transparent 50%);
    z-index: 0;
}

.about-philosophy__content-centered {
    max-width: var(--container-xl);
    margin: 0 auto;
    padding: 0 var(--spacing-6);
    text-align: center;
    position: relative;
    z-index: 1;
}

.about-philosophy__title {
    font-family: var(--font-display);
    font-size: var(--font-size-4xl);
    font-weight: var(--font-weight-bold);
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-4);
    position: relative;
}

.about-philosophy__title::after {
    content: '';
    position: absolute;
    bottom: -15px;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
    height: 4px;
    background: linear-gradient(90deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    border-radius: var(--radius-full);
}

.about-philosophy__text {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: var(--spacing-8);
    margin-top: var(--spacing-16);
}

.service-direction {
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(20px);
    padding: var(--spacing-8);
    border-radius: var(--radius-2xl);
    text-align: center;
    border: 1px solid rgba(255, 255, 255, 0.2);
    transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    position: relative;
    overflow: hidden;
}

.service-direction::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-accent), var(--color-accent-hover));
    transform: scaleX(0);
    transition: transform 0.3s ease;
}

.service-direction:hover::before {
    transform: scaleX(1);
}

.service-direction:hover {
    transform: translateY(-8px);
    box-shadow: var(--shadow-2xl);
    background: rgba(255, 255, 255, 0.95);
}

.service-direction__icon {
    font-size: var(--font-size-4xl);
    margin-bottom: var(--spacing-4);
    display: block;
    color: var(--color-accent);
}

.service-direction__title {
    font-size: var(--font-size-xl);
    font-weight: var(--font-weight-semibold);
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-3);
}

.service-direction__description {
    font-size: var(--font-size-base);
    color: var(--color-text-secondary);
    line-height: var(--line-height-relaxed);
}

/* Фотошкола */
.flashback-info {
    background: rgba(255, 255, 255, 0.9);
    backdrop-filter: blur(20px);
    padding: var(--spacing-10);
    border-radius: var(--radius-2xl);
    box-shadow: var(--shadow-xl);
    margin: var(--spacing-12) 0;
    border: 1px solid rgba(201, 169, 110, 0.2);
    position: relative;
    overflow: hidden;
}

.flashback-info::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 6px;
    height: 100%;
    background: linear-gradient(135deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
}

.flashback-info__title {
    font-family: var(--font-display);
    font-size: var(--font-size-3xl);
    font-weight: var(--font-weight-bold);
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-4);
}

.flashback-info__description {
    font-size: var(--font-size-lg);
    line-height: var(--line-height-relaxed);
    color: var(--color-text-secondary);
    margin-bottom: var(--spacing-6);
}

.flashback-info__features {
    list-style: none;
    padding: 0;
    margin: var(--spacing-6) 0;
}

.flashback-info__features li {
    padding: var(--spacing-3) 0;
    border-bottom: 1px solid var(--color-border-light);
    font-size: var(--font-size-base);
    color: var(--color-text-secondary);
    position: relative;
    padding-left: var(--spacing-8);
}

.flashback-info__features li::before {
    content: '✓';
    position: absolute;
    left: 0;
    color: var(--color-success);
    font-weight: var(--font-weight-bold);
    font-size: var(--font-size-lg);
}

.flashback-info__features li:last-child {
    border-bottom: none;
}

.flashback-info__link {
    display: inline-flex;
    align-items: center;
    gap: var(--spacing-2);
    background: linear-gradient(135deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    color: var(--color-white);
    padding: var(--spacing-4) var(--spacing-8);
    border-radius: var(--radius-full);
    text-decoration: none;
    font-weight: var(--font-weight-semibold);
    font-size: var(--font-size-base);
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    margin-top: var(--spacing-4);
    box-shadow: var(--shadow-lg);
}

.flashback-info__link:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-xl);
    background: linear-gradient(135deg, var(--color-accent-hover) 0%, var(--color-accent-dark) 100%);
}

/* Преимущества */
.advantages-section {
    background: linear-gradient(135deg, var(--color-bg-primary) 0%, var(--color-bg-secondary) 50%, var(--color-bg-primary) 100%);
    padding: var(--spacing-20) 0;
    position: relative;
    overflow: hidden;
}

.advantages-section::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: radial-gradient(circle at 60% 40%, rgba(201, 169, 110, 0.06) 0%, transparent 50%);
    z-index: 0;
}

.advantages-section__title {
    font-family: var(--font-display);
    font-size: var(--font-size-4xl);
    font-weight: var(--font-weight-bold);
    text-align: center;
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-4);
    position: relative;
    z-index: 1;
}

.advantages-section__title::after {
    content: '';
    position: absolute;
    bottom: -15px;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
    height: 4px;
    background: linear-gradient(90deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    border-radius: var(--radius-full);
}

.advantages-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: var(--spacing-8);
    max-width: var(--container-xl);
    margin: var(--spacing-16) auto 0;
    padding: 0 var(--spacing-6);
    position: relative;
    z-index: 1;
}

.advantage-item {
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(20px);
    padding: var(--spacing-8);
    border-radius: var(--radius-2xl);
    text-align: center;
    border: 1px solid rgba(255, 255, 255, 0.2);
    transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    position: relative;
    overflow: hidden;
}

.advantage-item::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--color-accent), var(--color-accent-hover));
    transform: scaleX(0);
    transition: transform 0.3s ease;
}

.advantage-item:hover::before {
    transform: scaleX(1);
}

.advantage-item:hover {
    transform: translateY(-8px);
    box-shadow: var(--shadow-2xl);
    background: rgba(255, 255, 255, 0.95);
}

.advantage-item__icon {
    font-size: var(--font-size-5xl);
    margin-bottom: var(--spacing-6);
    display: block;
    color: var(--color-accent);
}

.advantage-item__title {
    font-size: var(--font-size-2xl);
    font-weight: var(--font-weight-bold);
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-4);
}

.advantage-item__description {
    font-size: var(--font-size-base);
    color: var(--color-text-secondary);
    line-height: var(--line-height-relaxed);
}

/* Факты */
.facts-section {
    padding: var(--spacing-20) 0;
    background: linear-gradient(135deg, var(--color-bg-primary) 0%, var(--color-bg-secondary) 100%);
    position: relative;
    overflow: hidden;
}

.facts-section::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: radial-gradient(circle at 80% 60%, rgba(201, 169, 110, 0.05) 0%, transparent 50%);
    z-index: 0;
}

.about-facts__title {
    font-family: var(--font-display);
    font-size: var(--font-size-4xl);
    font-weight: var(--font-weight-bold);
    text-align: center;
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-4);
    position: relative;
    z-index: 1;
}

.about-facts__title::after {
    content: '';
    position: absolute;
    bottom: -15px;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
    height: 4px;
    background: linear-gradient(90deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    border-radius: var(--radius-full);
}

.facts-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: var(--spacing-8);
    max-width: var(--container-lg);
    margin: var(--spacing-16) auto 0;
    padding: 0 var(--spacing-6);
    position: relative;
    z-index: 1;
}

.fact-item {
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(20px);
    padding: var(--spacing-8);
    border-radius: var(--radius-2xl);
    text-align: center;
    transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    border: 1px solid rgba(255, 255, 255, 0.3);
    position: relative;
    overflow: hidden;
}

.fact-item::before {
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(135deg, transparent 0%, rgba(201, 169, 110, 0.05) 50%, transparent 100%);
    opacity: 0;
    transition: opacity 0.3s ease;
}

.fact-item:hover::before {
    opacity: 1;
}

.fact-item:hover {
    transform: translateY(-8px);
    box-shadow: var(--shadow-2xl);
    border-color: rgba(201, 169, 110, 0.3);
}

.fact-item__icon {
    font-size: var(--font-size-4xl);
    margin-bottom: var(--spacing-4);
    display: block;
    color: var(--color-accent);
}

.fact-item__title {
    font-size: var(--font-size-xl);
    font-weight: var(--font-weight-bold);
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-2);
}

.fact-item__description {
    font-size: var(--font-size-base);
    color: var(--color-text-secondary);
    line-height: var(--line-height-relaxed);
}

/* FAQ секция */
.about-faq {
    padding: var(--spacing-20) 0;
    background: linear-gradient(135deg, var(--color-bg-primary) 0%, var(--color-bg-secondary) 50%, var(--color-bg-primary) 100%);
    position: relative;
    overflow: hidden;
}

.about-faq::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: radial-gradient(circle at 40% 80%, rgba(201, 169, 110, 0.05) 0%, transparent 50%);
    z-index: 0;
}

.about-faq__title {
    font-family: var(--font-display);
    font-size: var(--font-size-4xl);
    font-weight: var(--font-weight-bold);
    text-align: center;
    color: var(--color-text-primary);
    margin-bottom: var(--spacing-4);
    position: relative;
    z-index: 1;
}

.about-faq__title::after {
    content: '';
    position: absolute;
    bottom: -15px;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
    height: 4px;
    background: linear-gradient(90deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    border-radius: var(--radius-full);
}

.about-faq .faq-list {
    max-width: var(--container-lg);
    margin: var(--spacing-16) auto 0;
    padding: 0 var(--spacing-6);
    position: relative;
    z-index: 1;
}

.about-faq .faq-item {
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(20px);
    border-radius: var(--radius-xl);
    margin-bottom: var(--spacing-4);
    overflow: hidden;
    border: 1px solid rgba(255, 255, 255, 0.2);
    transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.about-faq .faq-item:hover {
    box-shadow: var(--shadow-lg);
    border-color: rgba(201, 169, 110, 0.3);
}

.about-faq .faq-item__question {
    width: 100%;
    padding: var(--spacing-5) var(--spacing-6);
    background: none;
    border: none;
    text-align: left;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: var(--font-size-base);
    font-weight: var(--font-weight-semibold);
    color: var(--color-text-primary);
    transition: color 0.3s ease;
}

.about-faq .faq-item__question:hover {
    color: var(--color-accent);
}

.about-faq .faq-item__question span {
    flex: 1;
    margin-right: var(--spacing-3);
}

.about-faq .faq-item__icon {
    width: 20px;
    height: 20px;
    transition: transform 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    color: var(--color-text-tertiary);
}

.about-faq .faq-item.active .faq-item__icon {
    transform: rotate(180deg);
    color: var(--color-accent);
}

.about-faq .faq-item__answer {
    max-height: 0 !important;
    padding: 0 var(--spacing-6) !important;
    overflow: hidden !important;
    transition: max-height 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94), padding 0.3s ease !important;
    background: rgba(var(--color-accent-rgb), 0.02) !important;
}

.about-faq .faq-item__answer > * {
    min-height: 0;
}

.about-faq .faq-item.active .faq-item__answer {
    max-height: none !important;
    height: auto !important;
    padding: var(--spacing-5) var(--spacing-6) !important;
}

.about-faq .faq-item__answer p {
    color: var(--color-text-secondary);
    line-height: var(--line-height-relaxed);
    margin: 0;
    font-size: var(--font-size-base);
}

/* ========================================
   АДАПТИВНЫЙ ДИЗАЙН
   ======================================== */

@media (max-width: 768px) {
    .about-story {
        padding: var(--spacing-16) 0;
    }

    .about-story__grid {
        grid-template-columns: 1fr;
        gap: var(--spacing-8);
    }

    .about-story__title {
        font-size: var(--font-size-3xl);
        text-align: center;
    }

    .about-story__photo {
        order: -1;
        height: 300px;
        max-width: 400px;
        margin: 0 auto;
    }
}

/* iPad Air - фиксим сжатие фото */
@media (min-width: 769px) and (max-width: 900px) and (orientation: portrait),
       (min-width: 1150px) and (max-width: 1300px) and (orientation: landscape),
       (min-width: 800px) and (max-width: 850px) {
    .about-story__photo {
        max-width: 500px;
        width: auto;
        height: 450px;
        margin: 0 auto;
        aspect-ratio: 3/4;
    }
    
    .about-story__photo img {
        object-fit: cover;
        object-position: center;
    }
    
    .about-story__grid {
        grid-template-columns: 1fr 1fr;
        gap: var(--spacing-12);
        align-items: center;
    }
}

@media (max-width: 768px) {
    .about-philosophy {
        padding: var(--spacing-16) 0;
    }

    .about-philosophy__title {
        font-size: var(--font-size-3xl);
    }

    .about-philosophy__text {
        grid-template-columns: 1fr;
        gap: var(--spacing-6);
    }

    .service-direction {
        padding: var(--spacing-6);
    }

    .flashback-info {
        padding: var(--spacing-8);
        margin: var(--spacing-8) 0;
    }

    .flashback-info__title {
        font-size: var(--font-size-2xl);
    }

    .advantages-section {
        padding: var(--spacing-16) 0;
    }

    .advantages-section__title {
        font-size: var(--font-size-3xl);
    }

    .advantages-list {
        grid-template-columns: 1fr;
        gap: var(--spacing-6);
    }

    .advantage-item {
        padding: var(--spacing-6);
    }

    .facts-section {
        padding: var(--spacing-16) 0;
    }

    .about-facts__title {
        font-size: var(--font-size-3xl);
    }

    .facts-grid {
        grid-template-columns: 1fr;
