/* =================================================================
   CTA SECTION
   Стили для секции призыва к действию
   ================================================================= */

.cta {
  padding: 6rem 0;
  background: linear-gradient(135deg, 
    var(--color-primary) 0%, 
    var(--color-primary-dark) 100%);
  color: white;
  position: relative;
  overflow: hidden;
}

.cta::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><circle cx="20" cy="20" r="2" fill="rgba(255,255,255,0.1)"/><circle cx="80" cy="40" r="1" fill="rgba(255,255,255,0.1)"/><circle cx="40" cy="80" r="1.5" fill="rgba(255,255,255,0.1)"/></svg>');
  animation: float 20s infinite linear;
}

@keyframes float {
  0% { transform: translateY(0px) rotate(0deg); }
  50% { transform: translateY(-10px) rotate(180deg); }
  100% { transform: translateY(0px) rotate(360deg); }
}

.cta__container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
  position: relative;
  z-index: 1;
}

.cta__content {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.cta__title {
  font-size: 2.5rem;
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.cta__subtitle {
  font-size: 1.125rem;
  line-height: 1.6;
  opacity: 0.9;
  margin-bottom: 2rem;
}

.cta__features {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 2rem;
}

.cta__feature {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-size: 1rem;
}

.cta__feature svg {
  color: #10b981;
  flex-shrink: 0;
}

.cta__actions {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

/* CTA Buttons */
.cta__btn {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 2rem;
  border-radius: 50px;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  border: 2px solid transparent;
}

.cta__btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s ease;
}

.cta__btn:hover::before {
  left: 100%;
}

.cta__btn--primary {
  background: white;
  color: var(--color-primary);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.cta__btn--primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
  background: #f8fafc;
}

.cta__btn--secondary {
  background: transparent;
  color: white;
  border-color: rgba(255, 255, 255, 0.3);
}

.cta__btn--secondary:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: white;
  transform: translateY(-3px);
}

.cta__btn-icon {
  transition: transform 0.3s ease;
}

.cta__btn:hover .cta__btn-icon {
  transform: scale(1.1);
}

/* CTA Image */
.cta__image {
  position: relative;
  text-align: center;
}

.cta__photo {
  width: 100%;
  max-width: 400px;
  height: 500px;
  object-fit: cover;
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  position: relative;
  z-index: 2;
}

.cta__image-decoration {
  position: absolute;
  top: -20px;
  right: -20px;
  width: 100px;
  height: 100px;
  background: linear-gradient(45deg, #fbbf24, #f59e0b);
  border-radius: 50%;
  opacity: 0.8;
  z-index: 1;
  animation: pulse 3s infinite;
}

.cta__image-decoration::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 120%;
  height: 120%;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  animation: ripple 2s infinite;
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

@keyframes ripple {
  0% { transform: translate(-50%, -50%) scale(1); opacity: 1; }
  100% { transform: translate(-50%, -50%) scale(1.5); opacity: 0; }
}

/* Statistics */
.cta__stats {
  margin-top: 4rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
  padding-top: 3rem;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}

.cta__stat-item {
  text-align: center;
  padding: 1rem;
}

.cta__stat-number {
  font-size: 2.5rem;
  font-weight: 700;
  color: white;
  margin-bottom: 0.5rem;
  display: block;
}

.cta__stat-label {
  font-size: 0.875rem;
  opacity: 0.8;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Responsive */
@media (max-width: 1024px) {
  .cta__container {
    grid-template-columns: 1fr;
    gap: 3rem;
    text-align: center;
  }
  
  .cta__image {
    order: -1;
  }
}

@media (max-width: 768px) {
  .cta {
    padding: 4rem 0;
  }
  
  .cta__container {
    padding: 0 1rem;
    gap: 2rem;
  }
  
  .cta__title {
    font-size: 2rem;
  }
  
  .cta__actions {
    flex-direction: column;
  }
  
  .cta__btn {
    justify-content: center;
    width: 100%;
  }
  
  .cta__photo {
    height: 400px;
  }
  
  .cta__stats {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
    margin-top: 3rem;
  }
  
  .cta__stat-number {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .cta__features {
    align-items: flex-start;
  }
  
  .cta__stats {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .cta__stat-item {
    padding: 0.5rem;
  }
}

/* Dark theme adjustments */
[data-theme="dark"] .cta__btn--primary {
  background: var(--color-background-light);
  color: var(--color-primary);
}

[data-theme="dark"] .cta__btn--primary:hover {
  background: var(--color-background);
}

/* Animation states */
.cta__content > * {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

.cta__content > *.fade-in.animated {
  opacity: 1;
  transform: translateY(0);
}

.cta__image {
  opacity: 0;
  transform: translateX(50px);
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.cta__image.slide-right.animated {
  opacity: 1;
  transform: translateX(0);
}

/* Parallax effect for decoration */
@media (min-width: 768px) {
  .cta__image-decoration {
    will-change: transform;
  }
}

/* Enhanced button effects */
.cta__btn {
  position: relative;
  z-index: 1;
}

.cta__btn::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.3) 0%, transparent 70%);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.3s ease;
  z-index: -1;
}

.cta__btn:active::after {
  width: 300px;
  height: 300px;
}
