/* ========================================
   CSS RESET & ОСНОВНЫЕ СТИЛИ
   Современный reset для фотографического сайта
   ======================================== */

/* Box model для всех элементов */
*,
*::before,
*::after {
    box-sizing: border-box;
}

/* Сброс отступов и границ */
* {
    margin: 0;
    padding: 0;
    border: 0;
}

/* Исключение для body - сохраняем возможность задать padding-top */
body {
    margin: 0;
    border: 0;
    /* padding остаётся управляемым через header.css */
}

/* Предотвращение перекрытия фиксированным хедером */
html {
    scroll-behavior: smooth;
}

main,
.main-content {
    position: relative;
    z-index: 1;
}

.header {
    z-index: 100;
}

/* HTML и Body настройки */
html {
    font-size: 16px;
    height: 100%;
    overflow-x: hidden; /* Предотвращение горизонтального скролла */
}

body {
    min-height: 100vh;
    line-height: 1.6;
    width: 100%;
    max-width: 100vw;
    overflow-x: hidden; /* Предотвращение горизонтального скролла */
    font-family: var(--font-primary);
    color: var(--color-text-primary);
    background-color: var(--color-bg-primary);
    overflow-x: hidden;
}

/* Улучшенный рендеринг текста */
body {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
}

/* Списки */
ul, ol {
    list-style: none;
}

/* Ссылки */
a {
    text-decoration: none;
    color: inherit;
    transition: all 0.3s ease;
}

/* Изображения */
img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
    height: auto;
}

/* Формы */
input, button, textarea, select {
    font: inherit;
    background: transparent;
    border: none;
    outline: none;
}

button {
    cursor: pointer;
}

/* Таблицы */
table {
    border-collapse: collapse;
    border-spacing: 0;
}

/* Фокус для доступности */
:focus-visible {
    outline: 2px solid var(--color-accent);
    outline-offset: 2px;
}
