/* =================================================================
   SERVICES SECTION WITH VISUAL SEPARATION
   Стили для секции услуг с визуальным разделением
   ================================================================= */

.services {
  padding: 8rem 0;
  background: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%);
  position: relative;
  overflow: hidden;
}

/* Элегантный декоративный "забор" сверху */
.services::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    rgba(201, 169, 110, 0.1) 5%,
    rgba(201, 169, 110, 0.3) 25%,
    rgba(201, 169, 110, 0.5) 50%,
    rgba(201, 169, 110, 0.3) 75%,
    rgba(201, 169, 110, 0.1) 95%,
    transparent 100%);
  box-shadow: 0 2px 8px rgba(201, 169, 110, 0.2);
}

/* Декоративная линия снизу */
.services::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(201, 169, 110, 0.3), transparent);
}

/* Декоративный фоновый паттерн */
.services {
  background-image: 
    radial-gradient(circle at 25% 25%, rgba(201, 169, 110, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 75% 75%, rgba(201, 169, 110, 0.03) 0%, transparent 50%),
    radial-gradient(circle at 50% 80%, rgba(201, 169, 110, 0.02) 0%, transparent 60%);
}

/* Контейнер с дополнительными отступами */

.services__container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 2;
}

/* Добавляем декоративную рамку для секции */
.services__container::before {
  content: '';
  position: absolute;
  top: -2rem;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 4px;
  background: linear-gradient(90deg, var(--color-accent), var(--color-accent-hover));
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(201, 169, 110, 0.3);
}

.services__header {
  text-align: center;
  margin-bottom: 4rem;
}

.services__title {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--color-text);
  margin-bottom: 1rem;
}

.services__subtitle {
  font-size: 1.125rem;
  color: var(--color-text-light);
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

.services__grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 8rem; /* Увеличен отступ для лучшего разделения */
  position: relative;
}

/* Декоративное разделение между подходом и услугами */
.services__grid::before {
  content: '';
  position: absolute;
  top: -4rem;
  left: 50%;
  transform: translateX(-50%);
  width: 200px;
  height: 2px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    rgba(201, 169, 110, 0.2) 20%,
    rgba(201, 169, 110, 0.6) 50%,
    rgba(201, 169, 110, 0.2) 80%,
    transparent 100%);
  border-radius: 1px;
}

/* Декоративные точки */
.services__grid::after {
  content: '• • •';
  position: absolute;
  top: -4.5rem;
  left: 50%;
  transform: translateX(-50%);
  color: var(--color-accent);
  font-size: 1.2rem;
  letter-spacing: 0.5rem;
  opacity: 0.7;
}

/* Заголовок блока услуг */
.services__services-title {
  text-align: center;
  margin: 6rem 0 3rem 0;
  position: relative;
}

.services__services-title h3 {
  font-size: 2.2rem;
  font-weight: 600;
  color: var(--color-text);
  margin-bottom: 1rem;
  position: relative;
  display: inline-block;
}

.services__services-title h3::before {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background: linear-gradient(90deg, var(--color-accent), var(--color-accent-hover));
  border-radius: 2px;
}

.services__services-title p {
  color: var(--color-text-light);
  font-size: 1.1rem;
  max-width: 500px;
  margin: 0 auto;
  line-height: 1.6;
}

/* Стабилизирующие стили для всех элементов service-card */
.service-card,
.service-card * {
  display: block;
  visibility: visible;
  opacity: 1;
}

.service-card__features {
  display: block;
  list-style: none;
  padding: 0;
  margin: 0 0 1.5rem 0;
}

.service-card__feature {
  display: block;
  padding: 0.5rem 0;
  color: var(--color-text-light);
  position: relative;
  padding-left: 1.5rem;
}

.service-card__action {
  display: inline-block;
  margin-top: auto;
  width: 100%;
}

.service-card__price-note {
  display: block;
  font-size: 0.875rem;
  color: var(--color-text-light);
  margin-bottom: 1.5rem;
  font-style: italic;
}

/* Service Cards */
.service-card {
  background: var(--color-background);
  border-radius: 16px;
  padding: 2rem;
  text-align: center;
  box-shadow: var(--shadow-md);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid var(--color-border);
  position: relative;
  overflow: hidden;
}

.service-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, var(--color-primary), var(--color-primary-light));
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.service-card:hover::before {
  transform: scaleX(1);
}

.service-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-lg);
  border-color: var(--color-primary);
}

.service-card__icon {
  width: 80px;
  height: 80px;
  margin: 0 auto 1.5rem;
  background: linear-gradient(135deg, var(--color-primary), var(--color-primary-light));
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  transition: transform 0.3s ease;
}

.service-card:hover .service-card__icon {
  transform: scale(1.1);
}

.service-card__content {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.service-card__title {
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--color-text);
  margin-bottom: 1rem;
}

.service-card__description {
  color: var(--color-text-light);
  line-height: 1.6;
  margin-bottom: 1.5rem;
  flex-grow: 1;
}

.service-card__features {
  list-style: none;
  padding: 0;
  margin: 0 0 1.5rem 0;
}

.service-card__features li {
  padding: 0.5rem 0;
  color: var(--color-text-light);
  position: relative;
  padding-left: 1.5rem;
}

.service-card__features li::before {
  content: '✓';
  position: absolute;
  left: 0;
  color: var(--color-primary);
  font-weight: bold;
}

.service-card__price {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--color-primary);
  margin-bottom: 1.5rem;
}

.service-card__btn {
  margin-top: auto;
  width: 100%;
}

/* Premium Service Card */
.service-card--premium {
  position: relative;
  border: 2px solid var(--color-primary);
  background: linear-gradient(135deg, 
    #ffffff 0%, 
    #fefefe 50%,
    #fdfdfd 100%);
  transform: scale(1.02);
  box-shadow: var(--shadow-lg);
}

.service-card--premium::before {
  background: linear-gradient(90deg, var(--color-primary), var(--color-accent));
  transform: scaleX(1);
}

.service-card--premium .service-card__icon {
  background: linear-gradient(135deg, var(--color-primary), var(--color-accent));
  box-shadow: 0 4px 15px rgba(201, 169, 110, 0.3);
}

.service-card--premium .service-card__title {
  color: var(--color-primary);
  font-weight: 700;
}

.service-card--premium .service-card__description {
  color: var(--color-text) !important;
  font-weight: 500;
}

.service-card--premium .service-card__feature {
  color: var(--color-text-light) !important;
  font-weight: 400;
}

.service-card--premium .service-card__price {
  color: var(--color-primary);
  font-size: 1.75rem;
  font-weight: 700;
}

.service-card--premium .service-card__price-note {
  color: var(--color-text-light) !important;
  font-weight: 400;
}

/* Премиум бейдж */
.service-card--premium::after {
  content: 'Популярно';
  position: absolute;
  top: -10px;
  right: 20px;
  background: linear-gradient(135deg, var(--color-primary), var(--color-accent));
  color: white;
  padding: 5px 15px;
  border-radius: 15px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  box-shadow: 0 2px 8px rgba(201, 169, 110, 0.3);
}

.service-card--premium:hover {
  transform: scale(1.02) translateY(-12px);
  border-color: var(--color-accent);
  box-shadow: 0 20px 40px rgba(201, 169, 110, 0.2);
}

/* Responsive */
@media (max-width: 768px) {
  .services {
    padding: 4rem 0;
  }
  
  .services__container {
    padding: 0 1rem;
  }
  
  .services__title {
    font-size: 2rem;
  }
  
  .services__grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .service-card {
    padding: 1.5rem;
  }
  
  .service-card__icon {
    width: 60px;
    height: 60px;
  }
}
