/* ========================================
   ПРЕМИАЛЬНАЯ НАВИГАЦИЯ
   Glassmorphism дизайн с backdrop-filter
   ======================================== */

/* Переменные для glassmorphism навигации */
:root {
    --nav-bg: rgba(255, 255, 255, 0.95);
    --nav-bg-scrolled: rgba(255, 255, 255, 0.98);
    --nav-border: rgba(255, 255, 255, 0.2);
    --nav-blur: blur(20px);
    --nav-transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Основная навигация с glassmorphism */
.nav {
    display: flex;
    align-items: center;
    gap: 2rem;
    background: var(--nav-bg);
    backdrop-filter: var(--nav-blur);
    border-bottom: 1px solid var(--nav-border);
    transition: var(--nav-transition);
    position: sticky;
    top: 0;
    z-index: 1000;
    padding: 0.75rem 0;
}

/* Состояние навигации при скролле */
.nav--scrolled {
    background: var(--nav-bg-scrolled);
    backdrop-filter: blur(30px) saturate(180%);
    box-shadow: var(--elevation-2);
    border-bottom-color: rgba(0, 0, 0, 0.1);
}

.nav__list {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    list-style: none;
}

.nav__link {
    position: relative;
    font-size: var(--font-size-base);
    font-weight: 500;
    color: var(--color-text-primary);
    text-decoration: none;
    padding: 0.5rem 1rem;
    border-radius: 8px;
    transition: var(--nav-transition);
    backdrop-filter: blur(5px);
}

/* Современные hover эффекты */
.nav__link::before {
    content: '';
    position: absolute;
    inset: 0;
    background: rgba(var(--color-accent-rgb), 0.1);
    border-radius: 8px;
    opacity: 0;
    transform: scale(0.8);
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.nav__link:hover::before {
    opacity: 1;
    transform: scale(1);
}

.nav__link:hover {
    color: var(--color-accent);
    transform: translateY(-1px);
}

/* Убираем подчеркивание - оставляем только hover эффекты фона */
.nav__link:hover {
    color: var(--color-accent);
    transform: translateY(-1px);
}

/* Активная ссылка */
.nav__link--active {
    color: var(--color-accent);
    background: rgba(var(--color-accent-rgb), 0.1);
    backdrop-filter: blur(10px);
    font-weight: 600;
    position: relative;
}

/* Мобильное меню */
.mobile-nav {
    display: none;
}

.mobile-nav__toggle {
    display: flex;
    flex-direction: column;
    gap: 4px;
    background: none;
    border: none;
    cursor: pointer;
    padding: var(--spacing-2);
}

.mobile-nav__line {
    width: 24px;
    height: 2px;
    background-color: var(--color-text-primary);
    transition: var(--transition-fast);
    transform-origin: center;
}

.mobile-nav__toggle--active .mobile-nav__line:nth-child(1) {
    transform: rotate(45deg) translateY(6px);
}

.mobile-nav__toggle--active .mobile-nav__line:nth-child(2) {
    opacity: 0;
}

.mobile-nav__toggle--active .mobile-nav__line:nth-child(3) {
    transform: rotate(-45deg) translateY(-6px);
}

.mobile-nav__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--color-bg-primary);
    background: #ffffff; /* Явный белый фон как fallback */
    backdrop-filter: blur(10px);
    z-index: var(--z-modal);
    padding: var(--spacing-20) var(--spacing-6);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: var(--spacing-8);
    transform: translateX(-100%);
    transition: var(--transition-base);
}

.mobile-nav__menu--active {
    transform: translateX(0);
}

.mobile-nav__link {
    font-size: var(--font-size-2xl);
    font-weight: var(--font-weight-medium);
    color: var(--color-text-primary);
    text-decoration: none;
    transition: var(--transition-fast);
}

.mobile-nav__link:hover,
.mobile-nav__link--active {
    color: var(--color-accent);
}

/* Dropdown меню */
.nav__dropdown {
    position: relative;
}

.nav__dropdown-toggle::after {
    content: '';
    display: inline-block;
    width: 0;
    height: 0;
    margin-left: var(--spacing-2);
    border-top: 4px solid currentColor;
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
    transition: var(--transition-fast);
}

.nav__dropdown:hover .nav__dropdown-toggle::after {
    transform: rotate(180deg);
}

.nav__dropdown-menu {
    position: absolute;
    top: 100%;
    left: 0;
    min-width: 200px;
    background-color: var(--color-bg-primary);
    border: 1px solid var(--color-border-light);
    border-radius: var(--radius-lg);
    box-shadow: var(--shadow-lg);
    padding: var(--spacing-2);
    opacity: 0;
    visibility: hidden;
    transform: translateY(-10px);
    transition: var(--transition-fast);
    z-index: var(--z-dropdown);
}

.nav__dropdown:hover .nav__dropdown-menu {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
}

.nav__dropdown-link {
    display: block;
    padding: var(--spacing-3) var(--spacing-4);
    color: var(--color-text-primary);
    text-decoration: none;
    border-radius: var(--radius-md);
    transition: var(--transition-fast);
    font-size: var(--font-size-sm);
}

.nav__dropdown-link:hover {
    background-color: var(--color-accent-light);
    color: var(--color-accent);
}

/* ========================================
   ПРОДВИНУТЫЕ ЭФФЕКТЫ НАВИГАЦИИ
   Современные hover-эффекты и анимации
   ======================================== */

/* Навигация с подчеркиванием */
.nav--underline .nav__link {
    position: relative;
    padding-bottom: var(--spacing-2);
}

.nav--underline .nav__link::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 2px;
    background: linear-gradient(90deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    transition: width 0.3s ease;
}

.nav--underline .nav__link:hover::after,
.nav--underline .nav__link--active::after {
    width: 100%;
}

/* Навигация с морфинг эффектом */
.nav--morph .nav__link {
    position: relative;
    padding: var(--spacing-2) var(--spacing-4);
    border-radius: var(--radius-lg);
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.nav--morph .nav__link::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: var(--color-accent-light);
    border-radius: inherit;
    transform: scale(0);
    transition: transform 0.3s ease;
    z-index: -1;
}

.nav--morph .nav__link:hover::before {
    transform: scale(1);
}

.nav--morph .nav__link:hover {
    color: var(--color-accent);
    transform: translateY(-2px);
}

/* Навигация с неоновым эффектом */
.nav--neon .nav__link {
    position: relative;
    color: var(--color-text-primary);
    transition: all 0.3s ease;
}

.nav--neon .nav__link:hover {
    color: var(--color-accent);
    text-shadow: 
        0 0 5px var(--color-accent),
        0 0 10px var(--color-accent),
        0 0 15px var(--color-accent);
}

/* Мега-меню с анимациями */
.nav__mega-menu {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background: var(--color-bg-primary);
    border-top: 1px solid var(--color-border-light);
    box-shadow: var(--shadow-lg);
    padding: var(--spacing-8);
    opacity: 0;
    visibility: hidden;
    transform: translateY(-10px);
    transition: all 0.3s ease;
    z-index: var(--z-dropdown);
}

.nav__dropdown:hover .nav__mega-menu {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
}

.nav__mega-content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: var(--spacing-8);
    max-width: var(--container-xl);
    margin: 0 auto;
}

.nav__mega-section h3 {
    color: var(--color-accent);
    font-size: var(--font-size-lg);
    margin-bottom: var(--spacing-4);
}

.nav__mega-link {
    display: block;
    padding: var(--spacing-2);
    color: var(--color-text-secondary);
    text-decoration: none;
    border-radius: var(--radius-md);
    transition: all 0.2s ease;
}

.nav__mega-link:hover {
    background: var(--color-accent-light);
    color: var(--color-accent);
    transform: translateX(5px);
}

/* Индикатор активной страницы */
.nav__indicator {
    position: absolute;
    bottom: 0;
    height: 3px;
    background: linear-gradient(90deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    border-radius: var(--radius-full);
    transition: all 0.3s ease;
    opacity: 0;
}

.nav__list:hover .nav__indicator {
    opacity: 1;
}

/* Хлебные крошки */
.breadcrumb {
    display: flex;
    align-items: center;
    gap: var(--spacing-2);
    padding: var(--spacing-4) 0;
    font-size: var(--font-size-sm);
}

.breadcrumb__item {
    color: var(--color-text-secondary);
    text-decoration: none;
    transition: color 0.2s ease;
}

.breadcrumb__item:hover {
    color: var(--color-accent);
}

.breadcrumb__item--current {
    color: var(--color-text-primary);
    font-weight: var(--font-weight-medium);
}

.breadcrumb__separator {
    color: var(--color-text-tertiary);
    user-select: none;
}

/* Табы навигации */
.nav__tabs {
    display: flex;
    border-bottom: 1px solid var(--color-border-light);
    position: relative;
}

.nav__tab {
    padding: var(--spacing-4) var(--spacing-6);
    color: var(--color-text-secondary);
    text-decoration: none;
    border-bottom: 2px solid transparent;
    transition: all 0.3s ease;
    position: relative;
}

.nav__tab:hover {
    color: var(--color-accent);
    background: var(--color-accent-light);
}

.nav__tab--active {
    color: var(--color-accent);
    border-bottom-color: var(--color-accent);
}

.nav__tab-slider {
    position: absolute;
    bottom: -1px;
    height: 2px;
    background: var(--color-accent);
    transition: all 0.3s ease;
    border-radius: var(--radius-full);
}

/* Сайдбар навигация */
.nav--sidebar {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-2);
    padding: var(--spacing-6);
}

.nav--sidebar .nav__link {
    display: flex;
    align-items: center;
    gap: var(--spacing-3);
    padding: var(--spacing-3) var(--spacing-4);
    border-radius: var(--radius-lg);
    color: var(--color-text-secondary);
    text-decoration: none;
    transition: all 0.2s ease;
    position: relative;
}

.nav--sidebar .nav__link::before {
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 4px;
    height: 0;
    background: var(--color-accent);
    border-radius: var(--radius-full);
    transition: height 0.3s ease;
}

.nav--sidebar .nav__link:hover,
.nav--sidebar .nav__link--active {
    background: var(--color-accent-light);
    color: var(--color-accent);
    transform: translateX(8px);
}

.nav--sidebar .nav__link:hover::before,
.nav--sidebar .nav__link--active::before {
    height: 20px;
}

/* Навигация с прогресс-баром */
.nav--progress {
    position: relative;
}

.nav--progress::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    height: 3px;
    background: linear-gradient(90deg, var(--color-accent) 0%, var(--color-accent-hover) 100%);
    width: var(--progress, 0%);
    transition: width 0.3s ease;
    border-radius: var(--radius-full);
}

/* Анимация появления мобильного меню */
@keyframes mobileMenuSlide {
    from {
        transform: translateX(-100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

.mobile-nav__menu--animated {
    animation: mobileMenuSlide 0.3s ease-out;
}

/* Стильная социальная навигация */
.nav--social {
    display: flex;
    gap: var(--spacing-4);
}

.nav--social .nav__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: var(--radius-full);
    background: var(--color-bg-secondary);
    color: var(--color-text-secondary);
    text-decoration: none;
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    position: relative;
    overflow: hidden;
}

.nav--social .nav__link::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: var(--color-accent);
    transform: scale(0);
    transition: transform 0.3s ease;
    border-radius: inherit;
}

.nav--social .nav__link:hover {
    transform: translateY(-3px);
    box-shadow: var(--shadow-lg);
    color: var(--color-text-inverse);
}

.nav--social .nav__link:hover::before {
    transform: scale(1);
}

/* ========================================
   RESPONSIVE DESIGN
   ======================================== */

/* Десктопная версия */
@media (min-width: 769px) {
    .nav {
        display: flex;
    }
    
    .mobile-nav {
        display: none;
    }
    
    /* Исправляем подчеркивание активного элемента */
    .nav__link--active::after {
        content: '';
        position: absolute;
        bottom: -4px;
        left: var(--spacing-3);
        right: var(--spacing-3);
        width: calc(100% - var(--spacing-6));
        height: 2px;
        background-color: var(--color-accent);
        border-radius: var(--radius-full);
        transform: none;
    }
}

/* Планшеты и мобильные устройства */
@media (max-width: 768px) {
    .nav {
        display: none;
    }
    
    .mobile-nav {
        display: block;
    }
}

/* ========================================
   ДЕСКТОПНАЯ НАВИГАЦИЯ - УЛУЧШЕНИЯ
   ======================================== */

/* Медиа-запрос для десктопа - без подчеркивания */
@media (min-width: 768px) {
    .nav__link {
        position: relative;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        overflow: visible;
    }
    
    /* Плавная анимация для активной ссылки */
    .nav__link--active {
        color: var(--color-accent);
    }
}


