/* ========================================
   СЕТКА И КОНТЕЙНЕРЫ
   Современная система layout'а
   ======================================== */

/* Основные контейнеры */
.container {
    width: 100%;
    max-width: var(--container-xl);
    margin: 0 auto;
    padding: 0 var(--spacing-6);
}

.container--sm {
    max-width: var(--container-sm);
}

.container--md {
    max-width: var(--container-md);
}

.container--lg {
    max-width: var(--container-lg);
}

.container--xl {
    max-width: var(--container-xl);
}

.container--2xl {
    max-width: var(--container-2xl);
}

.container--full {
    max-width: none;
}

.container--narrow {
    max-width: 800px;
}

/* Секции */
.section {
    padding: var(--spacing-20) 0;
}

.section--small {
    padding: var(--spacing-12) 0;
}

.section--large {
    padding: var(--spacing-32) 0;
}

.section--hero {
    padding: var(--spacing-32) 0;
    min-height: 100vh;
    display: flex;
    align-items: center;
}

/* Сетки */
.grid {
    display: grid;
    gap: var(--spacing-6);
}

.grid--1 {
    grid-template-columns: 1fr;
}

.grid--2 {
    grid-template-columns: repeat(2, 1fr);
}

.grid--3 {
    grid-template-columns: repeat(3, 1fr);
}

.grid--4 {
    grid-template-columns: repeat(4, 1fr);
}

.grid--5 {
    grid-template-columns: repeat(5, 1fr);
}

.grid--6 {
    grid-template-columns: repeat(6, 1fr);
}

/* Адаптивные сетки */
.grid--responsive-2 {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.grid--responsive-3 {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.grid--responsive-4 {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}

/* Сетка портфолио */
.grid--portfolio {
    display: grid;
    gap: var(--spacing-4);
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
}

@media (min-width: 640px) {
    .grid--portfolio {
        gap: var(--spacing-6);
        grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    }
}

@media (min-width: 1024px) {
    .grid--portfolio {
        grid-template-columns: repeat(3, 1fr);
    }
}

/* Flexbox утилиты */
.flex {
    display: flex;
}

.flex--column {
    flex-direction: column;
}

.flex--row {
    flex-direction: row;
}

.flex--wrap {
    flex-wrap: wrap;
}

.flex--nowrap {
    flex-wrap: nowrap;
}

/* Выравнивание по главной оси */
.justify-start {
    justify-content: flex-start;
}

.justify-center {
    justify-content: center;
}

.justify-end {
    justify-content: flex-end;
}

.justify-between {
    justify-content: space-between;
}

.justify-around {
    justify-content: space-around;
}

.justify-evenly {
    justify-content: space-evenly;
}

/* Выравнивание по перекрестной оси */
.items-start {
    align-items: flex-start;
}

.items-center {
    align-items: center;
}

.items-end {
    align-items: flex-end;
}

.items-stretch {
    align-items: stretch;
}

.items-baseline {
    align-items: baseline;
}

/* Gap утилиты */
.gap-0 {
    gap: 0;
}

.gap-1 {
    gap: var(--spacing-1);
}

.gap-2 {
    gap: var(--spacing-2);
}

.gap-3 {
    gap: var(--spacing-3);
}

.gap-4 {
    gap: var(--spacing-4);
}

.gap-5 {
    gap: var(--spacing-5);
}

.gap-6 {
    gap: var(--spacing-6);
}

.gap-8 {
    gap: var(--spacing-8);
}

.gap-10 {
    gap: var(--spacing-10);
}

.gap-12 {
    gap: var(--spacing-12);
}

/* Особые layout'ы */
.layout-split {
    display: grid;
    gap: var(--spacing-8);
    grid-template-columns: 1fr;
    align-items: center;
}

@media (min-width: 768px) {
    .layout-split {
        grid-template-columns: 1fr 1fr;
        gap: var(--spacing-12);
    }
}

.layout-sidebar {
    display: grid;
    gap: var(--spacing-8);
    grid-template-columns: 1fr;
}

@media (min-width: 1024px) {
    .layout-sidebar {
        grid-template-columns: 1fr 300px;
        gap: var(--spacing-12);
    }
}

.layout-sidebar--reverse {
    grid-template-columns: 1fr;
}

@media (min-width: 1024px) {
    .layout-sidebar--reverse {
        grid-template-columns: 300px 1fr;
    }
}

/* Стеки */
.stack {
    display: flex;
    flex-direction: column;
}

.stack > * + * {
    margin-top: var(--spacing-4);
}

.stack--small > * + * {
    margin-top: var(--spacing-2);
}

.stack--large > * + * {
    margin-top: var(--spacing-6);
}

/* Центрирование */
.center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.center-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

/* Aspect ratio */
.aspect-square {
    aspect-ratio: 1 / 1;
}

.aspect-video {
    aspect-ratio: 16 / 9;
}

.aspect-photo {
    aspect-ratio: 4 / 3;
}

.aspect-portrait {
    aspect-ratio: 3 / 4;
}

/* Адаптивность сеток */
@media (max-width: 768px) {
    .grid--2,
    .grid--3,
    .grid--4,
    .grid--5,
    .grid--6 {
        grid-template-columns: 1fr;
    }
    
    .grid--2.grid--mobile-2 {
        grid-template-columns: repeat(2, 1fr);
    }
}
