/* ========================================
   ГАЛЕРЕЯ
   Отдельные галереи проектов
   ======================================== */

.gallery-page {
    padding-top: var(--spacing-8);
}

.gallery__header {
    text-align: center;
    padding: var(--spacing-16) 0;
    background: linear-gradient(135deg, var(--color-bg-secondary) 0%, var(--color-bg-primary) 100%);
}

.gallery__title {
    font-size: var(--font-size-5xl);
    margin-bottom: var(--spacing-4);
}

.gallery__subtitle {
    font-size: var(--font-size-lg);
    color: var(--color-text-secondary);
    max-width: 600px;
    margin: 0 auto;
}

.gallery__content {
    padding: var(--spacing-12) 0;
}

.gallery__grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: var(--spacing-4);
}

.gallery__item {
    position: relative;
    aspect-ratio: 4 / 3;
    border-radius: var(--radius-lg);
    overflow: hidden;
    cursor: pointer;
    transition: var(--transition-base);
}

.gallery__item:hover {
    transform: scale(1.02);
}

.gallery__item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@media (min-width: 768px) {
    .gallery__grid {
        grid-template-columns: repeat(3, 1fr);
        gap: var(--spacing-6);
    }
}

@media (min-width: 1024px) {
    .gallery__grid {
        grid-template-columns: repeat(4, 1fr);
    }
}

/* Информация о галерее */
.gallery-info {
    padding: var(--spacing-16) 0;
    background: var(--color-bg-secondary);
}

.gallery-info__title {
    font-size: var(--font-size-3xl);
    margin-bottom: var(--spacing-8);
    text-align: center;
}

.gallery-info__grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: var(--spacing-6);
    margin-bottom: var(--spacing-8);
}

.gallery-info__item {
    text-align: center;
    padding: var(--spacing-4);
}

.gallery-info__item h3 {
    font-size: var(--font-size-lg);
    color: var(--color-text-secondary);
    margin-bottom: var(--spacing-2);
    text-transform: uppercase;
    letter-spacing: 0.05em;
}

.gallery-info__item p {
    font-size: var(--font-size-xl);
    font-weight: 600;
    color: var(--color-primary);
}

.gallery-info__description {
    max-width: 800px;
    margin: 0 auto;
    text-align: center;
}

.gallery-info__description p {
    font-size: var(--font-size-lg);
    line-height: 1.6;
    color: var(--color-text-secondary);
    margin-bottom: var(--spacing-4);
}

.gallery-info__description p:last-child {
    margin-bottom: 0;
}

/* Адаптивность для мобильных */
@media (max-width: 768px) {
    .gallery-info__grid {
        grid-template-columns: repeat(2, 1fr);
        gap: var(--spacing-4);
    }
    
    .gallery-info__description p {
        font-size: var(--font-size-base);
    }
}

/* Лайтбокс */
.lightbox {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
}

.lightbox--active {
    opacity: 1;
    visibility: visible;
}

.lightbox__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.9);
    cursor: pointer;
}

.lightbox__container {
    position: relative;
    max-width: 90vw;
    max-height: 90vh;
    z-index: 1;
}

.lightbox__content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.lightbox__image {
    max-width: 100%;
    max-height: 85vh;
    object-fit: contain;
    border-radius: 8px;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
}

.lightbox__counter {
    margin-top: 1rem;
    color: white;
    font-size: 1rem;
    text-align: center;
    background: rgba(0, 0, 0, 0.5);
    padding: 0.5rem 1rem;
    border-radius: 20px;
}

.lightbox__close,
.lightbox__prev,
.lightbox__next {
    position: fixed;
    background: rgba(255, 255, 255, 0.1);
    border: 2px solid rgba(255, 255, 255, 0.3);
    color: white;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    font-size: 24px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;
    backdrop-filter: blur(10px);
}

.lightbox__close:hover,
.lightbox__prev:hover,
.lightbox__next:hover {
    background: rgba(255, 255, 255, 0.2);
    border-color: rgba(255, 255, 255, 0.5);
    transform: scale(1.1);
}

.lightbox__close {
    top: 20px;
    right: 20px;
    font-size: 30px;
    line-height: 1;
    z-index: 10001;
}

.lightbox__prev {
    top: 50%;
    left: 20px;
    transform: translateY(-50%);
    font-size: 28px;
    font-weight: bold;
}

.lightbox__next {
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
    font-size: 28px;
    font-weight: bold;
}

/* Мобильная адаптация */
@media (max-width: 768px) {
    .lightbox__container {
        max-width: 95vw;
        max-height: 95vh;
    }
    
    .lightbox__image {
        max-height: 75vh;
    }
    
    .lightbox__close,
    .lightbox__prev,
    .lightbox__next {
        width: 50px;
        height: 50px;
        font-size: 24px;
        border-width: 3px;
        background: rgba(0, 0, 0, 0.7);
        border-color: rgba(255, 255, 255, 0.8);
    }
    
    .lightbox__close {
        top: 15px;
        right: 15px;
        font-size: 30px;
        width: 55px;
        height: 55px;
        z-index: 10000;
    }
    
    .lightbox__prev {
        left: 15px;
        font-size: 28px;
    }
    
    .lightbox__next {
        right: 15px;
        font-size: 28px;
    }
    
    .lightbox__counter {
        font-size: 0.9rem;
        padding: 0.4rem 0.8rem;
        margin-top: 0.5rem;
    }
    
    /* Увеличиваем область клика для кнопок на мобильных */
    .lightbox__close::before {
        content: '';
        position: absolute;
        top: -10px;
        left: -10px;
        right: -10px;
        bottom: -10px;
        background: transparent;
    }
}

/* Анимации */
@keyframes lightboxFadeIn {
    from {
        opacity: 0;
        transform: scale(0.8);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}

.lightbox--active .lightbox__image {
    animation: lightboxFadeIn 0.3s ease-out;
}
